/*
  批量预授信
*/

@Component({
    selector: 'app-group-statistics',
    templateUrl: './group-statistics.component.html',
    styleUrls: ['./group-statistics.component.scss'],
    providers: [MessageService, DecimalPipe]
})

export class GroupStatistics implements OnInit {
    isReadonly: boolean = true;//是否只读

    msgs: Message[] = [];//提示信息
    maximumInfoBean: MaximumInfoBean = new MaximumInfoBean();
    batchData: any;
    constructor(
        public commfunc: Commfunc,
        public decimalPipe: DecimalPipe,
    ) {

    }
    ngOnInit() {
        this.batchData = JSON.parse(this.commfunc.getSessionDataCH('groupBranchData'));
        console.log(this.batchData)
        this.maximumInfoBean = this.batchData.groupData || {};
        // this.maximumInfoBean.pre_crdt_amt = '1.00'
        if(this.maximumInfoBean.pre_crdt_amt){
            this.maximumInfoBean.pre_crdt_amt = this.transMony(this.maximumInfoBean.pre_crdt_amt * 10000);
            // this.maximumInfoBean.pre_crdt_amt = 
        }
    }
    // 金额格式转换
	transMony(param) {
		let result = "";
		if (param) {
			result = this.commfunc.transform(param) + '';
		}
		result = this.decimalPipe.transform(param, '1.2-2');//申请金额
		return result;
	}



}