    // 提交之前做一次校验
    preValidateForSubmit() {
        //校验的
        for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
        //处理是否提交
        for (const i in this.myform.controls) {
            if (!this.myform.controls[i].valid) {
                return false;
            }
        }
        return true;
    }

