    submit_2() {
        // if (this.applyAmt() != 'success') {
        //     this.msgs = [{ severity: 'error', summary: '查询申请金额失败：', detail: this.applyAmt() }];
        //     return;
        // }
        this.juege = true;
        let custRequestNo = this.data.transferCreditData['custRequestNo'] ? this.data.transferCreditData['custRequestNo'] : this.data.transferCreditData.varMap.dataMap.custRequestNo;

        //授信提交需要传申请金额，卡要素额度调整需要传
        let workFlowAmt
        if (this.data.transferCreditData.varMap.taskCategoryId.indexOf('RUM0009') == -1 && this.data.transferCreditData.varMap.taskCategoryId != "RUM_0020") {
            if (this.applyAmts) {
                workFlowAmt = this.applyAmts
            } else {
                this.juege = false;
                this.msgs = [{ severity: 'error', summary: '提示', detail: '申请金额尚未查出，请稍等！' }];
                return
            }
        } else {
            if (this.data.transferCreditData.varMap.dataMap.creditType == '0003' && this.data.transferCreditData.varMap.dataMap.applyAmt) {
                workFlowAmt = this.data.transferCreditData.varMap.dataMap.applyAmt;
            } else {
                workFlowAmt = '0.00';
            }
        }
        // 最高额传申请金额
        let createBean = JSON.parse(this.commfunc.getSessionDataCH('createBean'));
        if(this.data.transferCreditData.varMap.dataMap.businessType == '06'){
            workFlowAmt = this.commfunc.transform(createBean['creditMap']['applyAmt']).toString();//申请金额格式转换
        }
        let queryParams = {
            'approvalOpinion': this.approveComment,
            'submitType': this.submitType_1,
            'taskId': this.data.transferCreditData['taskId'],
            'processId': this.data.transferCreditData['processId'],
            'custNo': this.data.transferCreditData['custNo'],
            'custRequestNo': custRequestNo,
            // 'custRequestNo':'10004',
            'tellerNo': this.commonHeader['userId'],
            'workFlowAmt': workFlowAmt,

            'workEffortId': this.data.transferCreditData.varMap.dataMap.workEffortId,
            'applyDate': this.data.transferCreditData.varMap.dataMap.applyDate,
            'dataSource': this.data.transferCreditData.varMap.dataMap.dataSource,
            'dataMap': this.data.transferCreditData.varMap.dataMap

        };
        
        this.creditCcmService.submitCreditApprove(queryParams).subscribe(data => {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            s['mainFlowNo'] = '';
            if (s.extParam['specifyNextOpers']) {
                delete s.extParam['specifyNextOpers']
                // let commonHeader = s;
                // this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            if (s.extParam['specifyNextNode']) {
                delete s.extParam['specifyNextNode']
                // let commonHeader = s;
            }
            this.commfunc.setSessionData('commonHeader', JSON.stringify(s));
            if (data.returnCode.code == 'success') {
                this.juege = false;
                this.msgs = [{ severity: 'info', summary: '信息', detail: this.approvalResult_1 + '成功' }];
                this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
            } else {
                this.juege = false;
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                return;
            }
        });
    }
