     // 校验最高额担保合同状态
  checkMaxContractStatus(resolve) {
      console.log("ad")
    const params = {
      custRequestNo:this.data.transferCreditData['custRequestNo'],
      processId:this.data.transferCreditData['processId']
    }
   return  this.creditCcmService.checkMaxContractStatus(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        if(data.maxContractStatus  == 'S'){
            resolve({
                maxStatus :true
            })
            //  return true;
        }else{
           this.tipMessages = data.tipMessages;
           resolve({
            maxStatus :false
        })
            // return false;
        } 
      } else {
        this.tipMessages = data.returnCode.message
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        resolve({
            maxStatus :false
        })
        // return false;
      }
    });
  }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';//节点处理名称
        this.outVal.forEach(item => {
            if (item.tellerId != undefined && item.tellerId != null) {
                ss += item.tellerId + ',';
            }
            specifyNextNode = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (ss != '') {
            commonHeader.extParam['specifyNextOpers'] = ss;
        }
        commonHeader.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if(this.data.transferCreditData.varMap.taskCategoryId == "RUM_0020"){
             this.bpmNodeList()
        }else{
            this.submit_2();
        }
       
    }
