    vetoChange(event) {
        if (event == '06') {
            this.vetoOption = this.personVeto;
        } else if (event == '07') {
            this.vetoOption = this.financeVeto;
        } else if (event == '08') {
            this.vetoOption = this.manageVeto;
        } else if (event == '09') {
            this.vetoOption = this.serveVeto;
        } else if (event == '10') {
            this.vetoOption = this.otherVeto;
        }
    }
