    //hjm star 添加胡佳接口取原合同是否结清状态
    checkContract(){
        if(this.data.transferCreditData&&this.data.transferCreditData['varMap']&&this.data.transferCreditData.varMap['dataMap']&&this.data.transferCreditData.varMap.dataMap.contactId){
        const params={
            contactId:this.data.transferCreditData.varMap.dataMap.contactId
        }
        this.creditCcmService.checkContract(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.settleFlag = data.settleFlag;
                if(this.settleFlag=='2'){
                    this.msgs = [];
                    this.msgs=[{ severity: 'info', summary: '提示', detail: '当前为预处理流程，请知悉' }];
                }
            } 
        })
    }
    }
     //hjm end 添加胡佳接口取原合同是否结清状态
    //next节点
    disposeSchedule(oneVetoReason, twoVetoReason, voteReason) {
        this.subApproveMapBean.result = '1';
        this.subApproveMapBean.info = this.approveComment;
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader['globalSeqNo'] = this.data.transferCreditData.mainFlowNo;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.data.transferCreditData.varMap.dataMap['approve'] = '1';
        let approvalOpinion1, approvalOpinion2;
        debugger
        for (let i of this.vetoFirsts) {
            if (i.value == oneVetoReason) {
                approvalOpinion1 = i.value;
            }
        }
        for (let i of this.vetoOption) {
            if (i.value == twoVetoReason) {
                approvalOpinion2 = i.value;
            }
        }
        let opinion = '';
        if (approvalOpinion1) {
            if (opinion) {
                opinion = opinion + '@@' + approvalOpinion1
            } else {
                opinion = approvalOpinion1
            }

        }
        if (approvalOpinion2) {
            if (opinion) {
                opinion = opinion + '@@' + approvalOpinion2
            } else {
                opinion = approvalOpinion2
            }
        }
        if (voteReason) {
            if (opinion) {
                opinion = opinion + '@@' + voteReason
            } else {
                opinion = voteReason
            }
        }
        if (this.approveComment) {
            if (opinion) {
                opinion = opinion + '@@' + this.approveComment
            } else {
                opinion = this.approveComment
            }
        }
        this.data.transferCreditData.varMap.dataMap['approvalOpinion'] = opinion
        let dataMap = this.data.transferCreditData.varMap.dataMap;
        dataMap['cusAssignFlag'] = '04';
        this.bussinessMap.cusAssignFlag = '04';
        this.bussinessMap.approve = '1';//否决
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.bussinessMap['dataMap'] = dataMap;
        this.appproResultBean.userId = commonHeader['userId'];
        this.appproResultBean.taskId = this.data.transferCreditData['taskId'];
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.customCenterHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.juege = false;
                this.msgs = [{ severity: 'success', summary: '提示', detail: '否决成功!' }];
                commonHeader['globalSeqNo'] = '';
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
            } else {
                this.juege = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.juege = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    // 关闭审批详情
    closeFirtproc() {
        this.creditApproveDisplay = false;
    }
    // 查询转换贷款类型
    getBusinessHappenTypeValue() {
        const param = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
        };
        this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 产品动态属性“转换贷款”的值===0或者1，展示组件isConverLoan  PCA0000013
                if (data.dynamicProductValue['PCA0000013'] === '1') {
                    this.displayComponent = '1';
                } else {
                    this.displayComponent = '0';
                }
            }
        });
    }

    // 打开名单制详情
    toCustListDetail(col) {
        if (col.reminder.indexOf('名单制') != -1) {
            const param = {
                blackListId: col.blackListId,
                registerUserName: JSON.parse(this.commfunc.getSessionDataCH('chName')).userName,
                operationType: '1007'
            }
            this.httpService.selectBlackListInfo(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    let inValue = data.returnList;
                    if (inValue.length > 0) {
                        this.blackListInValue = inValue[0];
                        this.blackListDisplay = true;
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询客户名单制为空' }];
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }
    }
    // 关闭名单制详情
    closeBlackList(){
        this.blackListInValue = undefined;
        this.blackListDisplay = false;
    }
