export class TransformLoanPlanBean {
    operateType: string;//	操作类型    1：新增  2：修改  3：删除
    custRequestId: string;//	客户请求标识
    custRequestItemSeqId: string;//	主键
    contractNo: string;//	合同编号
    creditCardNum: string;//	信用卡号
    partyIdTo: string;//	目标当事人标识
    custNameTo: string;//	目标当事人名称
    fromDate: string;//	开始日期
    agreementDate: string;//	协议日期
    thruDate: string;//	结束日期
    rate: string;//	利率
    guaranteeMode: string;//	主担保方式
    guaranteeName: string;//	担保人
    tellerName: string;//	经办人
    productNo: string;//	产品编号
    productName: string;//	产品名称
    loanAmt: string;//	贷款金额
    loanBalance: string;//	贷款余额
    convertContractAmount: string;//	转化金额
    convertEffectRatio: string;//	转化成效率
    branchApprover: string;//	支行其他审批责任人
    institutionalApprover: string;//	机构审批人
    headofficeApprover: string;//	总行审批人
    convertFrequency: string;//	转化次数
    convertObject: string;//	转化对象
    isLoanrePayment: string;//	首次转化日期
    lastConvertContractNo: string;//	上笔转化合同编号
    lastConvertContractAmount: string;//	上笔转化金额
    lastConvertContractBalance: string;//	上笔转化余额
    lastLoanDateStart: string;//	上笔转化日期
    isLoanRepayment: string;//	是否以贷还贷
    isInterestFree: string;//	是否免息
    isExpirePrincipaInterest: string;//	是否到期本息一并处置
    remark: string;//	情况说明
    listBox: any;//	列表信息
    theStepsClassify: string;//十级分类
    taskRate: any;
    tranSeqNo: string;
    processId: string;
    taskId: string;
    bussType: string;
}

