import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../../http/overdue.http.service';
import { element } from 'protractor';
import { OverdueReasonsBean } from './bean/overdue-reasons.bean';
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { OverdueReasonData } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/base/localData";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { ProcessHelper } from "app/pages/tzb/custom/overdue/common/processHelper";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";
import { HistoryHelper } from "app/pages/tzb/custom/overdue/common/history";

//2.逾期原因分析
@Component({
    selector: "overdue-reasons",
    templateUrl: './overdue-reasons.component.html',
    styleUrls: ['./overdue-reasons.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService, CustomCenterHttpService]
})

export class OverdueReasons implements OnInit {

    code: any;
    workFlowResultType: any = [];

    msgs: Message[] = [];
    overdueReasonsBean: OverdueReasonsBean = new OverdueReasonsBean();
    //码值

    taskId: string = '';
    // 接收路由传参
    baseInfo: any = {};   //基本信息
    overDoData: any;
    isDetailFlag: boolean = false;  //是否展示详情
    markFlag: string;
    // 回显信息
    preLoanFinancialCondition: any = [];        //  贷前
    overdueFinancialCondition: any = [];        //  逾期后
    overdueReasonAnalyze: any = [];             //  2.逾期原因分析
    bondsmanCondition: any = [];                //  3.担保人情况分析
    //回调信息
    overdueFinancialConditionCall: any = {};        // 1.逾期后回调数据
    overdueReasonAnalyzeCall: any = {};             // 2.逾期原因分析
    bondsmanConditionCall: any = [];                // 3.担保人情况分析
    realityCreditPurpose: any = [];                 // 实际用途
    originalCreditPurpose: any = [];
    overdueReasonAnalyzeHook: any = [];
    isDetail: boolean = true;//详情进入层级
    detail: boolean = true;
    // 操作人员等级
    operatingLevel: number = 0;
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    overdueReasonData: OverdueReasonData = new OverdueReasonData(this.commfunc);
    user: any;
    userId: string;//用户id
    processHelper: ProcessHelper = new ProcessHelper(
        this.commfunc,
        this.customService,
        this.commonlyMessage,
        this.router,
        this.customCenterHttpService,
        this.httpService
    );
    checkValueNode: string;  //列表选项
    productNo: string = '';
    processId: string = '';
    historyConfig: any;
    historyHelper: HistoryHelper = new HistoryHelper();
    pageData: any;
    // 最大授信金额
    creditAmountList: any = [];
    maxCreditAmt: number = 0;
    agentOrg: any;


    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public route: ActivatedRoute,
        public customService: CustomeHttpService,
        public customCenterHttpService: CustomCenterHttpService
    ) {
        // this.codeValues() //调用方法，获取全部码值
        // this.workFlowResultType = this.code['WorkFlowResultType'];
        // this.statusId = this.code['useStatus'];
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // console.log(this.user)
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        this.overdueReasonData.isNeedBondsmanCondition = false; 
    }

    ngOnInit() {
        this.overDoData = JSON.parse(this.commfunc.getSessionDataCH('overdueData'));
        if (this.overDoData.allowed != "2") {
            this.isDetail = false;
            this.detail = false;
        }

        let params = this.route.snapshot.params;
        if (params.detail && params.detail == '1') {
            // this.detail = true;
            this.isDetail = false;
        }
        // 接收路由传参
        this.grapParameters();
        if (this.overDoData.allowed == 2) {
            this.bpmQueryTaskList();
        }
    }

    grapParameters() {
        this.getConfidential();
        if (this.overDoData && this.overDoData.isModule == '1') {

        }

        if (Object.keys(this.route.snapshot.params).length != 0 || (Object.keys(this.overDoData).length != 0 && this.overDoData.isModule == '1')) {
            // let parameter = this.route.snapshot.params;
            let parameter: any;
            if (this.overDoData && this.overDoData.isModule == '1') {
                parameter = this.overDoData;//判断是否是模块化跳转
            } else {
                parameter = this.route.snapshot.params;
            }
            this.taskId = parameter.taskId;
            this.processId = parameter.processId;
            this.historyConfig = {
                processId: this.processId
            }
            // this.operatingLevel = parameter.operatingLevel; 
            // this.isDetailFlag = parameter.isDetailFlag == "true" ? true : false; //（为true表示是从案例查询列表跳转过来的，展示详情；false表示从待办过来的，展示页面）
            let t = Object.keys(JSON.parse(parameter.baseInfo));
            if (t.length != 0) {
                this.baseInfo = JSON.parse(parameter.baseInfo);//基本信息
                this.processHelper.loadData({
                    userId: this.userId,
                    taskId: this.taskId,
                    overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId
                })
                this.productNo = this.baseInfo.productNo;
                // 回显
                this.showDetailData();
            }
        }
    }
    getConfidential() {
        let confidential = this.overdueReasonData.getOverdueConfidential();
        if (confidential) {
            this.operatingLevel = confidential.operatingLevel;
            this.isDetailFlag = confidential.isDetailFlag;
            this.markFlag = confidential.markFlag;
        }
    }

    // 担保方式判断
    guaranteeJudge(data) {
        if (data) {
            data.forEach(element => {
                if (element.guaranteeType == '101') {
                    this.overdueReasonData.isNeedBondsmanCondition = true;
                } else {
                    this.overdueReasonData.isNeedBondsmanCondition = false;
                }
            });
        }else{
            this.overdueReasonData.isNeedBondsmanCondition = false; 
        }

    }

    //获取回显数据
    showDetailData() {
        // 调回显信息
        let param = {
            overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId,   //案例编号
            custNo: this.baseInfo.custNo,            //客户号
            contractNumber: this.baseInfo.contractNumber,   //合同编号/卡号
        }
        this.httpService.overdueCaseDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.guaranteeJudge(data.selfDebtInfo.selfDebtList);
                this.pageData = data;

                // 1.贷前逾期后列表
                this.preLoanFinancialCondition = this.fixPreLoan(data.preLoanFinancialCondition);    // 贷前调查时财务状况

                this.overdueFinancialCondition = this.fixOverdueFinancial(data.overdueFinancialCondition);    // 逾期后财务状况
                // 2.逾期原因分析
                this.overdueReasonAnalyze = data.overdueReasonAnalyze;
                //3. 担保人情况分析
                this.bondsmanCondition = this.fixBondsmanCondition(data.bondsmanCondition);
                //4. 实际用途
                this.realityCreditPurpose = data.realityCreditPurpose;
                //5. 原贷款用途
                this.originalCreditPurpose = data.originalCreditPurpose;
                // 原经办机构
                this.agentOrg = {
                    agentOrgNo: data.headOfficeApprove.agentOrgNo,
                    agentOrgName: data.headOfficeApprove.agentOrgName
                }

                // 获取最大 授信金额
                this.getCreditAmountList(data.selfDebtInfo.selfDebtList);
                this.getMaxCreditAmt()
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    fixPreLoan(source: any) {
        if (!source || !Object.keys(source).length) {
            source = [];
        } else {
            source = [source];
        }
        source = this.fixOverdueFinancial(source);
        source = source[0];
        return source;
    }

    fixOverdueFinancial(source: any) {
        if (!source || !source.length) {
            source = [{
                bankBorrowerAmt: 0,
                societyRaisingAmt: 0,
                accountsPayable: 0,
                debtsTotal: 0,
                receivables: 0,
                stocks: 0,
                fixedAssets: 0,
                investmentsAbroad: 0,
                propertyTotal: 0,
                debts1: 0,
                property1: 0,
                property2: 0,
                rightAndInterests: 0
            }];
        }
        source = this.fixNumber(source)
        return source;
    }

    fixNumber(source: any) {
        if (source && source.length) {
            source.forEach(element => {
                for (let property in element) {
                    if (element.hasOwnProperty(property) && typeof element[property] == 'number') {
                        element[property] = parseFloat(element[property]).toFixed(2);
                    } else if (property.toString() == 'otherBankEnsureAmt' || property.toString() == 'otherBankLoanAmt') {
                        element[property] = '0.00';
                    }
                }
            })
        }

        return source;
    }

    fixBondsmanCondition(source: any) {
        let ret = [];
        source.forEach(element => {
            ret.push(this.fixNumber(element));
        })
        return ret;
    }

    //保存按钮
    saveClick() {
        let param = this.getUploadData('1');
        param['operatePage'] = '2';
        param['operateType'] = '1';

        this.httpService.overdueCaseAnalyze(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    showSaveDataAlert() {
        this.confirmationService.confirm({
            message: '请确认页面录入信息是否已保存',
            accept: () => {
                this.submitClick();
            }
        });
    }


    // 提交按钮
    submitClick() {
        this.AnalyzeClick('2');
    }

    //调逾期案例分析受理  
    AnalyzeClick(operateType) {
        let param = this.getUploadData(operateType);
        param['operatePage'] = '2';
        if (operateType == '2') {
            this.overdueReasonData.runEmptyCheck()
            if (this.overdueReasonData.msg && this.overdueReasonData.msg.length) {
                this.commonlyMessage.logicError(this.commonlyMessage.combineMsgs(this.overdueReasonData.msg));
                return;
            }
        }

        // console.log(param);
        this.processHelper.setMaxCreditAmount(this.maxCreditAmt);
        this.processHelper.setSubmitEnvironment(param, 'overdue-reason');
        this.processHelper.selectNextPerson(this.agentOrg);
    }


    getUploadData(operateType) {

        let ret: any = {};
        let t: any;

        let baseInfo = this.overdueReasonData.getBaseInfo();

        this.overdueReasonData.source = this.pageData;

        // 页面没有初始化，就获取原始数据
        let realityCreditPurpose = this.overdueReasonData.getRealityCreditPurpose();
        let overdueFinancialCondition = this.overdueReasonData.getOverdueFinancialCondition();
        let overdueReasonAnalyze = this.overdueReasonData.getOverdueReasonAnalyze();
        let bondsmanCondition = this.overdueReasonData.getBondsmanCondition();


        for (let property in baseInfo) {
            if (baseInfo.hasOwnProperty(property)) {
                ret[property] = baseInfo[property];
            }
        }

        ret.realityCreditPurpose = realityCreditPurpose;
        ret.overdueFinancialCondition = overdueFinancialCondition;
        ret.overdueReasonAnalyze = overdueReasonAnalyze;
        ret.bondsmanCondition = bondsmanCondition;
        ret.operateType = operateType;
        ret.taskId = this.taskId;

        return ret;
    }

    cancel() {
        this.processHelper.cancel('');
    }

    // 1.贷前、逾期后列表回调
    loantableCall(param) {
        this.overdueFinancialConditionCall = param;
    }
    // 2.逾期原因分析回调
    reasonCall(param) {
        this.overdueReasonAnalyzeHook = param;
    }
    // 3.担保人情况分析回调
    bondsmanCall(param) {
        // 删除历史修改过程属性
        param.forEach(element => {
            delete element.historyProcess;
        });
        this.bondsmanConditionCall = param;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }

    // 查询(历史)审批记录
    bpmQueryTaskList() {
        this.processHelper.bpmQueryTaskList({ processId: this.processId, taskId: this.taskId });
    }


    // 获取最大的 授信金额
    getCreditAmountList(selfDelbt: any) {
        if (selfDelbt && selfDelbt.length) {
            selfDelbt.forEach(item => {
                if (item.creditBalance > 0) {
                    this.creditAmountList.push(item.creditAmount);
                }
            })
        }
    }
    getMaxCreditAmt() {
        if (this.creditAmountList && this.creditAmountList.length) {
            this.maxCreditAmt = Math.max.apply(null, this.creditAmountList);
        }
    }

    backToSource() {
        let t = this.overdueReasonData.getOverdueConfidential();
        let backUrl = parseInt(t.backUrl);
        window.history.go(backUrl);
    }

}
