import { Component, OnInit, Input } from '@angular/core';
import { ParentChildCommunicationService } from '../common/service/parent-child-comm.service';
import { DecisionMessageBean } from './decision-message.bean';
import { CreditCcmService } from '../common/http/credit-ccm.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
  selector: 'app-decision-message',
  templateUrl: './decision-message.component.html',
  styleUrls: ['./decision-message.component.scss'],
  providers: [Commfunc]
})
export class DecisionMessageComponent implements OnInit {
  decisionMessageBean:DecisionMessageBean=new DecisionMessageBean();
  @Input('data') data;
  count: number;//计数，防抖
  decisionTypelist=[{ label: '请选择', value: '' },{ label: '通过', value: '1' },{ label: '审慎通过', value: '2' },{ label: '拒绝', value: '3' }];
  incomeSourcess=[
  { label: '请选择', value: '' },
  { label: '公积金估算', value: '1X' },
  { label: '公积金估算', value: '10' },
  { label: '公积金估算', value: '11' },
  { label: '公积金估算', value: '12' },
  { label: '公积金估算', value: '13' },
  { label: '房贷估算', value: '3X' },
  { label: '房贷估算', value: '30' },
  { label: '房贷估算', value: '31' },
  { label: '房贷估算', value: '32' },
  { label: '房贷估算', value: '33' },
  { label: '高净值月日均', value: '23' },
  { label: '高净值月日均', value: '43' },
  { label: '高净值月日均', value: '22' },
  { label: '高净值月日均', value: '42' },
  { label: '统计估算', value: '21' },
  { label: '统计估算', value: '20' },
  { label: '统计估算', value: '2X' },
  { label: '最低收入', value: '4X' },
  { label: '最低收入', value: '40' },
  { label: '最低收入', value: '41' },
  { label: '外接公积金收入', value: '50' },
  { label: '外接公积金收入', value: '51' },
  { label: '外接公积金收入', value: '52' },
  { label: '外接公积金收入', value: '53' },
  { label: '外接公积金收入', value: '5X' }
];
  idenTypes=[];
  xyreson:any;
  srreson: any;
  constructor(
    public communicationService: ParentChildCommunicationService,
    public creditCcmService:CreditCcmService,
    public commfunc: Commfunc,
  ) {
    
    this.communicationService.queryTransfer.subscribe(_event => {
      if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditDebt') !== -1) {
        this.count++;
        if (this.count == 1) {
          this.query();
        }
      }
    });
  }

  ngOnInit() {
    this.query();
    this.getCodeValues();
  }
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.idenTypes = codeValues['ALL_IDEN_TYPE'];
   
  }
  query() {
    //  this.decisionMessageBean.excRuleArrayList = [
    //       {excRuleDesc:"全部贷记卡"},
    //       {excRuleDesc:"历史贷款6个月"},
    //       {excRuleDesc:"历史贷款24个月"},
    //     ]
    let param = {
      custRequestId:this.data.transferCreditData.custRequestId,
      // custRequestId: '19360004',
    }
    this.creditCcmService.queryCustRequestDetail(param).subscribe(data=>{
      console.log("lllll")
      if(data.returnCode.code === 'success'){
        this.decisionMessageBean = data;
        // this.decisionMessageBean.excRuleArrayList = [
        //   {excRuleDesc:"全部贷记卡"},
        //   {excRuleDesc:"历史贷款6个月"},
        //   {excRuleDesc:"历史贷款24个月"},
        // ]
        this.xyreson=this.decisionMessageBean.reasonInfoArrayList.length;
        this.srreson=this.decisionMessageBean.incomeInfoArrayList.length;
      }
    })
  }

}
