	/**
	 * 规则校验
	 * 校验规则如下：
	 * check 1.如果主担保方式选择保证,合同方式选择普通 则必须要增加一个担保人
	 * check 2.如果合同方式为引用最高额,担保信息中至少有一个是最高额担保
	 * 
	 * check 4.根据自助类贷款和非自助类分别校验
	 * a)当自助类贷款(包含供应链)和消费贷款（包含自助和非自助），最高额担保剩余可用金额必须大于等于申请金额；最高额到期日期必须大于等于贷款申请到期日期。(根据业务品种为010002判断)
	 * b)其他情况判断，最高额担保剩余可用金额必须大于等于申请金额，最高额到期日期必须大于或等于当前系统日期；
	 * 
	 */
	checkRuleVerification(list) {
		// check1 start ...
		if (this.creditDetail.guaranteeType === '101' && this.creditDetail.contractMode === '01') {
			if (!this._check1()) {
				// 返回校验失败标示
				this.msgs = [];
				this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少增加一个担保人作为担保!' }];
				return false;
			}
		}
		// check1 end ...

		// check2 start ...
		if (this.creditDetail.contractMode === '02') {
			if (!this._check2(list)) {
				// 返回校验失败标示
				this.msgs = [];
				this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少增加一条最高额担保记录作为担保!' }];
				return false;
			}
		}
		// check2 end ...

		// check4 start ...
		// 校验最高额总金额是否大于申请金额 
		// if (!this._check4(list)) {
		// 	this.msgs = [];
		// 	this.msgs = [{ severity: 'info', summary: '提示', detail: '请确定引入最高额合同的可用余额大于或等于申请金额(申请金额)!' }];
		// 	return false;
		// }
		// 根据自助类贷款和非自助类分别校验
		if (this.cateNumValue === '010002') {
			// 最高额到期日期必须大于等于贷款申请到期日期
			let tempDate;
			// let maxtargetDate = moment(new Date(this.creditDetail.endDate)).add(6, 'month').format('Y-MM-DD');
			// let targetDate = new Date(maxtargetDate); // 贷款到期日期
			let targetDate = new Date(this.creditDetail.endDate); // 贷款到期日期
			for (const item of list) {
				tempDate = new Date(item.guaranteeEndDate);
				if (tempDate < targetDate) {
					this.msgs = [];
					// this.msgs = [{ severity: 'info', summary: '提示', detail: '非自助类贷款允许最高额到期日最多后延六个月!' }];
					this.msgs = [{ severity: 'info', summary: '提示', detail: '请确保最高额到期日期大于或等于贷款到期日期!' }];
					return false;
				}
			}
		} else {
			// 最高额到期日期必须大于等于当前系统日期
			let currentDate = new Date();
			if (!((this.data.transferCreditData && this.data.transferCreditData.varMap && (this.data.transferCreditData.varMap.dataMap['taskType'] == '10' || this.data.transferCreditData.varMap.dataMap['taskType'] == '16')) ||
				this.inValue['creditCardElement'] ||
				(this.data.transferCreditData && this.data.transferCreditData.varMap && this.data.transferCreditData.varMap.taskCategoryId == 'RUM0009'))) {
				for (const item of list) {
					if (currentDate > new Date(item.guaranteeEndDate)) {
						this.msgs = [];
						this.msgs = [{ severity: 'info', summary: '提示', detail: '请确保最高额到期日期大于当前系统日期!' }];
						return false;
					}
				}
			}
		}
		// check4 end ...
		return true;
	}

	// check 1.如果主担保方式选择保证,合同方式选择普通 则必须要增加一个担保人
	_check1() {
		for (const item of this.guarantor.queryList) {
			if (!item.contractNo || item.contractNo === '') {
				return true;
			}
		}
		return false;
	}

	// check 2.如果合同方式为引用最高额,担保信息中至少有一个是最高额担保
	_check2(list) {
		for (const item of list) {
			if (item.contractNo || item.contractNo !== '') {
				return true;
			}
		}
		return false;
	}
	// check 4 金额校验:每条‘引用的最高额担保合同’的可用余额，必须大于等于,贷款申请金额(担保金额)
	_check4(list) {
		for (const item of list) {
			if (parseFloat(item.useAmt) > parseFloat(item.contractBalance) && item.contractMode != "01") {
				return false;
			}
		}
		return true;
	}

