import { Component, OnInit, Input } from '@angular/core';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { DomSanitizer } from '@angular/platform-browser';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_FIELD_URL } from 'app/@uisftech/common/service/http.constant';

@Component({
    selector: 'field-investigation',
    templateUrl: './field-investigation.component.html',
    styleUrls: ['./field-investigation.component.scss'],
    providers: [CreditCcmService]
})
export class FieldInvestigation implements OnInit {
    @Input('data') data;
    custName: any;
    custNo: any;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    extParam: any;

    srcUrl_1: any;
    info_num: string = '';//客户个数
    info_numVal: string = '';//共同借款人
    queryFlag: string;//上传或者查看的标志
    custRequestId: string;//流水号
    msgs: Message[] = [];
    tzbcun:any;

    constructor(
        public httpService: CreditCcmService,
        private sanitizer: DomSanitizer,
        private communicationService: ParentChildCommunicationService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let flagTip = this.user.targetId.charAt(this.user.targetId.length - 1);//0是台行
        if(flagTip != '0'){
           this.tzbcun ="S08"
        }else{
            this.tzbcun =""
        }
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        //flag=2  我的历史进入
        if (this.extParam.managePermission == 'N'
            && this.data.transferCreditData['taskRate'] == 1
            && this.data.transferCreditData['flag'] != '2') {
            this.queryFlag = '2';//上传补录
        } else {
            this.queryFlag = '1';//查看
        }

        this.communicationService.queryTransfer.subscribe(_event => {

            if (_event && _event.bookmarkId && _event.componentNames.indexOf('FieldInvestigation') !== -1) {
                //console.log(this.data)
                this.custNo = this.data.transferCreditData['custNo'];
                this.custName = this.data.transferCreditData['custName'];
                this.custRequestId = this.data.transferCreditData.varMap['custRequestNo'];
                // 出现查询——拼接
                this.queryCommonBorrowerInfo();
            }
        });
    }
    //获取共同借款人的信息
    queryCommonBorrowerInfo() {
        let param = {
            loanSeqNo: this.data.transferCreditData['custRequestNo'],
            questionType: '01',//共同借款人
        }
        this.httpService.queryCreditQuestionInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList && data.resultList.length > 0) {
                    let param;
                    let tempArray = [];
                    for (let item of data.resultList) {
                        param = JSON.parse(item.questionReslponse);
                        param['cqiId'] = item.cqiId;
                        param['custRequestNo'] = item.custRequestNo;
                        tempArray.push(param)
                    }
                    //共同借款人
                    for (let i = 0; i < tempArray.length; i++) {
                        let tempBookmarkOrder = i + 2 + '';
                        this.info_numVal += '';
                        this.info_numVal += '&info' + tempBookmarkOrder + "=TABSORDER:" + tempBookmarkOrder + ";BUSI_SERIAL_NO:IMP"
                            + tempArray[i].commonBorrowerCustNo + ";TABNAME:共同借款人-" + tempArray[i].commonBorrowerCustName
                            + ";PROSERIALNO:"+ this.tzbcun + "gtjkr_" + tempArray[i].custRequestNo +"_" + tempArray[i].commonBorrowerCustNo +";OBJECTNAME:APP1009;";
                    }

                    //console.log(1111, this.info_num);
                    //console.log(1111, tempArray);

                    this.queryGuarantorInfo(data.resultList.length);
                } else {
                    this.queryGuarantorInfo(0);
                }

            } else {
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                this.queryGuarantorInfo(0);
            }
        }, error => {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
            this.queryGuarantorInfo(0);

        });

    }
    //获取担保人的信息
    queryGuarantorInfo(borrowerLength: number) {
        //获取担保人的信息
        let params = {
            loanSeqNo: this.data.transferCreditData['custRequestNo'],
            questionType: '02',//担保人
        }
        this.httpService.queryCreditQuestionInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList && data.resultList.length > 0) {
                    let param;
                    let tempArray = [];
                    for (let item of data.resultList) {
                        param = JSON.parse(item.questionReslponse);
                        param['cqiId'] = item.cqiId;
                        param['custRequestNo'] = item.custRequestNo;
                        tempArray.push(param)
                    }

                    //担保人
                    for (let i = 0; i < tempArray.length; i++) {

                        let tempBookmarkOrder = i + 2 + borrowerLength + '';
                        this.info_num += '';
                        // 村镇加S08
                        this.info_num += '&info' + tempBookmarkOrder + "=TABSORDER:" + tempBookmarkOrder + ";BUSI_SERIAL_NO:IMP"
                            + tempArray[i].custNo + ";TABNAME:担保人-" + tempArray[i].custName + ";PROSERIALNO:"+ this.tzbcun + "dbr_"
                            + tempArray[i].custRequestNo + "_" + tempArray[i].custNo + ";OBJECTNAME:APP1009;";
                    }
                    //console.log(222, tempArray)
                    //console.log(333, this.info_num)
                    this.lastExcuteConcat();
                } else {
                    this.lastExcuteConcat();
                }
            } else {
                this.lastExcuteConcat();
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.lastExcuteConcat();
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }


    //最后执行拼接url
    lastExcuteConcat() {
        // console.log("URL")
        // console.log(TZB_FIELD_URL)
        debugger
        let info1 = "info1=TABSORDER:1;BUSI_SERIAL_NO:IMP" + this.custNo + ";TABNAME:借款人-" + this.custName + ";PROSERIALNO:"+ this.tzbcun + this.custRequestId + ";OBJECTNAME:APP1009;" + this.info_num + this.info_numVal;
        let newurl = TZB_FIELD_URL + "/SunIAP/SunIAPRequestController.do?UID=MOBILE&PWD=cyg&AppID=APP1009&UserID=" + this.userId + "&UserName=" + this.userName + "&OrgID=" + this.orgId + "&OrgOwner=CRM&QueryFlag=" + this.queryFlag + "&" + info1;
        this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(newurl);
        //console.log(newurl)
    }
}
