import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';

import { ParamsBean } from '../bean/params.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { VetoBean, BussinessMap, AppproResultBean, SubApproveMapBean } from '../credit-biz-apply/bean/vetoReason.bean';
import { CustomCenterHttpService } from '../../../customCenter/http/custom-center.http.service';
import { MessageService } from 'primeng/components/common/messageservice';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { BigCusHttpService } from 'app/pages/tzb/custom/bigCustomer/http/big-cus.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
