        // 提交之前做一次校验
        preValidateForSubmit() {
            //校验的
            for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
            //处理是否提交
            for (const i in this.myform.controls) {
                if (!this.myform.controls[i].valid) {
                    return false;
                }
            }
            return true;
        }
    
        // 审批详情弹框回调方法
        creditApproveCall() {
            this.creditApproveDisplay = false;
        }
    
        // 提交审批意见
        //doreasn-------0-同意，1-否决，2-打回上一节点，3-追回，4-终止，5-打回拟稿人，6-任务转办，7-退回任意节点，n-自动关闭，S2-正常结束
        submitCreditApprove(submitType) {
            this.submitType_1 = submitType;
            // 判断报文头有无人员、节点信息-清空
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            s['globalSeqNo'] = this.data.transferCreditData.mainFlowNo;
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
            }
            if (s.extParam.specifyNextNode) {
                delete s.extParam.specifyNextNode
            }
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    
            if (this.data.transferCreditData['isPreview']) {
                return;
            }
            if (!this.preValidateForSubmit()) {
                return;
            }
            
            switch (submitType) {
                case 1: this.approvalResult_1 = '提交'; break;
                case 2:
                    this.approvalResult_1 = '同意';
                    this.juege = true;
                    this.submit_1();
    
                    break;
                case 3:
                    this.approvalResult_1 = '否决';
                    this.display = true;
                    if (this.data.transferCreditData.varMap.dataMap.voteReason) {
                        let voteReason = this.data.transferCreditData.varMap.dataMap.voteReason.split(',');
                        this.vetoBean.oneVetoReason = voteReason[0];
                        this.vetoBean.twoVetoReason = voteReason[1];
                        this.vetoBean.voteReason = voteReason[2] ? voteReason[2] : '';
                    }
                    break;
                case 4:
                    this.approvalResult_1 = '打回';
                    this.backDisplay = true;
                    break;
                // case 5:
                //     this.approvalResult_1 = '打回拟稿人';
                //     this.backBtnComfir('B4', '5');
                //     break;
                // case 6:
                //     this.approvalResult_1 = '追回';
                //     this.backBtn();
                //     break;
                // case 7:
                //     this.approvalResult_1 = '退回任意节点';
                //     this.nodeDisplay = true;
                //     this.queryNodeInfo();
                //     break;  
            }
        }
    
