    // 查看子流程初始化
    viewSubprocInit(col) {
        this.mainTaskId = col.taskId;
        this.pageSize = 1000;
        this.pageNum = 1;
        this.first = 0;
        this.viewSubproc()
    }

    // 查看子流程初始化
    viewSubprocInitAdd() {
        this.paramsBean.pageSize = 1000;
        this.paramsBean.pageNum = 1;
        this.paramsBean.startIndex = 0;
        this.viewSubprocAdd()
    }



    // 查看子流程
    viewSubprocAdd() {
        let paramSub = {
            mainTaskId: this.mainTaskId,
            isAllNode: false,
            pageRows: this.paramsBean.pageSize,
            curPage: this.paramsBean.pageNum,
        }
        this.processInstanceManagementService.queryChildTaskList(paramSub).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let subflowInstance = {};
                this.paramsBean.queryList = [];
                subflowInstance = data.pageData[0].result;
                this.paramsBean.queryList = subflowInstance ? subflowInstance[0].workList : [];
                this.paramsBean.totalRecords = this.paramsBean.queryList.length;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }


    // 查看子流程
    viewSubproc() {
        let paramSub = {
            mainTaskId: this.mainTaskId,
            isAllNode: false,
            pageRows: this.pageSize,
            curPage: this.pageNum,
        }
        this.processInstanceManagementService.queryChildTaskList(paramSub).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let subflowInstance = {};
                subflowInstance = data.pageData[0].result;
                this.subprocList = subflowInstance ? subflowInstance[0].workList : [];
                this.totalNum = this.subprocList.length;
                this.subprocDisplay = true;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }


