import { Component, Input, OnInit, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { ContainerResourceDirective } from './container-resource.directive';
import { ResourceTemplateComponent } from './resource-template';
import { ContainerResourceService } from './container-resource.service';
import { ResourceItem } from './resource-item';
import { setTimeout } from 'timers';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-container-resource',
    template: `
                <div >
                  <ng-template container-resource></ng-template>
                </div>
              `
})

export class ContainerResourceComponent implements OnInit {
    @Input() basicSourceEnName: string;
    @Input() bookmarkId: string;

    @Input() transferCreditData: string;

    // tslint:disable-next-line:no-output-rename
    resource: ResourceItem;
    @ViewChild(ContainerResourceDirective) containerResourceDirective: ContainerResourceDirective;
    constructor(private componentFactoryResolver: ComponentFactoryResolver,
        private containerResourceService: ContainerResourceService,private commfunc: Commfunc,) { }
    ngOnInit() {
        // if(this.commfunc.getSessionDataCH('creditDataWorkFlow')){
        //     this.transferCreditData = JSON.parse(this.commfunc.getSessionDataCH('creditDataWorkFlow'))
        // }
        this.loadComponent();
    }

    loadComponent() {
        const params = {
            'transferCreditData': this.transferCreditData,
            'bookmarkId': this.bookmarkId,
        };
        this.resource = this.containerResourceService.getResources(this.basicSourceEnName, params);
        // 传入组件，根据组件类型，创建一个可以生产该类型组件的组件工厂
        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.resource.component);
        // 通过@ViewChild 及指令 获得对 `ViewContainerRef`访问，从而告诉Angular把动态组件插入到什么位置  (`ViewContainerRef`:展示一个可装载一个或多个视图的容器)
        const viewContainerRef = this.containerResourceDirective.viewContainerRef;
        // `viewContainerRef`利用组件工厂获得一个`ComponentRef`
        const componentRef = viewContainerRef.createComponent(componentFactory);
        // `ComponentRef` 可以对组件实例访问及与此组件实例相关的其他对象，并允许销毁组件实例
        (<ResourceTemplateComponent>componentRef.instance).data = params;
    }
}
