import { Component, OnInit, Input } from '@angular/core';

import { ConfirmationService, Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ParamsBean } from '../bean/params.bean';
import { CreditBorrowerBean } from '../bean/credit-borrower.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { CreditDebtBean } from '../credit-debt/bean/credit-debt.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import * as $ from 'jquery';
import { TZB_HTTP_CUS1 } from '../../../../../@uisftech/common/service/http.constant';
import { API } from 'app/pages/tzb/custom/http/custom-view/custom-view.constant';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';
@Component({
    selector: 'app-credit-borrower',
    templateUrl: './credit-borrower.component.html',
    styleUrls: ['./credit-borrower.component.scss'],
    providers: [CreditCcmService, ConfirmationService]
})

// tslint:disable-next-line:component-class-suffix
export class CreditBorrower implements OnInit {
    TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
    hiddenBtn: boolean = false; // 控制按钮是否展示

    blockedPanel: boolean = false; // 如果只有查看权限的情况下不能编辑数据

    // 提示信息
    msgs: Message[] = [];

    // 参数信息bean
    paramsBean: ParamsBean = new ParamsBean();

    // 共同借款人bean
    creditBorrowerBean: CreditBorrowerBean = new CreditBorrowerBean();

    // 用于展示新增页面信息
    displayAdd = false;

    // 修改页面使用
    param_data: any;
    param_detail: any;
    // 新增修改
    addOrUpdateTitle = '';

    // tslint:disable-next-line:no-input-rename
    @Input('data') data;

    // 共同借款人关系
    borrowerRelationshipList = [];
    userId: string;
    reportDisplay: boolean = false;
    progress: boolean = false;
    certType: string;
    certID: string;
    inValueReportId: string;
    reportCustNm: string;
    displayDetail: boolean = false;
    isApprove: boolean = true;//判断是否是审批节点
    showInView: boolean = true;//是否在我的历史中显示 added by yuanzhenghe
    isHasCoBorrower: any;//是否允许有共同借款人
    coBorrowerType:any;//共同借款人类型 
    coBorrowerTypeName:any;//共同借款人类型名称     
    // 构造器
    constructor(
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService,
        private communicationService: ParentChildCommunicationService,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public httpServices: CusViewHttpService,
    ) {
        this.communicationService.queryTransfer.subscribe(_event => {
            debugger;
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditBorrower') !== -1) {
                this.query();
            }
            if(_event && _event.isHasCoBorrower){
                console.log(_event.isHasCoBorrower);
                this.isHasCoBorrower = _event.isHasCoBorrower;
            }
            if(_event && _event.coBorrowerType){
                console.log(_event.coBorrowerType);
                this.coBorrowerType = _event.coBorrowerType;
            }
            if(_event && _event.coBorrowerTypeName){
                console.log(_event.coBorrowerTypeName);
                this.coBorrowerTypeName = _event.coBorrowerTypeName;
            }
        });
    }

    // 初始化信息
    ngOnInit() {
        //判断是否是审批节点
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'approve') {
            this.isApprove = false;
            this.showInView = false;
        }
        //判断是否是从历史进入 added by yuanzhenghe
        console.log(this.data.transferCreditData['flag']);
        if (this.data.transferCreditData['flag'] && this.data.transferCreditData['flag'] == '2') {
            this.showInView = false;
        } else {
            if(this.isApprove){
                this.showInView = true;
            }
        }
    }
    query() {
        debugger;
        // if (this.data.transferCreditData['isView']) {
        //    this.hiddenBtn = true;
        //     this.blockedPanel = true;
        // }

        // 初始化查询
        this.queryCommonBorrower('notCloseDialog');
        this.getCodeValues();
    }
    // 数据字典
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        // 与共同借款人关系
        this.borrowerRelationshipList = codeValues['borrowerRelationship'];
    }
    // 查询共同借款人信息列表
    queryCommonBorrower(flag?: string) {
        if (flag === 'closeDialog') {
            this.displayAdd = false;
        }
        const param = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '01',       // 共同借款人
            'viewSize': this.paramsBean.pageSize,
            'viewIndex': this.paramsBean.pageNum
        };
        this.creditCcmService.queryFacQuestions(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.paramsBean.queryList = [];
                let custInfoList = [];
                for (const item of data.list) {
                    const questionReslponse = JSON.parse(item['questionReslponse']);
                    questionReslponse.cqiId = item['cqiId'];
                    this.paramsBean.queryList.push(questionReslponse);
                    custInfoList.push({custNo:questionReslponse.commonBorrowerCustNo,custName:questionReslponse.commonBorrowerCustName});
                }
                this.paramsBean.totalRecords = data.totalSize;

                this.commfunc.setSessionDataCH('queryCreditQuestions', JSON.stringify(this.paramsBean.queryList));
                this.communicationService.custInfoList.emit(custInfoList);
            } else {
                this.msgs = [{ severity: 'error', summary: '错误:', detail: data.returnCode.message }];
            }
        });
    }

    // 共同借款人删除
    delete(col) {
        this.confirmationService.confirm({
            message: '确定要删除这条数据吗？',
            accept: () => {
                const cqiIds = [];
                cqiIds.push(col.cqiId);
                const params = {
                    'cqiIds': cqiIds
                };
                this.creditCcmService.deleteFacQuestions(params).subscribe(data => {
                    if (data.returnCode.code === 'success') {
                        this.queryCommonBorrower('notCloseDialog');
                    } else {
                        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                        return;
                    }
                });
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }

    // 共同借款人新增展示
    add() {
        this.param_data = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '01',   // 共同借款人
            'type': 'add',
            'custNo': this.data.transferCreditData['custNo'],
            'custName': this.data.transferCreditData['custName'],
            'isHasCoBorrower': this.isHasCoBorrower,
            'coBorrowerType':this.coBorrowerType,//共同借款人类型 
            'coBorrowerTypeName':this.coBorrowerTypeName//共同借款人类型名称    
        }
        this.addOrUpdateTitle = '新增';
        this.displayAdd = true;
    }

    modify(col) {
        this.param_data = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '01',   // 共同借款人
            'type': 'update',
            'detailBorrower': col,
            'custNo': this.data.transferCreditData['custNo'],
            'custName': this.data.transferCreditData['custName'],
            'isHasCoBorrower': this.isHasCoBorrower,
            'coBorrowerType':this.coBorrowerType,//共同借款人类型 
            'coBorrowerTypeName':this.coBorrowerTypeName//共同借款人类型名称    
        };
        this.addOrUpdateTitle = '修改';
        this.displayAdd = true;
    }
    //详情
    toDetail(col) {
        this.param_detail = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '01',   // 共同借款人
            'detailBorrower': col,
            'custNo': this.data.transferCreditData['custNo'],
            'custName': this.data.transferCreditData['custName'],
            'isHasCoBorrower': this.isHasCoBorrower,
            'coBorrowerType':this.coBorrowerType,//共同借款人类型 
            'coBorrowerTypeName':this.coBorrowerTypeName//共同借款人类型名称    
        };

        this.displayDetail = true;
    }
    // 分页按钮事件
    paginate(event) {
        // tslint:disable-next-line:radix
        this.paramsBean.pageNum = event.page + 1;
        this.paramsBean.pageSize = event.rows;
        this.paramsBean.startIndex = event.first;
        this.queryCommonBorrower();
    }
    //查看BI报告
    toBi(item) {
        this.queryCard(item)
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        let userName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;        
        let param = { 
            custInfoList:[{custNo: item.commonBorrowerCustNo ,custName:item.commonBorrowerCustName}],
            userName,
            isAsyncFlag:'1'
        }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.checkCreditReportInfo;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        _self.creatMainMsg({ custNo: item.commonBorrowerCustNo, custName: item.commonBorrowerCustName, qryUserId: _self.userId, cretType: _self.certType, cretNo: _self.certID, qryUserName: userName, accessWay: '1' })
                    } else {
                        _self.inValueReportId = data.custInfoList[0].reportId;
                        _self.reportCustNm = item.commonBorrowerCustNo;
                        _self.reportDisplay = true;
                        _self.msgs = [];
                        _self.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
                }
            },
            error: function (err) {
            }
        })

    }
    creatMainMsg(param) {
        this.progress = true;
        let params = this.commfunc.handleParams(param);
        this.httpServices.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = data.reportId;
                this.reportCustNm = param.custNo;
                this.progress = false;
                this.reportDisplay = true;
            } else {
                this.progress = false;
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        }, err => {
            this.progress = false;
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }

    queryCard(item) {
        this.httpService.selectCustBriefInfo({ custNo: item.commonBorrowerCustNo }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.certType = data.idenType;
                this.certID = data.idenNum;
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }
    
    compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}
}