    // 初始化数据
    // 获取数据
    getData() {

        const param = {
            commonHeader: this.commonHeader,
            custRequestNo: this.data.transferCreditData['custRequestNo'] // 客户请求编号
            // custRequestNo: "910253" // 客户请求编号
        };
        this.creditCcmService.queryCreditInfo(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.allData = data;
                this.maximumInfoBean.applyDate = data.creditMap.applyDate;
                this.maximumInfoBean.productName = data.creditMap.productName;
                this.maximumInfoBean.guaranteeType = data.creditMap.guaranteeType;
                this.maximumInfoBean.assistGuaranteeType = data.creditMap.assistGuaranteeType;
                this.maximumInfoBean.currency = data.creditMap.currency || "1";
                this.maximumInfoBean.applyAmt = data.creditMap.applyAmt;
                this.applyAmt = data.creditMap.applyAmt;
                this.maximumInfoBean.endDate = data.creditMap.endDate;
                this.maximumInfoBean.tellerOrgName = data.creditMap.tellerOrgName;
                this.maximumInfoBean.tellerDate = data.creditMap.tellerDate;
                this.maximumInfoBean.maxContractType = data.attributeMap.maxContractType;
                this.maximumInfoBean.tenClearStatus = data.attributeMap.tenClearStatus;
                // this.maximumInfoBean.tenClearStatus = null;//应测试要求修改为空
                this.maximumInfoBean.tenManualClear = data.attributeMap.tenManualClear;
                this.maximumInfoBean.surveyConclusion = data.attributeMap.surveyConclusion;
                this.maximumInfoBean.tellerName = data.attributeMap.tellerName;
                this.maximumInfoBean.currentApprovalStage = data.attributeMap.currentApprovalStage;
                this.maximumInfoBean.approvalStatus = data.creditMap.approvalStatus;

                this.maximumInfoBean.vetoType = data.attributeMap.vetoType;//否决类型
                this.maximumInfoBean.twoVetoType = data.attributeMap.twoVetoType;//二级否决类型

                if (data.attributeMap.loanReport) {
                    let fileList = [];
                    fileList.push({
                        "attrName": "loanReport",
                        "attrValue": data.attributeMap.loanReport
                    })
                    this.maximumInfoBean.productAttrList = fileList;
                } else {
                    let fileList = [];
                    fileList.push({
                        "attrName": "loanReport",
                        "attrValue": `/copdata/template/loanReport/${this.data.transferCreditData['custRequestNo']}`
                    })
                    this.maximumInfoBean.productAttrList = fileList;
                }
                // this.maximumInfoBean = data.creditMap
                ;
                if (!this.maximumInfoBean.currentApprovalStage) {
                    this.maximumInfoBean.currentApprovalStage = this.data.transferCreditData['taskName'];
                }
                this.commitSurveyResult()
                // this.productAttrListAnalysis();// 处理调查人结论数据    

                // 调查人结论优化
                if (this.maximumInfoBean.surveyConclusion == '3') {
                    this.surveyConclusionChange(this.maximumInfoBean.surveyConclusion)
                    // if (this.creditResultInfoBean.vetoType == '10' && this.maximumInfoBean.twoVetoType == '4110') {
                    this.vetoChange(this.maximumInfoBean.vetoType)
                    this.vetoTypeChange(this.maximumInfoBean.twoVetoType)
                    // }
                } else if (this.maximumInfoBean.surveyConclusion == '2') {
                    this.surveyConclusionChange(this.maximumInfoBean.surveyConclusion)
                }
                this.commfunc.setSessionDataCH('createBean', JSON.stringify(data));
                // this.guaranteeModeOptionsChange();
                // this.guaranteeModeOptionsChangeTwo()
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })

    }
    // 当前为审批岗并且调查人结论为不同意
    commitSurveyResult() {
        //当前为审批岗并且调查人结论为不同意，触发广播事件
        if (this.maximumInfoBean.surveyConclusion && this.maximumInfoBean.surveyConclusion == '3') {
            let param = {
                vetoType: this.maximumInfoBean.vetoType,
                twoVetoType: this.maximumInfoBean.twoVetoType,
                remark: this.maximumInfoBean.remark
            }
            this.communicationService.surveyResult.emit(param);
        } else {
            this.communicationService.surveyResult.emit({});
        }
    }
