    // 获取到期日期上下限
    judgmentDueDate() {
        let minStartMonth = moment(new Date(this.maximumInfoBean.applyDate)).add(this.dueDateScope.min, 'month').format('Y-MM-DD');
        let MaxEndMonth = moment(new Date(this.maximumInfoBean.applyDate)).add(this.dueDateScope.max, 'month').format('Y-MM-DD');
        this.dueDateScopeDate = {
            min: minStartMonth,
            max: MaxEndMonth
        }
    }
    // 获取清分状态（十级分类状态）
    queryTenLeven() {
        const params = {
            commonHeader: this.commonHeader,
            targetSysId: '304010',//目标系统
            clientNo: this.data.transferCreditData['custNo'],
            clientFlag: '0',//客户性质
            tranType: '2'  // 交易类型
        };
        this.creditCcmService.getTenLeven(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.list_array && data.list_array.length > 0) {
                    this.maximumInfoBean.tenClearStatus = data.list_array[0].ten_type;
                } else {
                    this.maximumInfoBean.tenClearStatus = null;
                }

            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
    // 数据字典
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.tenClearStatusList = codeValues['tenClearStatusList'];//十级清分状态
        this.tenGradeClassifys = codeValues['TenClass'];//十级分类手工清分
        this.maxContractTypeList = codeValues['maxContractTypeList'];//最高额担保合同种类
        this.guaranteeModeOptions = codeValues['guaranteeModeTransform'];//担保方式
        this.guaranteeModeOptionsOne = codeValues['guaranteeModeTransform'];//主担保方式
        this.guaranteeModeOptionsTwo = codeValues['guaranteeModeTransform'];//辅担保方式
        // this.currencys = codeValues['CURRENCY'];//货币号
        this.surveyConclusionList = codeValues['surveyConclusion'];//调查人结论

        // 否决类型
        this.vetoTypes = codeValues['VETOFIRDTS'];
        // 二级否决类型
        this.secondVetoTypes = [{ label: "请选择", value: "" }];
        this.personVeto = codeValues['personVeto'];//个人原因
        this.financeVeto = codeValues['financeVeto'];//财务因素
        this.manageVeto = codeValues['manageVeto'];//经营因素
        this.serveVeto = codeValues['serveVeto'];//服务因素
        this.otherVeto = codeValues['otherVeto'];//其他原因
        this.approvalStatusList = codeValues['maximumStatusList']//max码值

    }
    // 产品动态属性和值(申请金额和到期日期) 申请金额和到期日期  取产品运营平台-对应产品-金额范围
    queryDynamicProductAttributeOrValue() {
        const params = {
            'productId': this.data.transferCreditData['productNo'], // 产品编号传入    
            'custRequestNo': this.data.transferCreditData['custRequestNo']//  客户请求编号
        };
        this.creditCcmService.queryProductAttributes(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                console.log("asd")
                data.productFeatureList.forEach(item => {
                    //申请金额范围
                    if (item.productFeatureTypeId == "PCA0000012") {
                        this.applicationScope = {
                            min: item.productFeatureList[0].min,
                            max: item.productFeatureList[0].max
                        }
                    }
                    // 到期日期范围
                    if (item.productFeatureTypeId == "PCA0000014") {
                        this.dueDateScope = {
                            min: item.productFeatureList[0].min,
                            max: item.productFeatureList[0].max
                        }
                        this.judgmentDueDate()//获取到期日期上下限
                    }
                    // 获取产品货币号
                    console.log("asd")
                    if (item.productFeatureTypeId == "PCA0000003") {
                        this.judgmentCurrency(item);//处理货币号  
                    }
                    // 主担保方式
                    if (item.productFeatureTypeId == "PCA0000022") {
                        this.judgmentCurrencyTwo(item);//处理货币号  
                    }
                    // 辅担保方式
                    if (item.productFeatureTypeId == "PCA0010070") {
                        this.judgmentCurrencyTwos(item);//处理货币号  
                    }
                })
                this.guaranteeModeOptionsChange();
                this.guaranteeModeOptionsChangeTwo()

            } else {
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        })
    }


