    // 获取当前系统时间
    queryNowTime() {
        let nowTime = {};
        let erro = '';
        let params = this.httpService.handleParams(nowTime)
        $.ajax({
            url: TZB_HTTP_CUS + API.queryNowTime,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: (data) => {
                if (data.returnCode.code == 'success') {
                    if (data.nowDate) {
                        let time = data.nowDate.split(' ');//时间格式2018-01-03 00:00:00，截取年月日
                        this.maximumInfoBean.applyDate = this.commfunc.transDateN(new Date(time[0]));
                        this.maximumInfoBean.tellerDate = this.commfunc.transDateN(new Date(time[0]));
                    }
                } else {
                    erro = data.returnCode.message;
                }
            },
            error: (err) => {
                erro = '调用服务失败';
            }
        })
        if (erro != '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: erro });
            return
        }
    }

