import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../../http/overdue.http.service';
import { element } from 'protractor';
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";
import { ProcessHelper } from "app/pages/tzb/custom/overdue/common/processHelper";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";

// 5.分行层级审批意见
@Component({
    selector: "branch-examination-approval",
    templateUrl: './branch-examination-approval.component.html',
    styleUrls: ['./branch-examination-approval.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService, CustomCenterHttpService]
})

export class BranchExaminationApproval implements OnInit {
    code: any;
    workFlowResultType: any = [];

    baseInfo: any = {};                 // 基本信息
    overDoData: any;
    overDoDataInfo: any;
    isDetailFlag: boolean = false;     // 是否展示详情   不知道用处
    taskId: string = '';
    markFlag: string;

    catenaMoralRisk: any = [];          // 系列/道德性风险
    workEvaluteResume: any = [];        // 工作评价简述
    complianceAnalyze: any = [];        // 合规性分析
    duteousManageEvalute: any = [];     // 尽职信贷管理评价
    overduePersonLiable: any = [];      // 经办责任人列表
    isMaxMoney: boolean = true;//判断20万规则
    isDetail: boolean = true;//审批人员层级和是否详情进入
    // 操作人员等级
    operatingLevel: number = 0;
    // 消息
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    branchExamApprovalData: BranchExamApprovalData = new BranchExamApprovalData(this.commfunc);

    user: any;
    userId: string;//用户id
    processHelper: ProcessHelper = new ProcessHelper(
        this.commfunc,
        this.customService,
        this.commonlyMessage,
        this.router,
        this.customCenterHttpService,
        this.httpService
    );
    processId: string = '';

    historyConfig: any;

    pageData: any;

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public customService: CustomeHttpService,
        public route: ActivatedRoute,
        public customCenterHttpService: CustomCenterHttpService
    ) {
        // this.codeValues();
        // this.workFlowResultType = this.code['WorkFlowResultType'];
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        let params = this.route.snapshot.params;
        if (params.detail && params.detail == '1') {
            this.isDetail = false;
        }
        this.overDoData = JSON.parse(this.commfunc.getSessionDataCH('overdueData'));
        this.overDoDataInfo = JSON.parse(this.overDoData.baseInfo);
        this.pageInit();
        if (this.operatingLevel == 4) {
            this.bpmQueryTaskList();
        }
    }


    // 页面初始化函数
    pageInit() {
        let info = this.baseInfo = this.grapRouteParameter();
        this.processHelper.loadData({
            userId: this.userId,
            taskId: this.taskId,
            overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId
        })
        if (info != null) {
            this.loadEchoData();
        }
    }

    // 接收路由传参
    grapRouteParameter() {
        let ret = null;
        // let params = this.route.snapshot.params;
        let params = this.overDoData;//由路由获取数据改为session
        if (params.allowed != "4") {
            this.isDetail = false
        }
        this.taskId = params.taskId;
        this.processId = params.processId;
        this.historyConfig = {
            processId: this.processId
        }
        let confidential = this.branchExamApprovalData.getOverdueConfidential();
        if (confidential) {
            this.operatingLevel = confidential.operatingLevel;
            this.isDetailFlag = confidential.isDetailFlag;
            this.markFlag = confidential.markFlag;
        }
        let t = Object.keys(JSON.parse(params.baseInfo));
        if (t.length != 0) {
            ret = JSON.parse(params.baseInfo);  //基本信息
        }
        return ret;
    }

    // 加载回显数据 贷后检查、年检、逾期业务清收方案、授信工作开展情况
    loadEchoData() {
        let info = this.baseInfo;
        let param = {
            overdueCaseAnalyzeId: info.overdueCaseAnalyzeId,   // 案例编号
            custNo: info.custNo,                               // 客户号
            contractNumber: info.contractNumber,               // 合同编号/卡号
        }

        // 获取回显数据
        this.httpService.overdueCaseDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.pageData = data;

                // console.log(data)
                this.distribute(data);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    distribute(data: any) {
        this.catenaMoralRisk = data.catenaMoralRisk;
        this.workEvaluteResume = data.workEvaluteResume;
        this.complianceAnalyze = data.complianceAnalyze;
        this.duteousManageEvalute = data.duteousManageEvalute;
        this.overduePersonLiable = data.overduePersonLiable;
        console.log(this.overduePersonLiable);
        // 授信金额判断 
        console.log("kkl")
        if (data.selfDebtInfo && data.selfDebtInfo.selfDebtList && data.selfDebtInfo.selfDebtList.length > 0) {
            data.selfDebtInfo.selfDebtList.forEach(element => {
                if (element.creditBalance > 0 && element.creditAmount > 200000) {
                    // 20万元展示规则
                    this.isMaxMoney = true;
                }
            });
        }
    }


    saveClick() {
        let param = this.getUploadData();
        // if(!param.workEvaluteResume.issucess){
        //     this.commonlyMessage.msgs=[];
        //     this.commonlyMessage.msgs.push({ severity: 'error', summary: '错误：', detail: '请查看工作评价简介必输项是否输入' })
        //     return;
        // }
        // if(!param.catenaMoralRisk.issucess){
        //     this.commonlyMessage.msgs=[];
        //     this.commonlyMessage.msgs.push({ severity: 'error', summary: '错误：', detail: '请查看系列性/道德性风险必输项是否输入' })
        //     return;
        // }
        // if(!param.complianceAnalyze.issucess){
        //     this.commonlyMessage.msgs=[];
        //     this.commonlyMessage.msgs.push({ severity: 'error', summary: '错误：', detail: '请查看合规性分析必输项是否输入' })
        //     return;
        // }
        param['operatePage'] = '4';
        param['operateType'] = '1';
        if (param.catenaMoralRisk && param.catenaMoralRisk.relePersonRisk.length > 0 && param.catenaMoralRisk.relePersonRisk[0].releCustId == "") {
            // delete param.catenaMoralRisk.relePersonRisk
            param.catenaMoralRisk.relePersonRisk = []
        }
        // 处理责任人列表
        let personLiableList = {
            overduePersonLiable: {
                personLiable: param.duteousManageEvalute.overduePersonLiable
            }
        }
        Object.assign(param, personLiableList)
        // param.overduePersonLiable['personLiable'] = param.duteousManageEvalute.overduePersonLiable;
        delete param.duteousManageEvalute.overduePersonLiable;
        console.log(param)
        this.httpService.overdueCaseApprove(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            delete param.overduePersonLiable;
            this.httpService.overdueCaseApprove(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.commonlyMessage.logicSuccess(data.returnCode.message);
                } else {
                    this.commonlyMessage.logicError(data.returnCode.message);
                }
            }, err => {

                this.commonlyMessage.logicError('调用失败!');
            });
            this.commonlyMessage.logicError('调用失败!');
        });
    }


    showSaveDataAlert() {
        this.confirmationService.confirm({
            message: '请确认页面录入信息是否已保存',
            accept: () => {
                this.submitClick();
            }
        });
    }

    // 提交按钮
    submitClick() {

        let param = this.getUploadData();
        param['taskId'] = this.taskId;
        param['operatePage'] = '4';
        param['operateType'] = '2';
        // 根据金额不同校验不同
        if (this.isMaxMoney) {
            this.branchExamApprovalData.runEmptyCheck()//大于20万校验
        } else {
            this.branchExamApprovalData.runEmptyCheckTwo()
        }

        if (this.branchExamApprovalData.msg && this.branchExamApprovalData.msg.length) {
            this.commonlyMessage.logicError(this.commonlyMessage.combineMsgs(this.branchExamApprovalData.msg));
            return;
        }

        this.processHelper.setSubmitEnvironment(param, 'branch-exam-approval');
        this.processHelper.selectNextPerson(null);
    }

    getUploadData() {
        console.log("oop")
        let ret: any = {};

        this.branchExamApprovalData.source = this.pageData;

        let baseInfo = this.branchExamApprovalData.getBaseInfo();
        let catenaMoralRisk = this.fixCatenaMoralRisk(this.branchExamApprovalData.getCatenaMoralRisk());
        let workEvaluteResume = this.branchExamApprovalData.getWorkEvaluteResume();
        let complianceAnalyze = this.branchExamApprovalData.getComplianceAnalyze();
        let duteousManageEvalute = this.branchExamApprovalData.getDuteousManageEvalute();
        console.log(duteousManageEvalute);

        for (let property in baseInfo) {
            if (baseInfo.hasOwnProperty(property)) {
                ret[property] = baseInfo[property];
            }
        }

        ret.catenaMoralRisk = catenaMoralRisk;
        ret.workEvaluteResume = workEvaluteResume;
        ret.complianceAnalyze = complianceAnalyze;
        ret.duteousManageEvalute = duteousManageEvalute;
        console.log(ret.duteousManageEvalute);
        return ret;
    }


    cancel() {
        this.processHelper.cancel('');
    }

    returnBack() {
        this.processHelper.returnBack('');
    }

    // 打回拟稿人
    returnToStart() {
        this.processHelper.returnToStart('');
    }

    showNodeChoose() {
        this.processHelper.queryNodeInfo();
    }

    returnToAnyNode() {
        this.processHelper.returnToAnyNode('');
    }

    // 查询(历史)审批记录
    bpmQueryTaskList() {
        this.processHelper.bpmQueryTaskList({ processId: this.processId });
    }

    fixCatenaMoralRisk(catenaMoralRisk) {
        let ret = catenaMoralRisk;
        if (catenaMoralRisk) {
            if (catenaMoralRisk.relePersonRisk && catenaMoralRisk.relePersonRisk.length) {
                let list = catenaMoralRisk.relePersonRisk;
                let flag = true;
                list.forEach(element => {
                    if (element.myBankLoanAmt == '') {
                        element.myBankLoanAmt = 0;
                    }
                });
            }
        }
        return ret;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    backToSource() {
        let t = this.branchExamApprovalData.getOverdueConfidential();
        let backUrl = parseInt(t.backUrl);
        window.history.go(backUrl);
    }
}
