import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { BranchApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/base/localData";


export class BranchExamApprovalData extends BranchApprovalData {

    isCateMorRiskEmpty: boolean = false;
    isWorkEvaluteEmpty: Boolean = false;
    isComplianceEmpty: Boolean = false;
    isDuteousManageEmpty: Boolean = false;
    isBranchExamApprovalEmpty: Boolean = false;

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    getCatenaMoralRisk () {
        let catenaMoralRisk = this.getTemporaryData("overdue-catenaMoralRisk");

        let t: any;
        if (catenaMoralRisk && catenaMoralRisk.hasOwnProperty('timeStamp')) {
            t = catenaMoralRisk ? catenaMoralRisk.catenaMoralRisk : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.catenaMoralRisk);
        }

        catenaMoralRisk = t ? t : null;

        return catenaMoralRisk;
    }
    isCatenaMoralRiskEmpty () {
        let catenaMoralRisk = this.getCatenaMoralRisk();
        if (!catenaMoralRisk) {
            this.isCateMorRiskEmpty = true;
            this.isBranchExamApprovalEmpty = true;
            return;
        }
        // !catenaMoralRisk.relePersonRisk || 
        // !catenaMoralRisk.relePersonRisk.length
        if (!catenaMoralRisk.setRisk || 
            !catenaMoralRisk.moralRisk) {
            this.isCateMorRiskEmpty = true;
            this.isBranchExamApprovalEmpty = true;
            return;
        }
        // for (let i = 0, len = catenaMoralRisk.relePersonRisk.length; i < len; i++) {
        //     let element = catenaMoralRisk.relePersonRisk[i];
        //     // element.releCustName
        //     if (!element.releCustId || !element.releCustName || !element.myBankLoanAmt) {
        //         this.isBranchExamApprovalEmpty = true;
        //         break;
        //     }
        // }
    }

    getWorkEvaluteResume () {
        let workEvaluteResume = this.getTemporaryData("overdue-workEvaluteResume");

        let t: any;
        if (workEvaluteResume && workEvaluteResume.hasOwnProperty('timeStamp')) {
            t = workEvaluteResume ? workEvaluteResume.workEvaluteResume : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.workEvaluteResume);
        }

        workEvaluteResume = t ? t : null;

        return workEvaluteResume;
    }
    isWorkEvaluteResumeEmpty () {
        let workEvaluteResume = this.getWorkEvaluteResume();
        if (!workEvaluteResume) {
            this.isWorkEvaluteEmpty = true;
            this.isBranchExamApprovalEmpty = true;
            return;
        }
        if (!workEvaluteResume.subbranchWorkEvaluate || 
            !workEvaluteResume.approveWorkEvaluate) {
                this.isWorkEvaluteEmpty = true;
                this.isBranchExamApprovalEmpty = true;
            return;
        }
    }

    getComplianceAnalyze () {
        let complianceAnalyze = this.getTemporaryData("overdue-complianceAnalyze");

        let t: any;
        if (complianceAnalyze && complianceAnalyze.hasOwnProperty('timeStamp')) {
            t = complianceAnalyze ? complianceAnalyze.complianceAnalyze : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.complianceAnalyze);
        }

        complianceAnalyze = t ? t : null;

        return complianceAnalyze;
    }
    isComplianceAnalyzeEmpty () {
        debugger;
        let complianceAnalyze = this.getComplianceAnalyze();
        if (!complianceAnalyze) {
            this.isComplianceEmpty = true;
            this.isBranchExamApprovalEmpty = true;
            return;
        }
        if (!complianceAnalyze.compliance || 
            !complianceAnalyze.conditionExplain) {
                this.isComplianceEmpty = true;
                this.isBranchExamApprovalEmpty = true;
            return;
        }
    }

    getDuteousManageEvalute () {
        let duteousManageEvalute = this.getTemporaryData("overdue-duteousManageEvalute");

        let t: any;
        if (duteousManageEvalute && duteousManageEvalute.hasOwnProperty('timeStamp')) {
            t = duteousManageEvalute ? duteousManageEvalute.duteousManageEvalute : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.duteousManageEvalute);
        }

        duteousManageEvalute = t ? t : null;

        return duteousManageEvalute;
    }
    isDuteousManageEvaluteEmpty () {
        let duteousManageEvalute = this.getDuteousManageEvalute();
        if (!duteousManageEvalute) {
            this.isDuteousManageEmpty = true;
            this.isBranchExamApprovalEmpty = true;
            return;
        }
        if (!duteousManageEvalute.dutyAffirmLevel || 
            !duteousManageEvalute.dutyAffirmReason || 
            !duteousManageEvalute.innerManageProblems || 
            !duteousManageEvalute.innerManageProblems.length) {
                this.isDuteousManageEmpty = true;
                this.isBranchExamApprovalEmpty = true;
            return;
        }
    }

    setMsg4 () {
        // super.setMsg3();
        if (this.isCateMorRiskEmpty) {
            this.msg.push('分行层级审批意见：是否系列性风险/是否道德性风险有空数据');
        }
        if (this.isWorkEvaluteEmpty) {
            this.msg.push('分行层级审批意见：工作评价简介有空数据');
        }
        if (this.isComplianceEmpty) {
            this.msg.push('分行层级审批意见：合规性分析有空数据');
        } else {
            let complianceAnalyze = this.getComplianceAnalyze();
            // conditionExplain	情况说明
            let conditionExplain = complianceAnalyze.conditionExplain;
            if (conditionExplain && this.myFormValidate.chineseCharacterLength(conditionExplain) > 600) {
                this.msg.push('分行层级审批意见：合规性分析情况说明长度超过300字');
            }
        }
        if (this.isDuteousManageEmpty) {
            this.msg.push('分行层级审批意见：尽职信贷管理评价有空数据');
        } else {
            let duteousManageEvalute = this.getDuteousManageEvalute();
            //  dutyAffirmReason  责任认定原因
            let dutyAffirmReason = duteousManageEvalute.dutyAffirmReason;
            if (dutyAffirmReason && this.myFormValidate.chineseCharacterLength(dutyAffirmReason) > 600) {
                this.msg.push('分行层级审批意见：尽职信贷管理评价责任认定原因长度超过300字');
            }
        }
    }

    clear4 () {
        this.msg = [];
        this.isWorkEvaluteEmpty = false;
        this.isComplianceEmpty = false;
        this.isDuteousManageEmpty = false;
        this.isBranchExamApprovalEmpty = false;
    }


    runEmptyCheck () {
        this.clear4();

        this.isCatenaMoralRiskEmpty();
        this.isWorkEvaluteResumeEmpty();
        this.isComplianceAnalyzeEmpty();
        this.isDuteousManageEvaluteEmpty();

        this.setMsg4();

        let current = this.isBranchExamApprovalEmpty;
        return current;
    }
    runEmptyCheckTwo () {
        this.clear4();

        this.isCatenaMoralRiskEmpty();
        this.isWorkEvaluteResumeEmpty();
        this.isComplianceAnalyzeEmpty();
        this.isDuteousManageEvaluteEmpty();

        // this.setMsg4();

        let current = this.isBranchExamApprovalEmpty;
        return current;
    }
    

}
