import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { MyFormValidate } from "app/pages/tzb/custom/overdue/common/formValidate";
import { HistoryHelper } from "app/pages/tzb/custom/overdue/common/history";

export class CreditInfoData extends LocalData {

    selfDebtInfo: any;
    isNeedSelfDebtInfo :Boolean = false;
    isNeedBondsmanCondition: Boolean = false;
    isCreditInfoEmpty :Boolean = false;
    msg: any = []; // 提示信息

    source: any;


    myFormValidate: MyFormValidate = new MyFormValidate();
    historyHelper: HistoryHelper = new HistoryHelper();

    constructor (public commfunc: Commfunc) {
        super(commfunc);
        this.getTransBondsmanFlag();
    }

    getBaseInfo () {
        let baseInfo = this.getTemporaryData("overdue-baseInfo");
        return baseInfo;
    }

    getSelfDebtInfo () {
        let selfDebtInfo = this.getTemporaryData("overdue-selfDebtInfo");
        let t: any;
        if (selfDebtInfo && selfDebtInfo.hasOwnProperty('timeStamp')) {
            t = selfDebtInfo;
        } else {
            t =  this.historyHelper.getCurrentImmediately(this.source.selfDebtInfo.transformRationalitList);
        }
        selfDebtInfo = { transformRationalit: t ? t.transformRationalit : null };
        
        this.selfDebtInfo = selfDebtInfo;

        return selfDebtInfo;
    }

    getTransBondsmanFlag () {
        let trans_bondsman_flag = this.getTemporaryData("overdue-transformRationalit-bondsmanCondition");
        if (trans_bondsman_flag) {
            this.isNeedSelfDebtInfo = trans_bondsman_flag.transformRationalitFlag;
            this.isNeedBondsmanCondition = trans_bondsman_flag.hasBondsmanConditionFlag;
        }
    }

    isSelfDebtInfoEmpty () {
        if (this.isNeedSelfDebtInfo) {
            let selfDebtInfo =  this.getSelfDebtInfo ();
            if (!selfDebtInfo) {
                this.isCreditInfoEmpty = true;
                return;
            }
            if (!selfDebtInfo.transformRationalit) {
                this.isCreditInfoEmpty = true;
            }
        } 
    }

    setMsg () {
        if (this.isCreditInfoEmpty) {
            this.msg.push('授信信息：转化合理性评判为空');
        } else {
            if (this.isNeedSelfDebtInfo && this.myFormValidate.chineseCharacterLength(this.selfDebtInfo.transformRationalit) > 1000) {
                this.msg.push('授信信息：转化合理性评判长度超过500字');
            }
        }
    }

    clear () {
        this.msg = [];
        this.isCreditInfoEmpty = false;
    }


    runEmptyCheck () {
        this.clear();
        
        this.isSelfDebtInfoEmpty();
        
        this.setMsg();
        
        return this.isCreditInfoEmpty;
    }


}