import { Injectable, EventEmitter } from '@angular/core';
@Injectable()
// tslint:disable-next-line:class-name
export class ParentChildCommunicationService {
    // 事件广播
    public submitTransfer: EventEmitter<any> = new EventEmitter<any>();

    // 调用查询广播
    public queryTransfer: EventEmitter<any> = new EventEmitter<any>();

    // 展示广播
    public displayTransfer: EventEmitter<any> = new EventEmitter<any>();


    // 卡要素调整广播
    public cardElementUpdate: EventEmitter<any> = new EventEmitter<any>();
    //转化贷款广播
    public changeTabview: EventEmitter<any> = new EventEmitter<any>();
    //数据传送
    public sendData: EventEmitter<any> = new EventEmitter<any>();
    //转化贷款下一步限制
    public transformSendData: EventEmitter<any> = new EventEmitter<any>();

    //下一步保存
    public next: EventEmitter<any> = new EventEmitter<any>();
    //直销保存
    public save: EventEmitter<any> = new EventEmitter<any>();

    public saveCreditInfo: EventEmitter<any> = new EventEmitter<any>();
    public tabelChangeArr: EventEmitter<any> = new EventEmitter<any>();

    //担保信息to卡要素
    public toCreditelement: EventEmitter<any> = new EventEmitter<any>();

    public submitButton: EventEmitter<any> = new EventEmitter<any>();
    //调查人结论广播
    public surveyResult: EventEmitter<any> = new EventEmitter<any>();
    public custRequestNo: EventEmitter<any> = new EventEmitter<any>();

    public custInfoList:EventEmitter<any> = new EventEmitter<any>();

    // 批量预授信保存广播
    public branchSave:EventEmitter<any> = new EventEmitter<any>();
}
