import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../../http/overdue.http.service';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { GeneralApprovalPageCache } from "app/pages/tzb/custom/overdue/overdue-case/genenal-approval/common/pageCache";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { GeneralApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/genenal-approval/common/localData";
import { ProcessHelper } from "app/pages/tzb/custom/overdue/common/processHelper";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";

//6.总行层级审批意见
@Component({
    selector: "genenal-approval",
    templateUrl: './genenal-approval.component.html',
    styleUrls: ['./genenal-approval.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService, CustomeHttpService, CustomCenterHttpService]
})

export class GenenalApproval implements OnInit {
    
    // msgs: Message[] = [];
    code: any;
    baseInfo: any = {};                // 基本信息
    overDoData:any;
    overDoDataInfo:any;
    isDetailFlag: Boolean;     // 是否展示详情   不知道用处
    taskId: string = '';
    processId: string = '';

    headOfficeApprove: any = {
        headOfficeOption: ''
    };

    approvalnfo: string = ''; //本岗意见

    statusKeeper: GeneralApprovalPageCache = new GeneralApprovalPageCache(this.commfunc);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    generalApprovalData: GeneralApprovalData = new GeneralApprovalData(this.commfunc);
    isDetail:boolean = true;//详情进入
    user: any;
    userId: string;//用户id
    processHelper: ProcessHelper = new ProcessHelper(
        this.commfunc, 
        this.customerService, 
        this.commonlyMessage, 
        this.router, 
        this.customCenterHttpService,
        this.httpService
    );
    // workFlowResultType: any = [];


    // 审批历史
    tableData: any = [];
    historyConfig: any;
    pageData: any;
    markFlag: string = '';



    constructor(
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public customerService: CustomeHttpService,
        public customCenterHttpService: CustomCenterHttpService
    ) {
        // this.codeValues();
        // this.workFlowResultType = this.code['WorkFlowResultType'];
        this.getConfidential();
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        
        if (this.user.userId) {
          this.userId = this.user.userId;
        }
    }

    getConfidential () {
        let confidential = this.generalApprovalData.getOverdueConfidential();
        if (confidential) {
            this.isDetailFlag = confidential.isDetailFlag;      
            this.markFlag = confidential.markFlag;
        }
    }



    ngOnInit() {
          let params = this.route.snapshot.params;
          if(params.detail && params.detail == '1'){
              this.isDetail = false;
          }
        //   else{
        //       console.log("params.detail")
        //   }
        this.overDoData  = JSON.parse(this.commfunc.getSessionDataCH('overdueData'));
        this.overDoDataInfo = JSON.parse(this.overDoData.baseInfo);
        this.pageInit();
        // this.bpmQueryTaskList();
    }
    

    // 页面初始化函数
    pageInit () {
        let info = this.baseInfo = this.grapRouteParameter();
        this.processHelper.loadData({
            userId: this.userId,
            taskId: this.taskId,
            overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId
        })
        if (info != null) {
            this.loadEchoData();
        }
    }

    // 加载回显数据 贷后检查、年检、逾期业务清收方案、授信工作开展情况
    loadEchoData () {
        let info = this.baseInfo;
        let param = {
            overdueCaseAnalyzeId: info.overdueCaseAnalyzeId,   // 案例编号
            custNo: info.custNo,                               // 客户号
            contractNumber: info.contractNumber,               // 合同编号/卡号
        }
        
        // 获取回显数据
        this.httpService.overdueCaseDetail(param).subscribe(data => {
            // console.log(data)
            if (data.returnCode.code == 'success') {
                this.pageData = data;
                this.distribute(data);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    distribute (data: any) {
        if (this.statusKeeper.isDataNeedRollback()) {
            this.headOfficeApprove = this.statusKeeper.getHeadOfficeApprove().headOfficeApprove;
        } else {
            this.headOfficeApprove = data.headOfficeApprove;
        }
        this.setHeadOfficeApprove();
    }

    // 接收路由传参
    grapRouteParameter () {
        let ret = null;
        // let params = this.route.snapshot.params;
        let params = this.overDoData;//由路由获取数据改为session
        // 判断当前审批人层级
        if(params.allowed != "5"){
            this.isDetail = false
        }
        this.taskId = params.taskId;
        this.processId = params.processId;
        this.historyConfig = {
            processId: this.processId
        }
        let t = Object.keys(JSON.parse(params.baseInfo));
        if (t.length != 0) {
            ret = JSON.parse(params.baseInfo);  //基本信息
        }
        return ret;
    }

    
    saveClick () {
        let param = this.getUploadData();
        param['operatePage'] = '5';
        param['operateType'] = '1';

        this.postData(param, null);
    }

    showSaveDataAlert () {
        this.confirmationService.confirm({
            message: '请确认页面录入信息是否已保存',
            accept: () => {
              this.submitClick();
            }
          });
    }
    
    //提交按钮
    submitClick () {
        let param = this.getUploadData();
        param['taskId'] = this.taskId;
        param['operatePage'] = '5';
        param['operateType'] = '2';

        // this.generalApprovalData.runEmptyCheck()
        // if (this.generalApprovalData.msg && this.generalApprovalData.msg.length) {
        //     this.commonlyMessage.logicError(this.commonlyMessage.combineMsgs(this.generalApprovalData.msg));
        //     return;
        // }

        this.postData(param, () => {
            this.processHelper.setInfo(this.headOfficeApprove.headOfficeOption)
            this.processHelper.setSubmitEnvironment(null, 'general-approval');
            this.processHelper.outValueCall([]);
        })
    }

    postData (param: any, callBack: Function) {
        this.httpService.overdueCaseApprove(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (callBack) {
                    callBack();
                }
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用服务失败！');
        });
    }

    returnBack () {
        this.processHelper.returnBack(this.headOfficeApprove.headOfficeOption);
    }

    // 打回拟稿人
    returnToStart () {
        this.processHelper.returnToStart(this.headOfficeApprove.headOfficeOption);
    }

    showNodeChoose () {
        this.processHelper.queryNodeInfo();
    }

    returnToAnyNode () {
        this.processHelper.returnToAnyNode(this.headOfficeApprove.headOfficeOption);
    }


    inputCall () {
        this.setHeadOfficeApprove();
    }

    setHeadOfficeApprove () {
        this.statusKeeper.setHeadOfficeApprove(this.headOfficeApprove);
    }


    getUploadData () {
        let ret: any = {};
        debugger;
        this.generalApprovalData.source = this.pageData;

        let baseInfo = this.generalApprovalData.getBaseInfo();

        for (let property in baseInfo) {
            if (baseInfo.hasOwnProperty(property)) {
                ret[property] = baseInfo[property];
            }
        }

        ret.headOfficeApprove = this.headOfficeApprove;


        return ret;
    }

    // 查询(历史)审批记录
    bpmQueryTaskList() {
        this.processHelper.bpmQueryTaskList({ processId: this.processId });
    }
    
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    backToSource () {
        debugger;
        let t = this.generalApprovalData.getOverdueConfidential();
        let backUrl = parseInt(t.backUrl);
        window.history.go(backUrl);
    }

}
