import { Component, OnInit, Input } from '@angular/core';
import { CALENDAR_CN } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.constant';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ParentChildCommunicationService } from '../common/service/parent-child-comm.service';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { CreditFiduciaryBean } from './credit-fiduciary.bean';
import { CreditCcmService } from '../common/http/credit-ccm.http.service';
import { Message } from 'primeng/primeng';
@Component({
  selector: 'credit-fiduciary-payment',
  templateUrl: './credit-fiduciary-payment.component.html',
  styleUrls: ['./credit-fiduciary-payment.component.scss']
})
export class CreditFiduciaryPaymentComponent implements OnInit {
  count: any = 0;
  code: any[];
  payType: any[];//支付方式
  commonHeader: any;
  immediateFiduciary: any[];
  ch: any = CALENDAR_CN;//讲英文转化为中文
  userform: FormGroup;
  creditFiduciaryBean: CreditFiduciaryBean = new CreditFiduciaryBean();
  msgs: Message[] = [];
  blockedPanel: boolean;//遮罩层
  @Input('data') data;
  displayComponent: boolean = true;//是否显示当前组件
  displayFlag: boolean = true;//是否立即受托支付
  planPayDate:any;
  constructor(
    public fb: FormBuilder,
    private communicationService: ParentChildCommunicationService,
    public commfunc: Commfunc,
    private creditCcmService: CreditCcmService,
  ) {

    this.communicationService.queryTransfer.subscribe(_event => {
      if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditFiduciaryPayment') !== -1) {
        if(_event.flag){                     this.count = 0;                 }
        this.count++;
        if (this.count > 1) {
            return
        }
        this.getBusinessHappenTypeValue();
        // this.query();
      }
    });

  }

  ngOnInit() {
    this.userform = this.fb.group({
      'immediateFiduciary': new FormControl('', [Validators.required]),
      'planPayDate': new FormControl('', [Validators.required]),
      'payAmount': new FormControl('', [Validators.required]),
      'payeeName': new FormControl('', [Validators.required]),
      // 'payeeBank': new FormControl('', [Validators.required]),
      // 'payeeAccount': new FormControl('', [Validators.required]),
      // 'paymentType': new FormControl('', [Validators.required]),
      'purpose': new FormControl('', [Validators.required]),
    });
    this.onchange('0')

  }
  query() {
    // if (this.data.transferCreditData['isView']) {
    //   // this.hiddenBtn = true;
    //   this.blockedPanel = true;
    // }
    // this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.codeValues()
    this.payType = this.code["payType"];//支付方式
    this.immediateFiduciary = this.code['immediateFiduciary'];//是否立即受托支付
    this.queryCreditQuestionInfoList()
  }
  // 查询业务发生类型
  getBusinessHappenTypeValue() {
    const param = {
      'custRequestNo': this.data.transferCreditData['custRequestNo'],
    };
    this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // 产品动态属性“贷款支付的方式”的值===1展示组件
        if (data.dynamicProductValue && data.dynamicProductValue['PCA0000015'] && data.dynamicProductValue['PCA0000015'] === '1') {
          this.displayComponent = true;

          this.query();

        } else {
          this.displayComponent = false;
        }

      } else {
        this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
        this.displayComponent = false;
      }
    });
  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //初始化查询
  queryCreditQuestionInfoList(){
    let param = {
      "custRequestNo":this.data.transferCreditData['custRequestNo'],
      "questionType":"09"
    }
    this.creditCcmService.queryCreditQuestionInfoList(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        debugger
        this.creditFiduciaryBean = JSON.parse(data.questionList[data.questionList.length-1].questionReslponse);
        this.creditFiduciaryBean['cqiId'] = data.questionList[data.questionList.length-1].cqiId;
        //处理日期
        let timeStr = this.creditFiduciaryBean.planPayDate.slice(0,10);
        let time = timeStr.split('-');
        this.planPayDate = new Date(Number(time[0]),Number(time[1])-1,Number(time[2]));

        this.onchange(this.creditFiduciaryBean.immediateFiduciary)
      }else{
        
        this.msgs=[{severity:'error',summary:'提示',detail:data.returnCode.message}]
      }
    })
  }
  onchange(item) {
    if (item == '0') {
      this.displayFlag = true;
      this.userform.addControl('payeeBank', this.fb.control('', [Validators.required]));
      this.userform.addControl('payeeAccount', this.fb.control('', [Validators.required]));
      this.userform.addControl('paymentType', this.fb.control('', [Validators.required]));
    } else {
      this.displayFlag = false;
      this.userform.removeControl('payeeBank');
      this.userform.removeControl('payeeAccount');
      this.userform.removeControl('paymentType');
    }
  }
  save() {
    //校验的
    ////////console.log(this.creditFiduciaryBean)

    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return false;
      }
    }
    this.creditFiduciaryBean.planPayDate = this.commfunc.transDateN(this.planPayDate);
    const params = {
      custRequestNo: this.data.transferCreditData['custRequestNo'],
      // custRequestNo: '01',
      cqiId:this.creditFiduciaryBean.cqiId,
      questionType: '09',
      questionReslponse: JSON.stringify(this.creditFiduciaryBean),
    };
    this.creditCcmService.saveFacQuestion(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.msgs = [{ severity: 'success', summary: '提示：', detail: '保存成功' }];
      } else {
        this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
    });
  }
}
