import { Component, OnInit, Output, Input, OnChanges, ViewChild } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../../http/overdue.http.service';
import { CreditDevelopmentComponent } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/credit-development/credit-development.component";
import { ClearancePlanComponent } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/clearance-plan/clearance-plan.component";
import { SelfDebt } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/selfDebt/selfDebt.component";
import { BranchApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-approval/base/localData";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { ProcessHelper } from "app/pages/tzb/custom/overdue/common/processHelper";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";

// 4.支行层级审批意见
@Component({
    selector: "branch-approval",
    templateUrl: './branch-approval.component.html',
    styleUrls: ['./branch-approval.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService, CustomCenterHttpService]
})

export class BranchApproval implements OnInit {
    code: any;
    workFlowResultType: any = [];
    // 消息
    msgs: Message[] = [];

    taskId: string = '';
    // 路由参数
    baseInfo: any = {};                // 基本信息
    isDetailFlag: boolean = false;     // 是否展示详情   不知道用处
    markFlag: string;
    processId: string = '';

    // 输入到组件的数据
    afterLoanInspection: any = [];          // 贷后检查情况
    annualInspection: any = [];             // 年检情况
    clearScheme: any = [];                  // 逾期业务清收方案
    workCarryOutCondition: any = [];        // 授信工作开展情况

    // 操作人员等级
    operatingLevel: number = 0;
    branchApprovalData: BranchApprovalData = new BranchApprovalData(this.commfunc);
    commonlyMessage: CommonlyMessage = new CommonlyMessage();

    overDoData:any;
    isDetail:boolean = true;//审批人员层级和是否详情进入
    user: any;
    userId: string;//用户id
    processHelper: ProcessHelper = new ProcessHelper(
        this.commfunc, 
        this.customService, 
        this.commonlyMessage, 
        this.router, 
        this.customCenterHttpService,
        this.httpService
    );

    historyConfig: any;

    pageData: any;

    ParentBusinessType: string;

    constructor(
        public commfunc: Commfunc,
        public httpService: OverdueHttpService,
        public customService: CustomeHttpService,
        public confirmationService: ConfirmationService,
        public route: ActivatedRoute,
        public router: Router,
        public customCenterHttpService: CustomCenterHttpService
    ) {
        // this.codeValues();
        // this.workFlowResultType = this.code['WorkFlowResultType'];
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
          this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        let params = this.route.snapshot.params;
        if(params.detail && params.detail == '1'){
            this.isDetail = false;
        }
       this.overDoData  = JSON.parse(this.commfunc.getSessionDataCH('overdueData'));
        console.log(this.overDoData)
        this.grapRouteParameter();
        this.pageInit();
        this.bpmQueryTaskList();
        // if (this.operatingLevel == 3) {
        //     this.bpmQueryTaskList();
        // }
    }


    // 页面初始化函数
    pageInit () {
        // let info = this.baseInfo = this.grapRouteParameter();
        let info = this.overDoData.baseInfo;
        let baseInfo = JSON.parse(this.overDoData.baseInfo)
        this.processHelper.loadData({
            userId: this.userId,
            taskId: this.overDoData.taskId,
            // overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId
            overdueCaseAnalyzeId: baseInfo.overdueCaseAnalyzeId
        })
        if (info != null) {
            this.loadEchoData();
        }
    }

    // 接收路由传参
    grapRouteParameter () {
        let ret = null;
        
        // let params = this.route.snapshot.params;
        let params = this.overDoData;
        if(params.allowed != "3"){
            this.isDetail = false
        }
        this.taskId = params.taskId;
        this.processId = params.processId;
        this.historyConfig = {
            processId: this.processId
        }
        let confidential = this.branchApprovalData.getOverdueConfidential();
        if (confidential) {
            this.operatingLevel = confidential.operatingLevel; 
            // this.isDetailFlag = confidential.isDetailFlag == "true" ? true : false;     // 是否是展示详情，true展示详情，否之展示页面
            this.isDetailFlag = confidential.isDetailFlag;
            this.markFlag = confidential.markFlag;
        }
        
        let t = Object.keys(JSON.parse(params.baseInfo));
        if (t.length != 0) {
            ret = JSON.parse(params.baseInfo);  //基本信息
        }
        return ret;
    }

    // 加载回显数据 贷后检查、年检、逾期业务清收方案、授信工作开展情况
    loadEchoData () {
        // let info = this.baseInfo;
        let info = JSON.parse(this.overDoData.baseInfo);
        let param = {
            overdueCaseAnalyzeId: info.overdueCaseAnalyzeId,   // 案例编号
            custNo: info.custNo,                               // 客户号
            contractNumber: info.contractNumber,               // 合同编号/卡号
        }
        
        // 获取回显数据
        this.httpService.overdueCaseDetail(param).subscribe(data => {
            // console.log(data)
            if (data.returnCode.code == 'success') {

                this.pageData = data;

                this.distribute(data);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    distribute (data: any) {
        this.afterLoanInspection = data.afterLoanInspection;
        this.annualInspection = data.annualInspection;
        this.clearScheme = data.clearScheme;
        this.workCarryOutCondition = data.workCarryOutCondition;
        this.ParentBusinessType = data.parentBusinessType;
    }



    saveClick () {
        let param = this.getUploadData();
        // if(!param.workCarryOutCondition.issuccess){
        //     this.commonlyMessage.msgs=[];
        //     this.commonlyMessage.msgs.push({ severity: 'error', summary: '错误：', detail: '请查看授信工作开展情况必输项是否输入' })
        //     return;
        // }
        param['operatePage'] = '3';
        param['operateType'] = '1';

        // console.log(param);
        this.httpService.overdueCaseApprove(param).subscribe(data => {
            // console.log(data)
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    showSaveDataAlert () {
        this.confirmationService.confirm({
            message: '请确认页面录入信息是否已保存',
            accept: () => {
              this.submitClick();
            }
          });
    }

    //提交按钮
    submitClick () {

        let param = this.getUploadData();
        param['taskId'] = this.taskId;
        param['operatePage'] = '3';
        param['operateType'] = '2';

        this.branchApprovalData.runEmptyCheck();
        if (this.branchApprovalData.msg && this.branchApprovalData.msg.length) {
            this.commonlyMessage.logicError(this.commonlyMessage.combineMsgs(this.branchApprovalData.msg));
            return;
        }

        this.processHelper.setSubmitEnvironment(param, 'branch-approval');
        this.processHelper.selectNextPerson(null);
    }


    getUploadData () {
        debugger;
        let ret: any = {};

        this.branchApprovalData.source = this.pageData;

        let baseInfo = this.branchApprovalData.getBaseInfo();
        let clearScheme = this.branchApprovalData.getClearScheme();
        let workCarryOutCondition = this.branchApprovalData.getWorkCarryOutCondition();
        console.log(workCarryOutCondition);
        console.log(clearScheme);
        console.log(baseInfo);
        for (let property in baseInfo) {
            if (baseInfo.hasOwnProperty(property)) {
                ret[property] = baseInfo[property];
            }
        }

        ret.clearScheme = clearScheme;
        ret.workCarryOutCondition = workCarryOutCondition;

        return ret;
    }

    cancel () {
        this.processHelper.cancel('');
    }

    returnBack () {
        this.processHelper.returnBack('');
    }

    // 打回拟稿人
    returnToStart () {
        this.processHelper.returnToStart('');
    }

    showNodeChoose () {
        this.processHelper.queryNodeInfo();
    }

    returnToAnyNode () {
        this.processHelper.returnToAnyNode('');
    }

    // 查询(历史)审批记录
    bpmQueryTaskList() {
        this.processHelper.bpmQueryTaskList({ processId: this.processId });
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    backToSource () {
        let t = this.branchApprovalData.getOverdueConfidential();
        let backUrl = parseInt(t.backUrl);
        window.history.go(backUrl);
    }

}
