import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { OverdueReasonData } from "app/pages/tzb/custom/overdue/overdue-case/overdue-reasons/base/localData";

export class BranchApprovalData extends OverdueReasonData {

    isBranchApprovalEmpty: Boolean = false;
    workCarryOutCondition: any;
    clearScheme: any;

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    getWorkCarryOutCondition () {
        let workCarryOutCondition = this.getTemporaryData("overdue-workCarryOutCondition");

        let t: any;
        if (workCarryOutCondition && workCarryOutCondition.hasOwnProperty('timeStamp')) {
            t = workCarryOutCondition ? workCarryOutCondition.workCarryOutCondition : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.workCarryOutCondition);
        }

        workCarryOutCondition = t ? t : null;
        
        return workCarryOutCondition;
    }
    isWorkCarryOutConditionEmpty () {
        let workCarryOutCondition = this.getWorkCarryOutCondition();
        if (!workCarryOutCondition) {
            this.isBranchApprovalEmpty = true;
            return;
        }
        for (let property in workCarryOutCondition) {
            if (workCarryOutCondition.hasOwnProperty(property) && property.toString().indexOf('history') == -1) {
                if (!workCarryOutCondition[property]) {
                    this.isBranchApprovalEmpty = true;
                }
            }
        }
    }


    getClearScheme () {
        let clearScheme = this.getTemporaryData("overdue-clearScheme");

        let t: any;
        if (clearScheme && clearScheme.hasOwnProperty('timeStamp')) {
            t = clearScheme ? clearScheme.clearScheme : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.clearScheme);
        }

        clearScheme = t ? t : null;
        
        return clearScheme;
    }
    isClearSchemeEmpty () {
        let clearScheme = this.getClearScheme();
        if (!clearScheme) {
            this.isBranchApprovalEmpty = true;
            return;
        }
        for (let property in clearScheme) {
            if (clearScheme.hasOwnProperty(property) && property.toString().indexOf('history') == -1) {
                if (!clearScheme[property]) {
                    this.isBranchApprovalEmpty = true;
                }
            }
        }
    }


    setMsg3 () {
        // super.setMsg2();
        if (this.isBranchApprovalEmpty) {
            this.msg.push('支行层级审批意见：授信工作开展情况有空数据');
        } else {
            let workCarryOutCondition = this.getWorkCarryOutCondition();

            // accessOffset	客户准入偏差
            let accessOffset = workCarryOutCondition.accessOffset;
            if (accessOffset && this.myFormValidate.chineseCharacterLength(accessOffset) > 1000) {
                this.msg.push('支行层级审批意见：客户准入偏差长度超过500字');
            }
            // preLoanInspectionCondition	贷前调查开展情况
            let preLoanInspectionCondition = workCarryOutCondition.preLoanInspectionCondition;
            if (preLoanInspectionCondition && this.myFormValidate.chineseCharacterLength(preLoanInspectionCondition) > 1000) {
                this.msg.push('支行层级审批意见：贷前调查开展情况长度超过500字');
            }
            // examineAndApproveCondition	审查审批工作开展情况
            let examineAndApproveCondition = workCarryOutCondition.examineAndApproveCondition;
            if (examineAndApproveCondition && this.myFormValidate.chineseCharacterLength(examineAndApproveCondition) > 1000) {
                this.msg.push('支行层级审批意见：审查审批工作开展情况长度超过500字');
            }
            // afterLoanInspectionCondition	贷后管理工作开展情况      
            let afterLoanInspectionCondition = workCarryOutCondition.afterLoanInspectionCondition;
            if (afterLoanInspectionCondition && this.myFormValidate.chineseCharacterLength(afterLoanInspectionCondition) > 1000) {
                this.msg.push('支行层级审批意见：贷后管理工作开展情况长度超过500字');
            }
        }
    }

    clear3 () {
        this.msg = [];
        this.isBranchApprovalEmpty = false;
    }


    runEmptyCheck () {

        this.clear3();

        this.isWorkCarryOutConditionEmpty();

        this.setMsg3();

        let current = this.isBranchApprovalEmpty;
        return current;
    }

}
