import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message } from 'primeng/primeng';

import { CreditCardBean } from '../bean/credit-card.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { checkZzNum, percent, integralCard } from 'app/@uisftech/common/service/validator';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-credit-largestaging',
    templateUrl: './credit-largestaging.component.html',
    styleUrls: ['./credit-largestaging.component.scss'],
})

// 大额分期
// tslint:disable-next-line:component-class-suffix
export class CreditLargestaging implements OnInit {
    inValue: any;
    prodList: any;
    merchantName: any;
    merchantNames: any[];
    largeStageRate: any[];
    largeStagePeriods: any[];
    commodityNames: any[];
    hiddenBtn: boolean = false; // 控制按钮是否展示

    blockedPanel: boolean = false; // 如果只有查看权限的情况下不能编辑数据
    // 提示信息
    msgs: Message[] = [];

    // 分期类别
    largeStageType = [];

    // 手续费支付方式
    paymentMethod = [];
    cardNoList: any[] = [];//借记卡列表
    myform: FormGroup = this.fb.group({
        // 'largeStageType': new FormControl('', [Validators.required]),
        // 'productName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'merchantName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'merchantNo': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9]{1,15}$/), Validators.required]),
        'commodityName': new FormControl('', [Validators.required]),
        'commodityNo': new FormControl('', [Validators.required]),
        'largeStagePeriods': new FormControl('', [checkZzNum, Validators.required]),
        'largeStageRate': new FormControl('', [Validators.required]),
        'paymentMethod': new FormControl('', [Validators.required]),
        'debitCardNo': new FormControl('', [integralCard, Validators.required]),
        'debitCardName': new FormControl('', [Validators.required]),
        'applyAmt': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]*$/)]),
    }); //校验

    // 信用卡信息bean
    creditCardBean: CreditCardBean = new CreditCardBean();

    displayComponent: boolean = true; // 是否展示当前组件

    // tslint:disable-next-line:no-input-rename
    @Input('data') data;

    // 判断一开始主卡是否有数据
    isFlagList = false;
    prodDisplay: boolean;//商品列表弹框
    isApprove: boolean = true;//判断是否是审批节点
    // 构造器
    constructor(
        public fb: FormBuilder,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute
    ) {
        this.communicationService.queryTransfer.subscribe(_event => {
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditLargestaging') !== -1) {
                if(this.data.transferCreditData.varMap.dataMap.cardType){
                    this.displayComponent = false;//续卡不展示
                }else{
                    this.getBusinessHappenTypeValue()
                }
            }
        });
    }

    // 初始化方法
    ngOnInit() {
        //判断是否是审批节点
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'approve') {
            this.isApprove = false;
        }
    }
    query() {
        // 初始化查询
        this.getCodeValues();
        setTimeout(() => {
            this.queryLargestagingDetail();
        }, 100);

        this.getMerchantName();
        this.queryCreditNo()
    }
    getMerchantName() {//获取商户名称、编号
        let productCode = this.data.transferCreditData.varMap.productId || this.data.transferCreditData.varMap.productNo;
        if (!productCode) {
            productCode = this.data.transferCreditData.varMap.dataMap.productId || this.data.transferCreditData.varMap.dataMap.productNo
        }
        let param = {
            clientNo: this.data.transferCreditData['custNo'],
            productCode: productCode
        }
        this.creditCcmService.supplyChainCoopAndGroupInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.infoList && data.infoList.length > 0) {
                    this.merchantNames = this.commfunc.listToLabelvalue(data.infoList, 'projectName', 'projectId');
                    this.merchantNames.unshift({ label: '请选择', value: '' });
                    this.querycommodityNo();

                }
            } else {
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
            }
        });
    }
    querycommodityNo() {//通过商户号查询商品编号、名称'DEFQ01' 
        debugger
        this.creditCcmService.selectProdList({ merchant: this.creditCardBean.merchantNo }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.prodList = data.returnList;
            } else {
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
            }
        });
    }

    //产品列表显示
    prodFocus() {
        if (this.creditCardBean.merchantNo) {
            this.prodDisplay = true;
        } else {
            this.msgs = [{ severity: 'info', summary: '提示', detail: '请先选择商户名称和编号！' }]
        }

    }
    // 查询业务发生类型
    getBusinessHappenTypeValue() {
        const param = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
        };
        this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 产品动态属性“业务发生类型”的值===3，展示组件
                if (data.dynamicProductValue && data.dynamicProductValue['PCA0000199'] === '3') {
                    this.displayComponent = true;
                    this.query();
                } else {
                    this.displayComponent = false;
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
                this.displayComponent = false;
            }
        });
    }
    // 获取下拉框值
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        // 分期类别：
        this.largeStageType = codeValues['largeStageType'];
        // 手续费支付方式：
        this.paymentMethod = codeValues['feePaymentMethod'];
        this.largeStagePeriods = codeValues[''];
        this.largeStageRate = codeValues[''];
    }




    onMerchChange(event) {//获取商品名称
        this.creditCardBean.merchantNo = event.value;
        for (let i in this.merchantNames) {
            if (this.merchantNames[i].value == event.value) {
                this.creditCardBean.merchantName = this.merchantNames[i].label;
                break;
            }
        }
        this.querycommodityNo();
    }
    // 详细信息查询
    queryLargestagingDetail() {
        const params = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '08'        // 附卡类型
        };
        this.creditCcmService.queryFacQuestions(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.list.length) {
                    this.isFlagList = true;
                }
                for (const item of data.list) {
                    const questionReslponse = JSON.parse(item['questionReslponse']);
                    questionReslponse.cqiId = item['cqiId'];
                    this.creditCardBean = questionReslponse;
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
            }
        });
    }
    //借记卡号查询
    queryCreditNo() {
        this.creditCcmService.BZ_0700300003901_COREBANK({ custNo: this.data.transferCreditData['custNo'] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.cardNoList = this.commfunc.listToLabelvalue(data.infoList, 'cardNo', 'cardNo', 'acctName');
                if (this.commfunc.listToLabelvalue(data.infoList, 'cardNo', 'cardNo', 'acctName').length > 0) {
                    this.creditCardBean.debitCardNo = this.commfunc.listToLabelvalue(data.infoList, 'cardNo', 'cardNo', 'acctName')[0].value
                    this.cardNoChange(this.creditCardBean.debitCardNo);
                }
            }
        })
    }
    //回显户名
    cardNoChange(event) {
        for (let i = 0; i < this.cardNoList.length; i++) {
            if (this.cardNoList[i]['value'] == event) {
                this.creditCardBean.debitCardName = this.cardNoList[i].item;
                break;
            }
        }
    }
    // 提交之前做一次校验
    preValidateForSubmit() {
        //校验的
        for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
        //处理是否提交
        for (const i in this.myform.controls) {
            if (!this.myform.controls[i].valid) return false;
        }
        return true;
        // 校验的
    }

    // 新增大额分期
    addLargestaging() {
        if (!this.preValidateForSubmit()) return;
        const params = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '08',
            'questionReslponse': JSON.stringify(this.creditCardBean)
        };
        if (this.isFlagList) {
            params['cqiId'] = this.creditCardBean.cqiId;
        }
        this.creditCcmService.saveFacQuestion(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功' }];
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
            }
        });
    }

    //商品选择
    selected(col) {
        this.creditCardBean.commodityName = col.prodDesc;
        this.creditCardBean.commodityNo = col.prodId
        this.creditCardBean.largeStagePeriods = col.nbrMths;
        this.creditCardBean.largeStageRate = col.feePcnt;
        this.creditCardBean.paymentMethod = col.feeFlag + '';
        this.prodDisplay = false;
    }
}