import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, checkNumber } from 'app/@uisftech/common/service/validator';
import { Message, ConfirmationService, BlockableUI } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { OverdueHttpService } from '../../http/overdue.http.service';
import { element } from 'protractor';
import { debug } from 'util';
import { CreditIfoBean } from './bean/credit-ifo.bean';
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { CreditInfoData } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/base/localData";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";

//1. 授信信息
@Component({
    selector: "credit-ifo",
    templateUrl: './credit-ifo.component.html',
    styleUrls: ['./credit-ifo.component.scss'],
    providers: [Commfunc, OverdueHttpService, ConfirmationService]
})

export class CreditIfo implements OnInit {
    // msgs: Message[] = [];
    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    code: any;
    creditIfoBean: CreditIfoBean = new CreditIfoBean();
    //传入组件的参数
    selfdebtInValue: any = [];  //自身债务
    reledebtInValue: any = [];  //关联债务
    // 接收路由传参
    baseInfo: any = {};              //基本信息
    overDoData:any;
    overDoDataInfo:any;

    isDetailFlag: boolean = false;  //是否展示详情
    transformRationalit: string = '';
    markFlag: string = '';

    creditInfoData: CreditInfoData = new CreditInfoData(this.commfunc);
    transformRationalitFlag: Boolean = false;
    localData: LocalData = new LocalData(this.commfunc);

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: OverdueHttpService,
        public route: ActivatedRoute,
    ) {
        // 
        this.codeValues() //调用方法，获取全部码值
        // this.statusId = this.code['useStatus'];
    }



    ngOnInit() {
        this.overDoData  = JSON.parse(this.commfunc.getSessionDataCH('overdueData'));
        this.grapRouterParameters();
    }

    grapRouterParameters () {

        this.getConfidential();

        // let params = this.route.snapshot.params;
        let params = this.overDoData;//由路由获取数据改为session
        // 接收路由传参
        let t = Object.keys(JSON.parse(params.baseInfo));
        if (t.length != 0) {
            this.baseInfo =JSON.parse(params.baseInfo) ;//基本信息
            // 回显
            this.showDetailData();
        }
    }

    getConfidential () {
        let confidential = this.creditInfoData.getOverdueConfidential();
        if (confidential) {
            this.isDetailFlag = confidential["isDetailFlag"];   
            this.markFlag = confidential.markFlag;  
        }
    }

    //获取回显数据
    showDetailData() {
        let param = {
            overdueCaseAnalyzeId: this.baseInfo.overdueCaseAnalyzeId,   //案例编号
            custNo: this.baseInfo.custNo,            //客户号
            contractNumber: this.baseInfo.contractNumber,   //合同编号/卡号
        }
        // console.log(param);
        this.httpService.overdueCaseDetail(param).subscribe(data => {
            // console.log(data);
            if (data.returnCode.code == 'success') {
                this.selfdebtInValue = data.selfDebtInfo;        //自身债务信息
                this.reledebtInValue = data.releDebtInfo;        //关联方债务

                this.grapTransformRationalitFlag();
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    grapTransformRationalitFlag () {
        let contractNumber = this.baseInfo.contractNumber;
        let t = this.selfdebtInValue.selfDebtList.filter(item => item.contractNumber == contractNumber);
        let hasBondsmanConditionFlag: Boolean = false;

        if (t && t.length) {
            // this.transformRationalitFlag = t[0].transformLoan == '1';
            this.transformRationalitFlag = t[0].transformshemeList && t[0].transformshemeList.length;
            hasBondsmanConditionFlag = t[0].bondsmanList && t[0].bondsmanList.length; // t[0].guaranteeType == '101' && 
        }
        // 应罗依妮要求更改显示规则
        // console.log("ppl")
        this.transformRationalitFlag = false
        if(this.selfdebtInValue.selfDebtList && this.selfdebtInValue.selfDebtList.length >= 1){
            this.selfdebtInValue.selfDebtList.forEach(element => {
                if(element.transformLoan == "1"){
                    this.transformRationalitFlag = true;
                    }
            });
        }
        // 暂存
        this.localData.setTemporaryData('overdue-transformRationalit-bondsmanCondition', {
            transformRationalitFlag: this.transformRationalitFlag,
            hasBondsmanConditionFlag: hasBondsmanConditionFlag
        });
        // console.log(this.transformRationalitFlag)
    }

    //保存按钮
    saveClick() {
        if (this.creditInfoData.runEmptyCheck()) {
            this.commonlyMessage.logicError(this.creditInfoData.msg[0]);
            return;
        }
        let param = {
            operatePage: '1',
            operateType: '1',          //操作类型Y(1-保存  2-提交)
            selfDebtInfo: {
                transformRationalit: this.transformRationalit
            }
        }
        //将基本信息的数据放到param中作为传参传给后台
        for (let key in this.baseInfo) {
            param[key] = this.baseInfo[key];
        }
        // console.log(param);
        this.httpService.overdueCaseAnalyze(param).subscribe(data => {
            // console.log(data)
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }


    //自身债务的回调
    selfdebtCall(param) {
        this.transformRationalit = param;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        this.code = ret
        return this.code; //code是自己声明的变量
    }

    backToSource () {
        let t = this.creditInfoData.getOverdueConfidential();
        let backUrl = parseInt(t.backUrl);
        window.history.go(backUrl);
    }

}
