import { Component } from '@angular/core';
import { OnInit } from '@angular/core/src/metadata/lifecycle_hooks';

import { ConfirmationService, Message } from 'primeng/primeng';

import { ParamsBean } from '../bean/params.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-credit-guarantor',
    templateUrl: './credit-guarantor.component.html',
    styleUrls: ['./credit-guarantor.component.scss'],
})

// 担保人信息
// tslint:disable-next-line:component-class-suffix
export class CreditGuarantor implements OnInit {
    // 提示信息
    msgs: Message[] = [];

    // 分页参数信息
    paramsBean: ParamsBean = new ParamsBean();

    // 控制新增页面
    displayAdd = false;

    // 最高额合同担保人信息
    displayReferenceAdd = false;

    // 传值到子组件
    param_data: any;

    // 新增修改title
    addOrUpdateTitle = '';

    // 构造器
    constructor(public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService) { }

    // 初始化方法
    ngOnInit() {
        this.queryGuarantor();
    }

    // 展示新增页面
    add() {
        this.displayAdd = true;
        this.param_data = '';
        this.addOrUpdateTitle = '新增';
    }

    modify(col) {
        this.addOrUpdateTitle = '修改';
        this.displayAdd = true;
        this.param_data = col.facProcessQuestionId;
    }

    // 查询担保人信息
    queryGuarantor() {
        const param = {
            'facProcessId': '100010',
            'questionType': 'guaranteeInfo'
        };
        const first1 = [];
        this.creditCcmService.queryFacProcessQuestions(param).subscribe(data => {
            if (data.Service.Header.RetStatus === 'S') {
                for (const params of data.Service.Body.list) {
                    let str1 = '';
                    let str2 = '{';
                    str2 += '"facProcessQuestionId":' + '"' + params.facProcessQuestionId + '"' + ',';
                    str1 = params.questionReslponse;
                    str1 = str1.substr(1, str1.length - 2);
                    str2 += str1;
                    str2 = str2 + '}';
                    first1.push(JSON.parse(str2));
                }
                this.paramsBean.queryList = first1;
            }
        });
    }

    // 引用
    referenceAdd() {
        this.displayReferenceAdd = true;
    }

    // 删除
    delete(col) {
        this.confirmationService.confirm({
            message: '确定要删除这些数据吗？',
            accept: () => {
                const params = {
                    'facProcessId': '100010',
                    'facProcessQuestionId': col.facProcessQuestionId
                };
                this.creditCcmService.deleteFacProcessQuestions(params).subscribe(data => {
                    if (data.Service.Header.RetStatus === 'S') {
                        this.queryGuarantor();
                    }
                });
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }
}