import { PageStatusKeeper } from "app/pages/tzb/custom/overdue/common/pageStatus";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

export class GeneralApprovalPageCache extends PageStatusKeeper {


    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }


    setHeadOfficeApprove (headOfficeApprove) {
        let obj = {};
        this.toolBag.copyObjectWithSkipping(obj, headOfficeApprove, 'historyProcess');
        this.commfunc.setSessionDataCH("overdue-headOfficeApprove", JSON.stringify({
            headOfficeApprove: obj,
            timeStamp: this.getTimeStamp().timeStamp
        }));
    }

    getHeadOfficeApprove () {
        return this.getTemporaryData("overdue-headOfficeApprove");
    }

    isDataNeedRollback () {
        let ret = false;
        
        this.timeStamp = this.getTimeStamp().timeStamp;
        let dataTimeStamp = this.getHeadOfficeApprove() ? this.getHeadOfficeApprove().timeStamp : null;
        
        if (this.timeStamp && dataTimeStamp) {
            ret = !this.changeMonitor(this.timeStamp, dataTimeStamp);
        }
        
        return ret;
    }

    
}