import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message, ConfirmationService } from 'primeng/primeng';

import { checkCard, checkTel } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CreditCardBean } from '../bean/credit-card.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-credit-mcard',
    templateUrl: './credit-mcard.component.html',
    styleUrls: ['./credit-mcard.component.scss'],
})

// tslint:disable-next-line:component-class-suffix
export class CreditMcard implements OnInit {

    count: any = 0;
    cardTuNameList: any[];
    custRequestNoFromScardGetMcard: string;//续卡
    spellList: any = [];
    params: any;
    telPhone: any;
    // hiddenBtn: boolean = false; // 控制按钮是否展示

    blockedPanel: boolean = false; // 如果只有查看权限的情况下不能编辑数据

    // 提示信息
    msgs: Message[] = [];
    // 是否凸字
    isTuWordList = [];
    creditCardNum: any;
    // 是否需要PIN密信
    isNeedPinPasswordList = [];
    // 消费需要密码
    isNeedPasswordList = [];
    // 开通短信通知
    inputValues: any;
    isOpenSmsNoticeList = [];
    // 证件类型
    idenTypeList = [];
    // 卡片类型
    creditCardTypeList = [];
    // 主卡面板
    mainCardLayoutList = [];
    // 判断一开始主卡是否有数据
    isFlagList = false;
    // mainCardTuNameList: any[];//凸字列表
    myformChild: FormGroup = this.fb.group({
        'creditCardType': new FormControl('', [Validators.required]),
        'custNo': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9]{1,15}$/), Validators.required]),
        'custName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'documentType': new FormControl('', [Validators.required]),
        'documentNo': new FormControl('', [Validators.required]),
        'mainCardTuWord': new FormControl('', [Validators.required]),
        'mainCardTuName': new FormControl('', [Validators.required, Validators.pattern(/^[A-Z]+\/[A-Z]+\/$/)]),
        'mainCardLayout': new FormControl('', [Validators.required]),
        // 'mainCardReserve': new FormControl('', [Validators.required]),
        'isMainCardNeedPinPassword': new FormControl('', [Validators.required]),
        'isMainCardNeedPassword': new FormControl('', [Validators.required]),
        'isMainCardOpenSmsNotice': new FormControl('', [Validators.required]),
        'telNum': new FormControl('', [Validators.required, checkTel])
    }); //校验
    // Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), 
    // 信用卡实体bean
    creditCardBean: CreditCardBean = new CreditCardBean();

    displayComponent: boolean = true; // 是否展示当前组件
    isattachedCardTuName: boolean = true;

    isApprove: boolean = true;//判断是否是审批节点
    // tslint:disable-next-line:no-input-rename
    @Input('data') data;
    constructor(public commfunc: Commfunc,
        private communicationService: ParentChildCommunicationService,
        private confirmationService: ConfirmationService,
        private creditCcmService: CreditCcmService, public fb: FormBuilder,
        public routerInfo: ActivatedRoute,
    ) {
        this.communicationService.custRequestNo.subscribe(event => {
            this.custRequestNoFromScardGetMcard = event.custRequestNo;
            this.creditCardNum=event.creditCardNum;
        })
        this.communicationService.queryTransfer.subscribe(_event => {
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditMcard') !== -1) {
                debugger
                if(_event.flag){
                    this.count = 0;
                    _event.flag = false;
                }
                console.log(this.count)
                debugger
                this.count++
                if(this.count>1){
                    return
                }
                this.getCodeValues();
                if (this.data.transferCreditData.varMap.dataMap.cardType) {
                    this.isApprove = false;
                    this.renewalCard();
                } else {
                    this.getBusinessHappenTypeValue();
                }
            }
           
        });

        if (this.routerInfo.snapshot) {
            this.params = this.routerInfo.snapshot
        }

        //申请附卡时，接收主卡的客户请求编号
      
    }

    // 初始化方法
    ngOnInit() {
       
        //判断是否是审批节点
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'approve') {
            this.isApprove = false;
        }
    }


    //续卡，主卡信息查询
    queryCreditCardInfo(custRequestNo) {
        let param = {
            custRequestNo,
            questionType: '05'
        }
        this.creditCcmService.queryCreditCardInfo(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.list.length) {
                    this.isFlagList = true;
                }
                if (data.list && data.list.length > 0) {
                    const questionReslponse = JSON.parse(data.list[0]['questionReslponse']);
                    // debugger
                    questionReslponse.cqiId = data.list[0]['cqiId'];
                    this.creditCardBean = questionReslponse;
                    // this.creditCardBean.creditCardType = '0';
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    xykbh(creditCardNum) {
        let param = {
            agreementId: "",
            creditCardNum
        }
        this.creditCcmService.queryCreditResultInfoByAcctNo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.creditResultList && data.creditResultList[0]) {
                    this.inputValues=data.creditResultList[0].custRequestId;
                    this.queryCreditCardInfo(this.inputValues);
                }
            } 
        })
    }
    // 查询业务发生类型
    getBusinessHappenTypeValue() {
        const param = {
            'custRequestNo':  this.data.transferCreditData['custRequestNo'],
        };
        this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                if (data.dynamicProductValue && (data.dynamicProductValue['PCA0000199'] === '0' || data.dynamicProductValue['PCA0000199'] === '1' || data.dynamicProductValue['PCA0000199'] === '3')) {
                    this.displayComponent = true;
                    this.query();
                } else {
                    this.displayComponent = false;
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });
                this.displayComponent = false;
            }
        });
    }
    //续卡
    renewalCard() {
        
        const cardType = this.data.transferCreditData.varMap.dataMap.cardType;
        if (cardType == '0' ||cardType == '1') {
            this.displayComponent = true;
            this.xykbh(this.creditCardNum);
        } else {
            this.displayComponent = false;
        }
    }
    query() {
        // if (this.data.transferCreditData['isView']) {
        //     this.hiddenBtn = true;
        //     this.blockedPanel = true;
        // }
        // 初始化查询

        // this.queryCustomerCenter();
        this.queryMcardDetail(true);

        this.creditCardBean.creditCardType = '0';
        //pin密码默认
        this.creditCardBean.isMainCardNeedPinPassword = '1';
    }

    // 获取下拉框值
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        // 是否凸字：
        this.isTuWordList = codeValues['isTuWord'];
        // 是否需要PIN密信：
        this.isNeedPinPasswordList = codeValues['isNeedPinPassword'];
        // 消费需要密码：
        this.isNeedPasswordList = codeValues['isNeedPassword'];
        // 开通短信通知：
        this.isOpenSmsNoticeList = codeValues['isOpenSmsNotice'];
        // 证件类型：
        this.idenTypeList = codeValues['ALL_IDEN_TYPE'];
        // 卡片类型
        this.creditCardTypeList = codeValues['creditCardType'];
        // 主卡面板
        // this.mainCardLayoutList = codeValues['mainCardLayout'];
        const params = {
            // 产品编号传入
            'productId': this.data.transferCreditData['productNo'],
            // productId: 'P01001000300001',
            //  客户请求编号
            'custRequestNo': this.data.transferCreditData['custRequestNo']
            // 'custRequestNo': '10005'
        };

        this.creditCcmService.queryProductAttributes(params).subscribe(data => {

            //初始化，避免下拉框出现重复
            this.mainCardLayoutList = [{ label: '请选择', value: '' }];
            for (const productFeatureType of data.productFeatureList) {

                if (productFeatureType.productFeatureTypeId === 'PCA0000209') {
                    for (const product of productFeatureType.productFeatureList) {
                        let a = { label: '', value: '' };
                        a.value = product.productFeatureLocalCode;
                        a.label = product.productFeatureName;
                        this.mainCardLayoutList.push(a);
                    }
                    if (this.mainCardLayoutList && this.mainCardLayoutList.length == 2) {
                        this.creditCardBean.mainCardLayout = this.mainCardLayoutList[1].value;
                    }
                }
            }
        })
    }

    // 信用卡主卡详细信息查询
    queryMcardDetail(queryCustomerCenterFlag) {
        const params = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            // custRequestNo:'5100029196',
            'questionType': '05' // 主卡类型
        };
        this.creditCcmService.queryFacQuestions(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.list.length) {
                    this.isFlagList = true;
                }
                for (const item of data.list) {
                    const questionReslponse = JSON.parse(item['questionReslponse']);
                    // debugger
                    questionReslponse.cqiId = item['cqiId'];
                    this.creditCardBean = questionReslponse;
                    this.creditCardBean.creditCardType = '0';

                }
                this.creditCardBean.isMainCardNeedPinPassword = '1';
                this.creditCardBean.isMainCardOpenSmsNotice = '1';
                this.creditCardBean.isMainCardNeedPassword = '1';
                // debugger
                if (!(data.list && data.list.length > 0)) {
                    this.queryCustomerCenter();
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }

    // 客户信息查询
    queryCustomerCenter() {
        const params = {
            'custNo': this.data.transferCreditData['custNo']
            // custNo:'5100029196'
        };
        // 调用查询客户中心接口，查询客户相关信息
        this.creditCcmService.queryCustNeedInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.creditCardBean.custNo = data.custNo;
                this.creditCardBean.custName = data.custName;
                this.creditCardBean.documentType = data.idenType;
                this.creditCardBean.documentNo = data.idenNum;
                this.creditCardBean.telNum = data.telNum;
                this.creditCardBean.mainCardTuWord = '1';
                let custNamese1 = data.custName;
                const paramse = {
                    'cnCharacter': custNamese1,
                };
                this.creditCcmService.translatorCNService(paramse).subscribe(data => {
                    if (data.returnCode.code === 'success') {
                        // debugger
                        if (data.spellList&&data.spellList.length == 1) {
                            this.creditCardBean.mainCardTuName = data.spellList[0];
                            // this.mainCardTuNameList = data.spellList;
                        }
                    } else {
                        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                    }
                });
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }

    telNumChange() {
        this.creditCcmService.queryCustNeedInfo({ custNo: this.data.transferCreditData['custNo'], }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.telPhone = data.telNum;
                if (this.creditCardBean.telNum != data.telNum) {
                    this.confirm()
                }
            }
        })

    }
    //手机号确认
    confirm() {
        this.confirmationService.confirm({
            message: `与客户信息中手机号${this.telPhone}不匹配，是否确认修改？`,
            accept: () => {
                // 条件
                let param = {
                    custNo: this.data.transferCreditData['custNo'],
                    telNum: this.creditCardBean.telNum,
                    custType: ''
                }
                this.creditCcmService.updateCustComposite(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [{ severity: 'success', summary: '成功', detail: '修改成功1' }];
                    }
                })
            }, reject: () => {
                this.creditCardBean.telNum = '';
            }
        });
    }
    // 提交之前做一次校验
    preValidateForSubmit() {
        //校验的
        for (const i in this.myformChild.controls) {
            this.myformChild.controls[i].markAsDirty()
        };
        //处理是否提交
        for (const i in this.myformChild.controls) {
            if (!this.myformChild.controls[i].valid) {
                return false
            };
        }
        return true;
    }

    //转换大小写
    conversion() {
        this.creditCardBean.mainCardTuName.toUpperCase();
    }

    // 信用卡主卡保存
    addCreditMcard() {
        if (!this.preValidateForSubmit()) return;
        const params = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            'questionType': '05',
            'cqiId': this.creditCardBean.cqiId,
            'questionReslponse': JSON.stringify(this.creditCardBean)
        };
        if (this.isFlagList) {
            params['cqiId'] = this.creditCardBean.cqiId;
        }
        this.creditCcmService.saveFacQuestion(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.creditCardBean.cqiId = data.cqiId;
                this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
    mainCardTuNameSelect(event) {
        //////console.log(this.creditCardBean.mainCardTuName)
    }
    mainCardTuName(event) {
        this.creditCcmService.translatorCNService({'cnCharacter':this.data.transferCreditData.custName}).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.spellList.length > 0) {
                    this.cardTuNameList = data.spellList;
                }

            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });

    }
    selectIsTuWordList(event) {
        if (this.creditCardBean.attachedCardTuWord == '0') {
            this.isattachedCardTuName = false;
        } else {
            this.isattachedCardTuName = true;
        }
    }
}
