export class CreditCardElementBean {
   
    custNo: string;   // 借款人客户号
    custName: string;  // 借款人名称
    idNo: string;      // 身份证号
    custAttr: string; // 借款人类型

    orgCode: string; // 申请机构
    org :string; // 申请机构

    IN_loanUser1: string; // 贷款用途1
    IN_loanUser2: string;  // 贷款用途2
    IN_loanUser3: string;  // 贷款用途3
    IN_loanUser4: string; //  贷款用途4

    IN_industry1: string; // 行业投向1
    IN_industry2: string; // 行业投向2
    IN_industry3: string; //  // 行业投向3
    IN_industry4: string; //  // 行业投向4

    productNo: string;  // 产品编号
    productAttr: string;  // 产品类型

    applyDate: string;  // 贷款开始时间--当前时间
    matureDate: string; // 贷款结束时间-不传

    contractNo: string;  // 最高额合同编号-传空

    applyAmt: string;  // 申请金额 - 如果额度调整，传调整后-否则，传调整前
    IN_isContrac: string; //  是否到期待处理合同-传空
    IN_contractStatus: string; // 到期待处理合同状态-传空
    IN_contractBalance: string; // 到期待处理金额-传空

    guaranteeMode: string;  // 担保方式-如果是担保方式调整，传调整后-否则，传调整前

    IN_coBorrowerList:any = []; // 共同借款人列表-传空
    IN_guarantorList:any = []; // 担保人列表
    overdueMark: string; // 是否逾期案例-空

    guaranteeType: string; // 担保方式
    creditType: string; // 授信类型
}