// 积减冲数
export class ProductCountBean {

    seqNo; // 流水号

    clientNo; // 客户号

    clientName; // 客户名称

    accountNo; // 账号

    relationshipType; // 关联关系

    lastActualAccrual; // 上年可冲减积数

    thisActualAccrual; // 本年可冲减积数

    contributionAccrual; // 贡献积数

    totalAccrual; // 总积数

    expectRrPn; // 需冲减积数

    rrPn: number = 0; // 已经确定积数

    lastRrPn; // 冲减上年积数

    thisRrPn; // 冲减本年积数

    contributionRrPn; // 冲减贡献积数

    remainingValue: number; // 剩余冲减积数
}
