import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

import { Message, ConfirmationService } from 'primeng/primeng';

import { CreditCustBean } from '../../bean/credit-cust.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import { API } from 'app/pages/tzb/custom/http/custom-view/custom-view.constant';
import * as $ from 'jquery';
import * as _ from 'lodash';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { MaximumInfoBean } from './bean/credit-result-info.bean';
import { Router, ActivatedRoute } from '@angular/router';
/*
  批量预授信
*/
@Component({
    selector: 'app-group-information',
    templateUrl: './group-information.component.html',
    styleUrls: ['./group-information.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})


// tslint:disable-next-line:component-class-suffix
export class GroupInformation implements OnInit {
    Permissioncontrol: any;
    isReadonly: boolean = true;//是否只读

    msgs: Message[] = [];//提示信息
    groupType:any[];//群类型
    maximumInfoBean: any;
    // maximumInfoBean: MaximumInfoBean = new MaximumInfoBean();
    batchData: any;

    firstToinvestTradeOptions: any[];//投向行业一级
    secondToinvestTradeOptions: any[];//投向行业二级
    thirdToinvestTradeOptions: any[];//投向行业三级
    fourthToinvestTradeOptions: any[];//投向行业四级
    firstToinvestTrade: any;//投向一
    secondToinvestTrade: any;//投向二
    thirdToinvestTrade: any;//投向三
    fourthToinvestTrade: any;//投向四


    regionSHeng: any = [];
    regionCity: any = [];
    regionArea: any = [];
    regionStreet: any = [];
    regionSHeng1: any = '';//一级
    regionCity1: any = '';//二级
    regionArea1: any = '';//三级
    regionStreet1: any = '';//四级



    isHangye: boolean = false;
    code:any;
    constructor(
        public router: Router,
        public activatedRouter: ActivatedRoute,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,

    ) {

    }
    ngOnInit() {
        this.batchData = JSON.parse(this.commfunc.getSessionDataCH('groupBranchData'));
        console.log(this.batchData)
        this.Permissioncontrol = this.batchData.detailData.Permissioncontrol;
        console.log(this.Permissioncontrol)
        this.maximumInfoBean = this.batchData.groupData || {};
        console.log(this.maximumInfoBean)
        this.getCode();
        this.selectCustGroupInfo();
        this.getWatch()
    }
    // 查看权限设置
    getWatch(){
        console.log("asd")
        switch (this.batchData.self) {
            case "1": {
                this.isReadonly = false;
                // this.isFirst = "1"
            }
                break;
            case "2":
                {
                    this.isReadonly = false;
                    // this.isFirst = "2"
                }
                break;
            case "3":
                {
                    this.isReadonly = true;
                    // this.isFirst = "3"
                }
                break;
            default: {
                this.isReadonly = true;
                // this.isFirst = "3"
            }

        }
    }
    // 跳转群影像
    lookReport() {
        let data = {
            // selfGroup: '1',

        };
        if(!this.isReadonly && this.batchData.detailData.Permissioncontrol != '0'){
            data = {
                selfGroup: '1',
                isShow:true
            };
        }else{
            data = {
                selfGroup: '1',
    
            };
        }
        this.commfunc.setSessionDataCH('groupBranchRelevance', JSON.stringify(data));
        this.router.navigate(['/pages/tzb/custom/credit-ccm/group-revance']);
        // this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view/custom-image-query', { groupId: this.batchData.groupData.GROUP_ID, groupName: this.batchData.groupData.GROUP_NAME, ownerName: this.batchData.groupData.GROUP_OWNER_NAME, groupType: this.batchData.groupData.GROUP_TYPE }]);
    }

    // 获取码值
    getCode() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.code = this.commfunc.codeDatas;
        this.groupType = codeValues['GroupTypeSave'];
        // this.vetoFirsts = codeValues['VETOFIRDTS'];//一级原因
    }
    //投向行业四级区划
    queryTaskByIndtstry(data) {
        if (data == '') {
            this.firstToinvestTradeOptions = [ ];
            this.secondToinvestTradeOptions = [ ];
            this.thirdToinvestTradeOptions = [ ];
            this.fourthToinvestTradeOptions = [ ];
            this.firstToinvestTrade = '';
            this.secondToinvestTrade = '';
            this.thirdToinvestTrade = '';
            this.fourthToinvestTrade = '';
            let param_zone = {
                taskTypeParentId: '',
                taskTypeKey: 'indtstryType'
            }
            //四级区划查询投向一
            this.httpService.taskTypeQry(param_zone).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.firstToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                    this.firstToinvestTradeOptions.unshift( );
                    this.secondToinvestTradeOptions = [ ];
                    this.thirdToinvestTradeOptions = [ ];
                    this.fourthToinvestTradeOptions = [ ];
                    this.secondToinvestTrade = '';
                    this.thirdToinvestTrade = '';
                    this.fourthToinvestTrade = '';
                }
            });
        } else if (data == '1') {
            if (this.firstToinvestTrade == '') {
                this.secondToinvestTradeOptions = [ ];
                this.thirdToinvestTradeOptions = [ ];
                this.fourthToinvestTradeOptions = [ ];
                this.secondToinvestTrade = '';
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
            } else {
                this.secondToinvestTradeOptions = [ ];
                this.thirdToinvestTradeOptions = [ ];
                this.fourthToinvestTradeOptions = [ ];
                this.secondToinvestTrade = '';
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.firstToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向二
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.secondToinvestTradeOptions.unshift( );
                        this.thirdToinvestTradeOptions = [ ];
                        this.fourthToinvestTradeOptions = [ ];
                        this.thirdToinvestTrade = '';
                        this.fourthToinvestTrade = '';
                    }
                });
            }
        } else if (data == '2') {
            if (this.secondToinvestTrade == '') {
                this.thirdToinvestTradeOptions = [ ];
                this.fourthToinvestTradeOptions = [ ];
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
            } else {
                this.thirdToinvestTradeOptions = [ ];
                this.fourthToinvestTradeOptions = [ ];
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.secondToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向三
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.thirdToinvestTradeOptions.unshift( );
                        this.fourthToinvestTradeOptions = [ ];
                        this.fourthToinvestTrade = '';
                    }
                });
            }
        } else if (data == '3') {
            if (this.thirdToinvestTrade == '') {
                this.fourthToinvestTradeOptions = [ ];
                this.fourthToinvestTrade = '';
            } else {
                this.fourthToinvestTradeOptions = [ ];
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.thirdToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向四
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.fourthToinvestTradeOptions.unshift( );
                    }
                });
            }
        }
    }
    typeChange(event) {
        if (event.value == 4) {
            this.isHangye = true;
        } else {
            this.isHangye = false;
        }
    }
    //投向行业回显
    queryParentZoneAll2(zoneId) {
        this.httpService.queryTaskByIndtstryAll({ taskTypeId: zoneId, taskTypeKey: 'indtstryType' }).subscribe(data => {
            this.firstToinvestTradeOptions = this.commfunc.listToLabelvalue(data.firstTask, 'taskTypeName', 'taskTypeId');
            this.firstToinvestTradeOptions.unshift( );
            this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.secondTask, 'taskTypeName', 'taskTypeId');
            this.secondToinvestTradeOptions.unshift( );
            this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.thirdTask, 'taskTypeName', 'taskTypeId');
            this.thirdToinvestTradeOptions.unshift( );
            this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.fourthTask, 'taskTypeName', 'taskTypeId');
            this.fourthToinvestTradeOptions.unshift( );
            if (data.taskList && data.taskList.length != 0) {
                data.taskList.forEach(item => {
                    switch (item.level) {
                        case '1': this.firstToinvestTrade = item.taskTypeId; break;
                        case '2': this.secondToinvestTrade = item.taskTypeId; break;
                        case '3': this.thirdToinvestTrade = item.taskTypeId; break;
                        case '4': this.fourthToinvestTrade = item.taskTypeId; break;
                        default: break;
                    }
                })
            }
        })
    }
     // 查询
  selectCustGroupInfo() {
    let params = {
      groupId: this.maximumInfoBean.GROUP_ID
    }
    // 客户群信息查询
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        //   备注
        this.maximumInfoBean.INT = data.description;
        //判断群类型是不是地理
        if (data.groupType == '3') {
          this.groupType = this.code['GroupTypeSave'];
          let arrTemp = [];
          this.groupType.forEach(element => {
            if (element.value != 'G10001' && element.value != 'G10002') {
              arrTemp.push(element);
            }
          });
          this.groupType = arrTemp;
        } else {
          this.groupType = this.code['GroupTypeCreate'];
        }
        //判断是否是供应链
        if (data.groupType == 'G10001' || data.groupType == 'G10002') {
          this.groupType = this.code['GroupTypeSave'];     
        }
        // 四级区划
        // this.custGroupInfoBean.region = data.region;//四级区划
        // this.region = data.region;
        this.queryParentZoneAll(data.region);
        // this.parentZoneInfoBean.zoneId = this.custGroupInfoBean.region;//四级区划
        //   投向行业
        // this.GroupOrgId = data.orgId;
        // this.custGroupInfoBean = data;//客户群信息查询bean
        // this.pushGroupPostition();//群定位    
        // this.directionValue = data.projectId;//合作方向id
        this.queryParentZoneAll2(data.subGroupType);
        // this.regionReturn();// 四级区划
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
// 获取投向区域
  queryParentZoneAll(region) {
    let params = {
      zoneId: region
    }
    let successFnc = data => {
      this.regionSHeng = [ ];
      data.firstZone.forEach(item => {
        this.regionSHeng.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.secondZone.forEach(item => {
        this.regionCity.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.thirdZone.forEach(item => {
        this.regionArea.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });

      data.fourthZone.forEach(item => {
        this.regionStreet.push({
          label: item.zoneName,
          value: item.zoneId
        })
      });


      this.regionSHeng = _.clone(this.regionSHeng)
      this.regionCity = _.clone(this.regionCity)
      this.regionArea = _.clone(this.regionArea)
      this.regionStreet = _.clone(this.regionStreet)
      data.zoneList.forEach(item => {
        switch (item.parentZoneLevel) {
          case '1': this.regionSHeng1 = item.parentZoneId; break;
          case '2': this.regionCity1 = item.parentZoneId; break;
          case '3': this.regionArea1 = item.parentZoneId; break;
          case '4': this.regionStreet1 = item.parentZoneId; break;
          default: break;
        }
      })
    }
    this.commfunc.interfaceComFnc(this.httpService.queryParentZoneAll(params), successFnc, this.msgs);
    // if (!this.region || this.region == '') {
    //   this.regionSHeng1 = '';
    // }
  }
//   客户群跳转
  toGroupView(col){
    //   if(this.batchData.detailData.Permissioncontrol == '0'){
    //     this.msgs = [];
    //     this.msgs = [{ severity: 'error', summary: '提示', detail: '无查看权限！' }];
    //       return;
    //   }
    let a = this.commfunc.compileStr(col);
    // window.open('/#/pages/tzb/custom/custom-group/custom-group-view;' + b);
    this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: a }])
    // this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-save', { groupId: col }])
  }
}