
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CreditInfoData } from "app/pages/tzb/custom/overdue/overdue-case/credit-ifo/base/localData";
import { MyFormValidate } from "app/pages/tzb/custom/overdue/common/formValidate";

export class OverdueReasonData extends CreditInfoData {

    isOverdueConditionEmpty: Boolean = false;
    isRealityPurposeEmpty: Boolean = false;
    isOvereReasonEmpty: Boolean = false;
    isBondsmanEmpty: Boolean = false;
    isReasonListEmpty:Boolean = false;//风险成因
    isOverdueReasonEmpty :Boolean = false;
    isSuperviceRiskReasonEmpty: Boolean = false;

    overdueReasonAnalyze: any;
    // myFormValidate: MyFormValidate = new MyFormValidate();

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    getOverdueFinancialCondition () {
        let overdueFinancialCondition = this.getTemporaryData("overdue-overdueFinancialCondition");

        let t: any;
        if (overdueFinancialCondition && overdueFinancialCondition.hasOwnProperty('timeStamp')) {
            t = overdueFinancialCondition ? overdueFinancialCondition.overdueFinancialCondition : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.overdueFinancialCondition);
        }

        overdueFinancialCondition = t ? t : null;

        return overdueFinancialCondition;
    }
    isOverdueFinancialConditionEmpty () {
        let overdueFinancialCondition = this.getOverdueFinancialCondition();
        if (!overdueFinancialCondition) {
            this.isOverdueConditionEmpty = true;
            this.isOverdueReasonEmpty = true;
            return;
        }
        for (let property in overdueFinancialCondition) {
            if (overdueFinancialCondition.hasOwnProperty(property) && property.toString().indexOf('history') == -1) {
                if (overdueFinancialCondition[property] === '') {
                    this.isOverdueConditionEmpty = true;
                    this.isOverdueReasonEmpty = true;
                }
            }
        }
    }

    getRealityCreditPurpose () {
        let realityCreditPurpose = this.getTemporaryData("overdue-realityCreditPurpose");

        let t: any;
        if (realityCreditPurpose && realityCreditPurpose.hasOwnProperty('timeStamp')) {
            t = realityCreditPurpose ? realityCreditPurpose.realityCreditPurpose : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.realityCreditPurpose);
        }

        realityCreditPurpose = t ? t : null;

        return realityCreditPurpose;
    }
    isRealityCreditPurposeEmpty () {
        let realityCreditPurpose = this.getRealityCreditPurpose();
        if (!realityCreditPurpose) {
            this.isRealityPurposeEmpty = true;
            this.isOverdueReasonEmpty = true;
            return;
        }
        // console.log(realityCreditPurpose)
        if (!realityCreditPurpose.firstPurpose || 
            !realityCreditPurpose.secondPurpose) {
                this.isRealityPurposeEmpty = true;
                this.isOverdueReasonEmpty = true;
            }
        // for (let property in realityCreditPurpose) {
        //     if (realityCreditPurpose.hasOwnProperty(property)) {
        //         if (!realityCreditPurpose[property]) {
        //             this.isOverdueReasonEmpty = true;
        //         }
        //     }
        // }
    }


    getOverdueReasonAnalyze () {
        let overdueReasonAnalyze = this.getTemporaryData("overdue-overdueReasonAnalyze");

        let t: any;
        if (overdueReasonAnalyze && overdueReasonAnalyze.hasOwnProperty('timeStamp')) {
            t = overdueReasonAnalyze ? overdueReasonAnalyze.overdueReasonAnalyze : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.overdueReasonAnalyze);
        }

        overdueReasonAnalyze = t ? t : null;

        return overdueReasonAnalyze;
    }
    isOverdueReasonAnalyzeEmpty () {
        let overdueReasonAnalyze = this.getOverdueReasonAnalyze();
        if (!overdueReasonAnalyze) {
            this.isOvereReasonEmpty = true;
            this.isOverdueReasonEmpty = true;
            return;
        }
        let flags = this.getTemporaryData("overdue-transformRationalit-bondsmanCondition");
        if (flags.isCredit) {
            let t = overdueReasonAnalyze.superviceRiskReasonAnalyze;
            if (!t || !t.length || !t[0].firstClass) {
                this.isSuperviceRiskReasonEmpty = true;
                this.isOverdueReasonEmpty = true;
                return;
            }
        }
        if (!overdueReasonAnalyze.originalFinanceDataDesc || 
            !overdueReasonAnalyze.financeChangeReason || 
            !overdueReasonAnalyze.notFinanceChangeReason || 
            !overdueReasonAnalyze.riskReason || 
            !overdueReasonAnalyze.riskReason.length) {
            this.isOvereReasonEmpty = true;
            this.isOverdueReasonEmpty = true;
            return;
        }
    }

    getBondsmanCondition () {
        let bondsmanCondition = this.getTemporaryData("overdue-bondsmanCondition");

        let t: any;
        if (bondsmanCondition && bondsmanCondition.hasOwnProperty('timeStamp')) {
            t = bondsmanCondition ? bondsmanCondition.bondsmanCondition : null;
        } else {
            t = this.historyHelper.getCurrentImmediately(this.source.bondsmanCondition);
        }

        bondsmanCondition = t ? t : null;

        return bondsmanCondition;
    }
    isBondsmanConditionEmpty () {
        debugger;
        if (this.isNeedBondsmanCondition) {
            let bondsmanCondition = this.getBondsmanCondition();
            if (!bondsmanCondition) {
                this.isBondsmanEmpty = true;
                this.isOverdueReasonEmpty = true;
                return;
            }
            for (let i = 0, len = bondsmanCondition.length; i < len; i++) {
                let element = bondsmanCondition[i];
                if ((!element.otherBankLoanAmt && element.otherBankLoanAmt != 0) || 
                    (!element.otherBankEnsureAmt && element.otherBankEnsureAmt != 0) || 
                    !element.insteadPayWill || !element.insteadPayAbility) {
                    this.isBondsmanEmpty = true;
                    this.isOverdueReasonEmpty = true;
                    break;
                }
            }
        }
    }
    isReasonListEmptys(){
        let overdueReasonAnalyze = this.getOverdueReasonAnalyze();
        if(overdueReasonAnalyze.riskReason){
            if(!overdueReasonAnalyze.riskReason[0].firstClass){
                  this.isReasonListEmpty = true
            }else{
                this.isReasonListEmpty = false
            } 
            
        }
        
       console.log("3213")
       console.log(overdueReasonAnalyze)
        
    }
    setMsg2 () {
        // super.setMsg();
        // if (this.isOverdueConditionEmpty) {
        //     this.msg.push('逾期原因分析：逾期后财务状况有空数据');
        // }
        if (this.isRealityPurposeEmpty) {
            this.msg.push('逾期原因分析：实际贷款用途一级或二级有空选项');
        }
        // if (this.isOvereReasonEmpty) {
        if (1 != 1) {
            this.msg.push('逾期原因分析：逾期原因分析有空数据');
        } else {
            let overdueReasonAnalyze = this.getOverdueReasonAnalyze();
            let originalFinanceDataDesc = overdueReasonAnalyze.originalFinanceDataDesc;

            let financeChangeReason = overdueReasonAnalyze.financeChangeReason;
            if (financeChangeReason && this.myFormValidate.chineseCharacterLength(financeChangeReason) > 1000) {
                this.msg.push('逾期原因分析：财务状况变化剖析及偏差原因长度超过500字');
            }
            let notFinanceChangeReason = overdueReasonAnalyze.notFinanceChangeReason;
            if (notFinanceChangeReason && this.myFormValidate.chineseCharacterLength(notFinanceChangeReason) > 1000) {
                this.msg.push('逾期原因分析：非财务状况变化剖析及偏差原因长度超过500字');
            }
        }
        if (this.isSuperviceRiskReasonEmpty) {
            this.msg.push('逾期原因分析：监管层面风险成因有空数据');
        }
        if (this.isBondsmanEmpty) {
            this.msg.push('逾期原因分析：担保人情况分析有空数据');
        }
        // 风险成因空数据校验
        if(this.isReasonListEmpty){
            this.msg.push('逾期原因分析：风险成因(最多选三项)有空数据');
        }
    }

    clear2 () {
        this.msg = [];
        this.isOverdueConditionEmpty = false;
        this.isRealityPurposeEmpty = false;
        this.isOvereReasonEmpty = false;
        this.isSuperviceRiskReasonEmpty = false;
        this.isBondsmanEmpty = false;
        this.isOverdueReasonEmpty = false;
    }

    runEmptyCheck () {

        this.clear2();

        this.isRealityCreditPurposeEmpty();
        this.isOverdueFinancialConditionEmpty();
        this.isOverdueReasonAnalyzeEmpty();
        this.isBondsmanConditionEmpty();
        // 2019-7-22风险成因非空校验
        this.isReasonListEmptys();
   
        this.setMsg2();
    
        let current = this.isOverdueReasonEmpty;
        return current;
    }


}
