import { Component, Output, EventEmitter, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

import { Message } from 'primeng/primeng';

import { CreditDebtBean } from './bean/credit-debt.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-credit-debt',
    templateUrl: './credit-debt.component.html',
    styleUrls: ['./credit-debt.component.scss'],
    providers: [Commfunc]
})

// 债务信息
// tslint:disable-next-line:component-class-suffix
export class CreditDebt implements OnInit {
    dataList: any[];
    bean: any;
    infoData: any;
    creditDebtBean: CreditDebtBean = new CreditDebtBean(); // 债务信息bean

    blockedPanel: boolean = false; // 如果只有查看权限的情况下不能编辑数据

    hiddenBtn: boolean; // 控制按钮显示或者隐藏

    msgs: Message[] = [];
    debtValue:any;
    @Input('data') data;
    creditDebtFlag:boolean=false;
    // 构造器
    constructor(
        public fb: FormBuilder,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
        private commfunc: Commfunc
    ) {
        // this.communicationService.queryTransfer.subscribe(_event => {
        //     if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditDebt') !== -1) {
        //         this.query();
        //     }
        // });

    }

    // 初始化方式
    ngOnInit() {
        this.communicationService.queryTransfer.subscribe(_event => {
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditDebt') !== -1) {
                this.query();
            }
        });
        let session = window.sessionStorage;
        let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
         // 判断债务信息样式头和进口
         this.debtValue = {
            isCreditCcm :true,
            custName: this.data.transferCreditData['custName'],
            custNo: this.data.transferCreditData['custNo'],
            flag: '1',
            orgCode: orgId
         }
        // this.queryEsbDebtInfo();

    }

    query() {
        if (this.data.transferCreditData['isPreview']) {
            return;
        }
        // if (this.data.transferCreditData['isView']) {
        //     this.hiddenBtn = true;
        //     this.blockedPanel = true;
        // }
        // 初始化查询


    }

    // 债务信息查询
    queryEsbDebtInfo() {
        let session = window.sessionStorage;
        let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
        let param = {
            custName: this.data.transferCreditData['custName'],
            custNo: this.data.transferCreditData['custNo'],
            flag: '1',
            orgCode: orgId
        }
        this.creditCcmService.queryDebtInfo(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.list && data.list.length) {
                    // this.creditDebtBean = data.list[0];
                    this.creditDebtBean.creditLmt = this.commfunc.formatMoney(data.list[0].creditLmt, 2);
                    this.creditDebtBean.usedLmt = this.commfunc.formatMoney(data.list[0].usedLmt, 2);
                    this.creditDebtBean.onwayBalance = this.commfunc.formatMoney(data.list[0].onwayBalance, 2);
                    this.creditDebtBean.depPledgeAmt = this.commfunc.formatMoney(data.list[0].depPledgeAmt, 2);
                    this.creditDebtBean.acceBalance = this.commfunc.formatMoney(data.list[0].acceBalance, 2);
                    this.creditDebtBean.acceExposureBal = this.commfunc.formatMoney(data.list[0].acceExposureBal, 2);
                    this.creditDebtBean.arrearageAmt = this.commfunc.formatMoney(data.list[0].arrearageAmt, 2);
                    this.creditDebtBean.guaranteeGtBal = this.commfunc.formatMoney(data.list[0].guaranteeGtBal, 2);
                    this.creditDebtBean.gtExposureBal = this.commfunc.formatMoney(data.list[0].gtExposureBal, 2);
                    this.creditDebtBean.creidtAmt = this.commfunc.formatMoney(data.list[0].creidtAmt, 2);
                    this.creditDebtBean.overDueAmt = this.commfunc.formatMoney(data.list[0].overDueAmt, 2);
                    this.creditDebtBean.overPeriod = this.commfunc.formatMoney(data.list[0].overPeriod, 2);
                    this.creditDebtBean.guarantAmt = this.commfunc.formatMoney(data.list[0].guarantAmt, 2);
                    this.creditDebtBean.overBal = this.commfunc.formatMoney(data.list[0].overBal, 2);
                    this.creditDebtBean.hisMaxCredBal = this.commfunc.formatMoney(data.list[0].hisMaxCredBal, 2);
                    this.creditDebtBean.relaCredLmt = this.commfunc.formatMoney(data.list[0].relaCredLmt, 2);
                    this.creditDebtBean.relaUsedLmt = this.commfunc.formatMoney(data.list[0].relaUsedLmt, 2);
                    this.creditDebtBean.relaOnwayLmt = this.commfunc.formatMoney(data.list[0].relaOnwayLmt, 2);
                    this.creditDebtBean.relaAcceExposureBal = this.commfunc.formatMoney(data.list[0].relaAcceExposureBal, 2);
                    this.creditDebtBean.relaDebtAmt = this.commfunc.formatMoney(data.list[0].relaDebtAmt, 2);
                    this.creditDebtBean.relaAcceBalance = this.commfunc.formatMoney(data.list[0].relaAcceBalance, 2);
                    this.creditDebtBean.relaGtExposureBal = this.commfunc.formatMoney(data.list[0].relaGtExposureBal, 2);
                    this.creditDebtBean.relaCreditLmt = this.commfunc.formatMoney(data.list[0].relaCreditLmt, 2);
                    this.creditDebtBean.relaOverDueAmt = this.commfunc.formatMoney(data.list[0].relaOverDueAmt, 2);
                    this.creditDebtBean.relaOverPeriod = this.commfunc.formatMoney(data.list[0].relaOverPeriod, 2);
                    this.creditDebtBean.relaOverBal = this.commfunc.formatMoney(data.list[0].relaOverBal, 2);
                    this.creditDebtBean.relaGuaTotalAmt = this.commfunc.formatMoney(data.list[0].relaGuaTotalAmt, 2);
                    // 他行负债、他行担保、关联人综合授信余额、关联人综合授信在途金额
                    this.creditDebtBean.otherBankDebt = this.commfunc.formatMoney(data.list[0].otherBankDebt, 2);
                    this.creditDebtBean.otherBankGuarantee = this.commfunc.formatMoney(data.list[0].otherBankGuarantee, 2);
                    this.creditDebtBean.relaGeneralCreditBalance = this.commfunc.formatMoney(data.list[0].relaGeneralCreditBalance, 2);
                    this.creditDebtBean.relaGeneralCreditOnWayLimit = this.commfunc.formatMoney(data.list[0].relaGeneralCreditOnWayLimit, 2);
                    let resultObj=data['list'][0];
                    for (let i in resultObj) {
                        if (resultObj.hasOwnProperty(i)) {
                            if ((i!='custName') && (i!='custNo')) {
                                if (resultObj[i]*1) {
                                    this.creditDebtFlag = true;//显示
                                    break;
                                }
                            }
                        }
                    }
                } else {
                    this.msgs = [{ severity: 'info', summary: '提示：', detail: '客户编号' + this.data.transferCreditData['custNo'] + '没有债务数据' }];
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误:', detail: data.returnCode.message }];
            }
        })
    }

}