import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ParamsBean } from 'app/pages/tzb/custom/credit-ccm/bean/params.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import * as _ from 'lodash';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { PROCESS_INSTANCE_MANAGE_API } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance.constant';
import * as $ from 'jquery';
/*
  审批意见 
*/ 
@Component({
    selector: 'app-credit-approve-history',
    templateUrl: './credit-approve-history.component.html',
    styleUrls: ['./credit-approve-history.component.scss'],
})

// tslint:disable-next-line:component-class-suffix
export class CreditApproveHistory implements OnInit {
    data: any={};
    nowProcessId:any;
    processId:any;
    creditApproveDisplay=false;
    taskId:any;
    nowTaskId:any;
    userId: any;
    msgs: Message[] = [];
    // DataTable=[];
    tableData2=[];
    paramsBean = new ParamsBean();
    pageSize: number;
    pageNum: number;
    subprocList: any = [];
    workFlowResultType: any = [];
    code: any;
    tablelist: any = [];
    subTaskId: string;
    display2=false;
    constructor(
        private creditCcmService: CreditCcmService,
        public commfunc: Commfunc,
        public httpService: CustomeHttpService,
        private processInstanceManagementService: ProcessInstanceManagementService,
    ) {
        // this.commfunc.setSessionDataCH('creditDataWorkFlow', JSON.stringify(pageParams))
        this.data['transferCreditData']=JSON.parse(this.commfunc.getSessionDataCH('overdue-params-from-reminder-do'));
      
    }

    // 初始加载
    ngOnInit() {
       this.data['transferCreditData']=JSON.parse(this.commfunc.getSessionDataCH('overdue-params-from-reminder-do'));
      this.processId=this.data.transferCreditData.processId;
      this.taskId='main';
      this.codeValues()
      this.workFlowResultType = this.code['WorkFlowResultType'];
      this.bpmQueryTaskList();
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
// 审批详情弹框回调方法
creditApproveCall() {
    this.creditApproveDisplay = false;
}
 // 关闭审批详情
 closeFirtproc() {
    this.creditApproveDisplay = false;
}
    // 查询审批流程
    bpmQueryTaskList() {
        this.tablelist = [];
        this.paramsBean.queryList = [];
        let param = {
            processId: this.processId,
            isAllNode: false,
            curPage: this.paramsBean.pageNum,
            pageRows: 1000,
        }
        console.log('sssssssssssssss1');
        this.httpService.bpmQueryTaskList(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.pageData.result && data.pageData.result.length > 0) {
                    if (this.taskId == 'sub') {
                        for (var i = 0; i < data.pageData.result.length; i++) {
                            if(data.pageData.result[i].taskType == 'subproc'){
                                this.subTaskId = data.pageData.result[i].taskId;
                                break;
                            }
                        }   
                        this.querySubTask(this.subTaskId); // 授信流程
                    } else {
                        this.paramsBean.queryList = data.pageData.result;
                        this.queryChildList();
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    detail1(col){
        this.display2=true;
        const pams={
            tellerId:col.taskActorsName,
        };
        this.httpService.queryTellerPermission(pams).subscribe(data => {
            if (data.returnCode.code == "success") {
         this.tableData2=data.permissionList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    // 根据taskId获取第二层的值
    querySubTask(id) {
        let newSubTaskId = id;
        let paramSub = {
            mainTaskId: newSubTaskId,
            isAllNode: false,
            pageRows: this.paramsBean.pageNum,
            curPage: this.paramsBean.pageSize,
        }
        this.processInstanceManagementService.queryChildTaskList(paramSub).subscribe(data => {
            if (data.returnCode.code == "success") {
                let subflowInstance = {};
                subflowInstance = data.pageData[0].result;
                this.subprocList = subflowInstance ? subflowInstance[0].workList : [];
                this.paramsBean.queryList = this.subprocList;
                this.queryChildList();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }

    // 判断是否含子流程
    queryChildList() {
        for (var i = 0; i < this.paramsBean.queryList.length; i++) {
            let error = '';
            switch (this.paramsBean.queryList[i]['taskType']) {
                case 'subproc':
                    error = this.queryChild('subproc', this.paramsBean.queryList[i], i);
                    break;
                default:
                    error = this.queryChild('other', this.paramsBean.queryList[i], i);
                    break;
            }
            if (error) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: error });
                break;
            }
        }
    }

    // 处理方式2
    queryChild(type, dataInfo, index) {
        let nowList = {};
        nowList = _.clone(dataInfo);
        if (type == 'subproc') { // 含子流程
            let taskId = dataInfo.taskId;
            let paramSub = {
                mainTaskId: taskId,
                isAllNode: false,
                pageRows: this.paramsBean.pageNum,
                curPage: this.paramsBean.pageSize,
            }
            let erro = '';

            $.ajax({
                url: RTS_HTTP_URL + PROCESS_INSTANCE_MANAGE_API.queryChildTaskList,
                type: 'POST',
                data: JSON.stringify(paramSub),
                async: false,
                headers: {
                    'x-ajax': '1',
                    'Content-Type': 'application/json'
                },
                dataType: 'json',
                success: (data) => {
                    if (data.returnCode.code == 'success') {
                        let subflowInstance = {};
                        subflowInstance = data.pageData.result;
                        this.subprocList = subflowInstance ? subflowInstance[0].workList : [];
                        let SaveList = {};
                        if (this.subprocList.length > 0) {
                            for (var j = 0; j < this.subprocList.length; j++) {
                                this.subprocList[j]['taskJd'] = this.subprocList[j]['taskName'];
                                this.subprocList[j]['taskName'] = dataInfo.taskName;
                                nowList = _.clone(this.subprocList[j]);
                                nowList['type'] = 'subproc';
                                if (j == 0) {
                                    nowList['order'] = 'last';
                                } else if (j == this.subprocList.length - 1) {
                                    nowList['order'] = 'first';
                                } else {
                                    nowList['order'] = 'middle';
                                }
                                this.tablelist.push(nowList)
                            }
                        }
                        this.tablelist = _.clone(this.tablelist)
                    } else {
                        erro = data.returnCode.message;
                    }
                },
                error: (err) => {
                    erro = '调用服务失败';
                }
            })

            return erro;

        } else { // 不含子流程

            if (index == this.paramsBean.queryList.length - 1) {
                nowList['type'] = 'start';
                nowList['taskJd'] = dataInfo.taskName;;
            } else {
                nowList['type'] = 'other';
                nowList['taskJd'] = dataInfo.taskName;;
            }
            this.tablelist.push(nowList);
            this.tablelist = _.clone(this.tablelist)

        }
    }
}