/*
  批量预授信
*/
export class GroupDetail implements OnInit {
    isReadonly: boolean = true;//是否只读
    msgs: Message[] = [];//提示信息
    // maximumInfoBean: MaximumInfoBean = new MaximumInfoBean();
    batchCreditBean: BatchCreditBean = new BatchCreditBean();
    batchVetoBean: BatchVetoBean = new BatchVetoBean(); VetoBean
    vetoBean: VetoBean = new VetoBean();
    tabMesg: string;
    batchData: any;
    batchId: string; //批量预授信登记编号
    custList: any = [];//明细列表
    custListTwo: any = [];//明细列表
    custListTotal: any;//明细列表
    vetoList: any = [];//否决列表
    vetoListTotal: any;//否决列表
    first: number = 0;
    firstB: number = 0;
    isCustSave: boolean = true;//保存修改转换
    isSelect: boolean = true;//选中
    display: boolean = false;//否决
    vetoFirsts: any = []; // 一级原因
    personVeto: any = []; // 二级原因
    financeVeto: any = []; // 二级原因
    manageVeto: any = []; // 二级原因
    serveVeto: any = []; // 二级原因
    otherVeto: any = []; // 二级原因
    twoReasonotherVeto: any = []; // 二级原因
    // personVeto: any = []; // 二级原因
    vetoItem: any;//否决数据
    isBatchCreditTwo: boolean = false;//是否是第一次
    isSelectF: any;
    operationType: any;
    isWatch: boolean = true; // 查看不能修改客户明细
    isSet: boolean = true;
    loanOfficerAdvisesMiddle: any;
    constructor(
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
        private routInfo: ActivatedRoute,
        private communicationService: ParentChildCommunicationService,
        private router: Router,
        public decimalPipe: DecimalPipe,
    ) {
        // this.communicationService.queryTransfer.subscribe(_event => {
        //     this.operationType = '1';
        //     this.getDetailList();

        // })
    }

    ngOnInit() {
        this.batchData = JSON.parse(this.commfunc.getSessionDataCH('groupBranchData'));
        this.batchId = this.batchData.detailData.batchId;
        let userP = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // (this.batchData.detailData.state != "000" && this.batchData.groupData.GROUP_OWNER_ID.indexOf(userP.userId) != -1) ||群主不可以可以否决和修改客户明显了
        console.log(this.batchData.self)
        // 群主和群联系人可以修改，其他人不可以
        // if ((this.batchData.groupData.GROUP_LINKMANID && this.batchData.groupData.GROUP_LINKMANID.indexOf(userP.userId) != -1) || (this.batchData.groupData.GROUP_OWNER_ID && this.batchData.groupData.GROUP_OWNER_ID.indexOf(userP.userId) != -1)) {
        //     this.isWatch = true;
        // } else {
        //     this.isWatch = false;
        // }
        if (this.batchData.self == '3') {
            this.isWatch = false;
        }
        // 群主和群联系人可以修改，其他人不可以

        // if (this.batchData && this.batchData.groupData && this.batchData.groupData.GROUP_LINKMANID && this.batchData.groupData.GROUP_LINKMANID.indexOf(userP.userId) == -1 && this.batchData.groupData.GROUP_OWNER_ID.indexOf(userP.userId) == -1) {
        //     this.isWatch = false;
        // }
        // if (!this.batchData || !this.batchData.groupData || (this.batchData.groupData.GROUP_LINKMANID == null && this.batchData.groupData.GROUP_OWNER_ID == null)) {
        //     this.isWatch = false;
        // }
        this.batchCreditBean.pageNum = 1;
        this.batchCreditBean.pageSize = 10;
        this.batchVetoBean.pageSize = 10;
        this.batchVetoBean.pageNum = 1;
        this.routInfo.params.subscribe(params => {
            let viewPageData = JSON.parse(params.transString);
            console.log(viewPageData)

            // if (this.batchData.detailData.state == "000") {
            if (this.batchData.self == "1") {
                this.operationType = '1'
                // this.isBatchCreditTwo = true;
                // this.getDetailListSet();
                this.getDetailList();//查询明细
                this.getProDetailInfo();

            } else {
                this.operationType = '2'
                this.getDetailList();
                this.getProDetailInfo();
            }
        })
        // 提交申请成功，群主不能否决和修改
        if (this.batchData.detailData.state != "000" && this.batchData.groupData.GROUP_OWNER_ID == userP.userId) {
            this.isSet = false;
        }
        // this.getDetailList();
        // this.getProDetailInfo();
        this.getCode()

    }
    // 获取码值
    getCode() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.vetoFirsts = codeValues['VETOFIRDTS'];//一级原因
        this.personVeto = codeValues['personVeto'];//二级原因-个人原因
        this.financeVeto = codeValues['financeVeto'];//财务因素
        this.manageVeto = codeValues['manageVeto'];//经营因素
        this.serveVeto = codeValues['serveVeto'];//服务因素
        this.otherVeto = codeValues['otherVeto'];//其他原因
        // 二级否决原因综合
        this.twoReasonotherVeto = this.personVeto.concat(this.financeVeto, this.manageVeto, this.serveVeto, this.otherVeto)
    }
    // 初始获取明细信息
    // operType（1），operationType（1）废弃
    getDetailListSet() {
        let params = {
            pageNum: this.batchCreditBean.pageNum,
            pageSize: this.batchCreditBean.pageSize,
            groupId: this.batchData.groupData.GROUP_ID,
            // statusId: "1",
            batchId: this.batchId,
        }
        this.creditCcmService.selectCustGroupList(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                let middel = data.custList;
                // middel.forEatch(item => {
                //     item.isCustSave = 
                // })
                this.custList = data.custList;
                // this.custList = [{ isCustSave: false }];
                this.custListTotal = data.custTotal;
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    // 分页
    paginateTwo(event) {
        this.batchCreditBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.batchCreditBean.pageNum = currentPage;
        this.first = event.page * this.batchCreditBean.pageSize;
        this.getDetailListSet();
    }
    // 获取明细信息
    getDetailList() {
        let params = {
            batchId: this.batchId,
            groupId: this.batchData.groupData.GROUP_ID,
            // datailPageNum: this.batchCreditBean.pageNum,
            // datailPageSize: this.batchCreditBean.pageSize,
            pageNum: this.batchCreditBean.pageNum,
            pageSize: this.batchCreditBean.pageSize,
            operType: '1',
            operationType: this.operationType
        }
        // if(this.operationType == '1'){
        //     params['groupId'] = this.batchData.groupData.GROUP_ID
        // }else{
        //     params['batchId'] = this.batchId
        // }
        this.creditCcmService.queryBatchPreCreditDetailInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {

                this.custList = data.resultList;
                this.custListTwo = data.resultList;
                // this.custList = [{ isCustSave: false }];
                this.custListTotal = data.total;
                this.custList.forEach(element => {
                    element.loanOfficerAdvises = this.transMony(element.loanOfficerAdvises * 10000);
                    element.loanSystemAdvises = this.transMony(element.loanSystemAdvises * 10000);
                });
                if (this.batchData.detailData.state == "000") {

                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    // 获取否决记录
    getProDetailInfo() {
        let params = {
            batchId: this.batchId,
            // vetoPageNum: this.batchVetoBean.pageNum,
            // vetoPageSize: this.batchVetoBean.pageSize,
            pageNum: this.batchVetoBean.pageNum,
            pageSize: this.batchVetoBean.pageSize,
            operType: '2',
            operationType: this.operationType
        }
        this.creditCcmService.queryBatchPreCreditDetailInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.vetoList = data.resultList;
                // this.vetoList = [{ isCustSave: false }];
                this.vetoListTotal = data.total;
                this.vetoList.forEach(element => {
                    element.loanOfficerAdvises = this.transMony(element.loanOfficerAdvises * 10000);
                    element.loanSystemAdvises = this.transMony(element.loanSystemAdvises * 10000);
                });
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    // 分页
    paginate(event) {
        this.batchCreditBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.batchCreditBean.pageNum = currentPage;
        this.first = event.page * this.batchCreditBean.pageSize;
        this.getDetailList();
    }
    // 否决分页
    paginateB(event) {
        this.batchVetoBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.batchVetoBean.pageNum = currentPage;
        this.firstB = event.page * this.batchVetoBean.pageSize;
        this.getProDetailInfo();
    }
    // 明细修改
    toCustUpdate(item, i) {
        this.custList[i].isCustSave = true;
        let ss = item
        this.loanOfficerAdvisesMiddle = ss;
    }
    // 明细保存
    toCustSave(item, i) {
        // let userP = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // if (this.batchData.detailData.state != "000" && this.commfunc.transform(item['loanOfficerAdvises']).toString()*1 > this.commfunc.transform(this.loanOfficerAdvisesMiddle['loanOfficerAdvises']).toString()*1 ) {
        //     this.msgs = [{ severity: 'error', summary: '错误', detail: "不能大于原额度" }];
        //     return;
        // }

        // let params = {
        // serialNo: item.serialNo,
        // batchId: item.batchId,
        // state: item.state,
        // custNo: item.custNo,
        // custName: item.custName,
        // age: item.age,
        // telNum: item.telNum,
        // loanOfficerAdvises: item.loanOfficerAdvises,
        // loanSystemAdvises: item.loanSystemAdvises,
        // threeResult: item.threeResult,
        //     batchId: this.batchId,
        //     operType: '1'
        // }
        let params = {}
        Object.assign(params, item)
        Object.assign(params, {
            batchId: this.batchId,
            operType: '1'
        })
        params['loanSystemAdvises'] = this.commfunc.transform(params['loanSystemAdvises']).toString();
        params['loanOfficerAdvises'] = this.commfunc.transform(params['loanOfficerAdvises']).toString();
        params['loanSystemAdvises'] = params['loanSystemAdvises'] * 1 / 10000;
        params['loanOfficerAdvises'] = params['loanOfficerAdvises'] * 1 / 10000;
        // if (this.batchData.detailData.state != "000" && params['loanOfficerAdvises'] > params['loanSystemAdvises']) {
        //     this.msgs = [{ severity: 'error', summary: '错误', detail: "不能大于原额度" }];
        //     return;
        // }
        this.custList[i].isCustSave = false;//保存修改转换
        params['loanSystemAdvises'] = params['loanSystemAdvises'].toString();
        params['loanOfficerAdvises'] = params['loanOfficerAdvises'].toString();
        // params['state'] = "0"
        this.creditCcmService.updateBatchPreCreditDetailInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.getDetailList()
                this.msgs = [{ severity: 'success', summary: '成功', detail: "修改成功" }];
              
            } else {
                // this.custList[i].loanOfficerAdvises = this.custList[i].loanSystemAdvises;
                this.getDetailList()
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
               
            }
        })
    }
    // 否决
    toReject(item) {
        this.vetoBean.oneVetoReason = null;
        this.vetoBean.twoVetoReason = null ;
        this.vetoBean.voteReason = null;
        this.vetoItem = item;
        this.display = true;
    }
    close() {
        this.display = false;
    }
    // 否决回掉
    vetoClick() {
        // let params = {
        //     batchId: this.batchId,
        //     operType: "1",
        // serialNo: this.vetoItem.serialNo,
        // serialNoList: this.vetoItem.serialNoList,
        // state: "2",
        // firstVetoReason: this.vetoBean.oneVetoReason,
        // secondVetoReason: this.vetoBean.twoVetoReason,
        // otherVetoReason: this.vetoBean.voteReason, 
        // }
        let params = {}
        Object.assign(params, this.vetoItem)
        Object.assign(params, {
            batchId: this.batchId,
            operType: "1", state: "2",
            firstVetoReason: this.vetoBean.oneVetoReason,
            secondVetoReason: this.vetoBean.twoVetoReason,
            otherVetoReason: this.vetoBean.voteReason,
        })
        params['loanSystemAdvises'] = this.commfunc.transform(params['loanSystemAdvises']).toString();
        params['loanOfficerAdvises'] = this.commfunc.transform(params['loanOfficerAdvises']).toString();
        params['loanSystemAdvises'] = params['loanSystemAdvises'] * 1 / 10000;
        params['loanOfficerAdvises'] = params['loanOfficerAdvises'] * 1 / 10000;
        params['loanSystemAdvises'] = params['loanSystemAdvises'].toString();
        params['loanOfficerAdvises'] = params['loanOfficerAdvises'].toString();
        this.creditCcmService.vetoBatchPreCreditDetailInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                // this.display = false;
                // this.msgs = [{ severity: 'success', summary: '成功', detail: "否决成功" }];

                // this.batchCreditBean.pageNum = 1;
                // this.batchCreditBean.pageSize = 10;
                // this.getDetailList();
                this.getCustDetail()
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }


    // 否决查询客户信息
    getCustDetail() {
        let params = {
            custNo: this.vetoItem.custNo
        }
        this.creditCcmService.selectCustBriefInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                // this.display = false;
                // this.msgs = [{ severity: 'success', summary: '成功', detail: "否决成功" }];

                // this.batchCreditBean.pageNum = 1;
                // this.batchCreditBean.pageSize = 10;
                this.getParamList(data);

            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    // 否决调工作流
    getParamList(data) {
        let toListReason = "";
        let backClassifyId = ""
        if (this.vetoBean.oneVetoReason == "09" || this.vetoBean.oneVetoReason == "10") {
            backClassifyId = "010701";
            toListReason = "当否决原因为“服务因素”、“其他原因”被否决的借款人和其统一授信关联人，列入黑名单--信贷否决--提示类"
        } else {
            backClassifyId = "010702";
            toListReason = "否决原因为因“个人因素”、“财务因素”、“经营因素”被否决的借款人和其统一授信关联人，列入黑名单--信贷否决--机构例外类"
        }
        let params = {
            paramList: [{
                isMybankCust: "Y",//是否行内客户
                idType: data.idenType,
                idNo: data.idenNum,
                partyId: data.custNo,//客户标识
                custName: data.custName,
                isCustomerType: "1",//客户角色
                customerType: data.custType,//客户类型
                // preClassifyId:"",//调整前名单编号
                backClassifyId: backClassifyId,//调整后名单编号
                toListReason: toListReason,//转入名单原因
                sourceDes: "贷款否决",//名单来源
                mainUser: data.principalLinkman,
                mainOrg: data.principalLinkmanOrg
            }]
        }
        this.creditCcmService.customerListUpdate(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.display = false;
                this.msgs = [{ severity: 'success', summary: '成功', detail: "否决成功" }];
                // this.batchCreditBean.pageNum = 1;
                // this.batchCreditBean.pageSize = 10;
                this.getDetailList();

            } else {
                this.getDetailList();
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }


    getVetoRegect() {
        this.isSelect = !this.isSelect;
        this.batchVetoBean.pageSize = 10;
        this.batchVetoBean.pageNum = 1;
        this.getProDetailInfo()
    }

    // 第三方评价结果
    toThreeView(col,i) {
        // #/pages/tzb/custom/custom-group/third-party-evaluation/custom-evaluate;groupId=1003500007;custNo=1610009899;doorId=null;custGroupView=1;pageNum=1
        // this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view/group-evaluator', { groupId: this.batchData.groupData.GROUP_ID }])
        // this.router.navigate(['/pages/tzb/custom/custom-group/third-party-evaluation/appraiser-evaluate', { groupId: this.batchData.groupData.GROUP_ID, groupName: this.batchData.groupData.GROUP_NAME }]);
        this.router.navigate(['/pages/tzb/custom/custom-group/third-party-evaluation/custom-evaluate', {yushouxin:'1', groupId: this.batchData.groupData.GROUP_ID, custNo: col.custNo, doorId: null, custGroupView: 1, pageNum: 1, groupName: this.batchData.groupData.GROUP_NAME }]);
        // this.toCustSave(col, i)
    }

    // 校验申请金额
    docheck(param, index) {
        if (param.applyAmt != '' && param.applyAmt != null && param.applyAmt != undefined) {
            var regAmt = /^[0-9]*(\.[0-9]{0,2})?$/;
            if (!regAmt.test(param)) {
                this.msgs = [{ severity: 'error', summary: '错误', detail: "额度格式有误！" }];
                return
            }
        }
    }
    //申请金额校验
    onKeyupNumber(param, i) {
        console.log("ads")
        if (param.loanOfficerAdvises) {
            this.custList[i].loanOfficerAdvises = this.commfunc.transform(param.loanOfficerAdvises) + '';
        }
        this.custList[i].loanOfficerAdvises = this.decimalPipe.transform(param.loanOfficerAdvises, '1.2-2');//申请金额
        this.toCustSave(param, i);
    }
    // 获取焦点，清空
    clear(param, i) {
        this.custList[i].loanOfficerAdvises = this.commfunc.transform(param['loanOfficerAdvises']).toString();
    }
    // 金额格式转换
    transMony(param) {
        let result = "";
        if (param) {
            result = this.commfunc.transform(param) + '';
        }
        result = this.decimalPipe.transform(param, '1.2-2');//申请金额
        return result;
    }
    // 跳转
    compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}
    // 关联影像group-revance
    toRelevance(data) {
        this.commfunc.setSessionDataCH('groupBranchRelevance', JSON.stringify(data));
        this.router.navigate(['/pages/tzb/custom/credit-ccm/group-revance']);
    }
    // 一级原因选择二级原因
    vetoChange(event) {
        // console.log(event)
        // debugger
        // this.comments = '';
        // this.bzIsshow = false;
        // this.reasonOne = '';
        // this.reasonTwo = '';
        if (event.value == '06') {
            this.personVeto = this.personVeto;
        } else if (event.value == '07') {
            this.personVeto = this.financeVeto;
        } else if (event.value == '08') {
            this.personVeto = this.manageVeto;
        } else if (event.value == '09') {
            this.personVeto = this.serveVeto;
        } else if (event.value == '10') {
            this.personVeto = this.otherVeto;
        }
        if (event.value == '') {
            this.vetoBean.twoVetoReason = '';
            this.personVeto = [];
        } else {
            this.vetoBean.twoVetoReason = this.personVeto[0].value;
        }
    }

}