import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";

export class HistoryInfor {

    historyList: any = [];
    hasHistory: Boolean = false;
    currentHistory: any = {}; // 当前历史
    historyOptions: any = []; //接收历史列表 
    display: Boolean = false;
    current: any = {};
    operatingLevel: string = '';
    currentLevel: string = '';
    pageLevel: string = '';
    isHistoryOrPushBack: boolean = false;

    objectHelper: ObjectHelper = new ObjectHelper();
    

    constructor () {

    }

    popupClosed () {
        this.currentLevel = '';
    }

    historyOptionsChange (event) {
        this.display = true;
        this.currentLevel = event.value;
        this.currentHistory = this.findCurrentHistory(event.value);
    }
  
    findCurrentHistory (historyProcess) {
        let ret = {};
        this.historyList.forEach(item => {
            if (item.historyProcess == historyProcess) {
                this.copyObject(ret, item);
            }
        })
        return ret;
    }

  
    historyControl (source, current) {
        if (source) {
            if (source.length == 1) {
              this.copyObject(current, source[0]);
            } else if (source.length > 1) {
                this.hasHistory = true;
                let array = this.sortByDesc(this.uniqueHistoryProcess(source));
                this.historyOptions = this.combineHistoryOptions(array);
                this.assignDataToDiffer(source, current);
            }
        }
    }
  
    assignDataToDiffer (source, current) {
        let array = this.sortByDesc(this.uniqueHistoryProcess(source));
        let currentHistoryProcess = array[0].historyProcess;
        source.forEach(item => {
            let historyProcess = item.historyProcess;
            if (historyProcess == currentHistoryProcess) {
              this.copyObject(current, item);
              //current = item;
            } else {
              this.historyList.push(item);
            }
        })
    }
  
    combineHistoryOptions (array) {
        let ret = [];

        for (let i = 1, len = array.length; i < len; i++) {
            let item = array[i];
            let obj = { label: '', value: '' };
            switch (item.historyProcess) {
                case '1':
                    obj.label = '客户经理';
                    obj.value = '1';
                    ret.push(obj);
                break;
                case '2':
                    obj.label = '支行层级';
                    obj.value = '2';
                    ret.push(obj);
                break;
                case '3':
                    obj.label = '分行层级';
                    obj.value = '3';
                    ret.push(obj);
                break;
                case '4':
                    obj.label = '总行层级';
                    obj.value = '4';
                    ret.push(obj);
                break;
            }
        }
        return ret;
    }
  
    uniqueHistoryProcess (array) {
        // let array = this.bondsmanConditionList;
        let ret = [];
        array.forEach(item => {
            let historyProcess = item.historyProcess;
            if (ret.indexOf(historyProcess) == -1) {
                ret.push({historyProcess: historyProcess});
            }
        })
        return ret;
    }

    copyObject (target, source) {
        for (let property in source) {
            if (source.hasOwnProperty(property)) {
                target[property] = source[property];
            }
        }
    }

    historyLevelHandle (pageLevel: string, isHistoryOrPushBack: boolean) {
        let ret = [];

        let increment = parseInt(this.operatingLevel) - 2;
        let start = {
            '1': { historyLevel: -1 + increment },
            '2': { historyLevel: -2 + increment },
            '3': { historyLevel: -3 + increment },
            '4': { historyLevel: -4 + increment },
        }
        let source = [
            { label: '客户经理', value: '1' },
            { label: '支行层级', value: '2' },
            { label: '分行层级', value: '3' },
            { label: '总行层级', value: '4' }
        ]
        let index = this.getIndex(source, pageLevel);
        let t = start[pageLevel].historyLevel;
        // if (isHistoryOrPushBack) {
        //     t += 1;
        // }
        for (let i = index, len = index + t; i < len; i++) {
            let element = source[i];
            ret.push(element);
        }

        return ret;
    }

    getIndex (source: any, val: string) {
        let ret = -1;
        for (let i = 0, len = source.length; i < len; i++) {
            let element = source[i];
            if (element.value == val) {
                ret = i;
                break;
            }
        }
        return ret;
    }

  
    sortByDesc (list) {
        return list.sort((A, B) => B.historyProcess - A.historyProcess);
    }

    setOperatingLevel (level: string) {
        this.operatingLevel = level;
    }
    setConfig (level: string, isHistoryOrPushBack: boolean, operatingLevel: string) {
        this.pageLevel = level;
        this.isHistoryOrPushBack = isHistoryOrPushBack;
        this.operatingLevel = operatingLevel;
    }

}

export class HistoryHelper {
    
        objectHelper: ObjectHelper = new ObjectHelper();
        
        source: any = [];               // 源数据
        historyOptions: any = [];       // 历史选项 { label: '客户经理', value: 1, label: '支行层级', value: 2, label: '分行层级', value: 3, label: '总行层级', value: 34 }
        historyList: any = [];          // 历史列表
        hasHistory: boolean = false;
        currentHistory: any = {};       // 当前历史
    
        current: any;
    
        display: Boolean = false;
    
    
        constructor () {
            
        }
    
        // -----------------------------------历史选项----------------------------------
        combineHistoryOptions (array) {
            let ret = [];
      
            for (let i = 1, len = array.length; i < len; i++) {
                let item = array[i];
                let obj = { label: '', value: '' };
                switch (item.historyProcess) {
                    case '1':
                        obj.label = '客户经理';
                        obj.value = '1';
                        ret.push(obj);
                    break;
                    case '2':
                        obj.label = '支行层级';
                        obj.value = '2';
                        ret.push(obj);
                    break;
                    case '3':
                        obj.label = '分行层级';
                        obj.value = '3';
                        ret.push(obj);
                    break;
                    case '4':
                        obj.label = '总行层级';
                        obj.value = '4';
                        ret.push(obj);
                    break;
                }
            }
            return ret;
        }
        getHistoryOptions () {
            return this.historyOptions;
        }
        setHistoryOptions () {
            this.historyOptions = this.combineHistoryOptions(this.source);
        }
    
    
        // -----------------------------------历史数据----------------------------------
        findHistoryList (list): any {
            let ret: any = [];
    
            if (list && list.length > 1) {
                for (let i = 1, len = list.length; i < len; i++) {
                    let element = list[i];
                    ret.push(this.objectHelper.objectDeepCopy(element));
                }
            }
    
            return ret;
        }
        getHistoryList (): any {
            return this.historyList;
        }
        setHistoryList () {
            let t = this.getSortList(this.source);
            this.historyList = this.findHistoryList(t);
        }
    
        // -----------------------------------当前数据----------------------------------
        findCurrentData (list): any {
            let ret: any;
    
            if (list && list.length) {
                let element = list[0];
                ret = this.objectHelper.objectDeepCopy(element)
            }
    
            return ret;
        }
        getCurrentData (): any {
            return this.current;
        }
        setCurrentData () {
            let t = this.getSortList(this.source);
            this.current = this.findCurrentData(t);
        }
    
        // -----------------------------------历史标志----------------------------------
        figureHasHistory (list): boolean {
            let ret;
            if (list && list.length > 1) {
                ret = true;
            }
            return ret;
        }
        getHasHistory (): boolean {
            return this.hasHistory;
        }
        setHasHistory () {
            this.hasHistory = this.figureHasHistory(this.source);
        }
    
    
        // -----------------------------------当前历史----------------------------------
        findCurrentHistory (list: any, historyProcess: string): any {
            let ret: any = null;
    
            for (let i = 0, len = list.length; i < len; i++) {
                let element = list[i];
                if (element.historyProcess == historyProcess) {
                    ret = this.objectHelper.objectDeepCopy(element);
                    break;
                }
            }
    
            return ret;
        }
        getCurrentHistory (): any {
            return this.currentHistory;
        }
        setCurrentHistory (historyProcess: string) {
           this.currentHistory = this.findCurrentHistory(this.historyList, historyProcess);
        }
    
    
    
        // -----------------------------------工具函数----------------------------------
    
        getCurrentImmediately (list: any): any {
            this.setSource(list);
            this.setCurrentData();
            return this.getCurrentData();
        }
    
        historyOptionsChange (event) {
            this.display = true;
            this.setCurrentHistory(event.value);
        }
    
        setHistoryData (list: any) {
            this.setSource(list);
            this.setHistoryOptions();
            this.setHistoryList();
            this.setCurrentData();
            this.setHasHistory();
        }
    
        setSource (list: any) {
            this.source = list;
        }
    
        getSortList (list: any) {
            let t = this.objectHelper.objectDeepCopy(list);
            return this.sortByDesc(t);
        }
        sortByDesc (list) {
            if (!list || !list.length) {
                return list;
            }
            return list.sort((A, B) => B.historyProcess - A.historyProcess);
        }
    
    }
    
