import { Injectable, EventEmitter } from '@angular/core';
@Injectable()
export class CreditCommfunc {
    //后台返回变成下拉框的值
    listToLabelvalue(data: any = [], label, value, custAttribute?) {
        
        let tempArr = [{ label: '请选择', value: '' }];
        if (data) {
            data.forEach(item => {
                let tempObj = { label: '', value: '' };
                tempObj['label'] = item[label];
                tempObj['value'] = item[value];
                if (custAttribute) {
                    if (custAttribute == '4') {
                        if (tempObj.label.indexOf('个人') == -1) {
                            tempArr.push(tempObj);
                        }
                    } else {
                        if (tempObj.label.indexOf('企业') == -1) {
                            tempArr.push(tempObj);
                        }
                    }
                } else {
                    tempArr.push(tempObj);
                }
            });
        }
        return tempArr;
    }
}
