    // 产品回掉
    productCall(event) {
        console.log(event)
        this.selectProductDisplay = false;
        this.maximumInfoBean.productName = event[0].productName;
        this.maximumInfoBean.productId = event[0].productId;
        this.getProductType(this.maximumInfoBean.productId);

    }
    // 获取主担保方式和产品计息方式和还款方式和合同类型
    getProductType(productId) {
        let params = {
            productId: productId
        }
        this.creditCcmService.findProductFeatureListByProductId(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.getProductTypeType(data.productFeatureList)
                console.log(data)
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }
    //  获取产品码值
    getProductTypeType(data) {
        data.forEach(element => {
            if (element.productFeatureCategoryId == "PC00000010") {
                element.productFeatureTypeList.forEach(item => {
                    if (item.productFeatureTypeId == "PCA0000057") {
                        this.rateKindType = this.typeTo(item.productFeatureList)//计息方式
                    }
                });
            }
            if (element.productFeatureCategoryId == "PC00000011") {
                element.productFeatureTypeList.forEach(item => {
                    if (item.productFeatureTypeId == "PCA0000062") {
                        this.returnKindType = this.typeTo(item.productFeatureList)//还款方式
                    }
                });
            }
            if (element.productFeatureCategoryId == "PC00000003") {
                element.productFeatureTypeList.forEach(item => {
                    if (item.productFeatureTypeId == "PCA0000011") {
                        this.contractTypeType = this.typeTo(item.productFeatureList)//合同种类
                    }
                });
            }
            if (element.productFeatureCategoryId == "PC00000005") {
                element.productFeatureTypeList.forEach(item => {
                    if (item.productFeatureTypeId == "PCA0000022") {
                        this.guaranteeModeOptions = this.typeTo(item.productFeatureList)//主担保方式
                    }
                });
            }
        });
    }
    // 码值处理第一步
    typeTo(data) {
        let codeType = [];
        data.forEach(element => {
            codeType.push({
                'label': element.productFeatureName, 'value': element.productFeatureLocalCode
            })
        });
        return codeType;
    }

    // 表单提交
    saveResult() {
        let params = {
            batchId: this.batchData.detailData.batchId,//批量预授信申请编号
            operType: "1",//操作类型
            groupId: this.batchData.detailData.groupId,//客户群号
            groupName: this.batchData.detailData.groupName,//客群名称
            groupType: this.batchData.groupData ? this.batchData.groupData.group_type : null,//客户群类型
            contractType: this.maximumInfoBean.contractType,//合同种类
            guaranteeType: this.maximumInfoBean.guaranteeType,//主担保方式
            productId: this.maximumInfoBean.productId,//产品编号
            productName: this.maximumInfoBean.productName,//产品名称
            rateKind: this.maximumInfoBean.rateKind,//计息方式
            returnKind: this.maximumInfoBean.returnKind,//还款方式
            endDate: this.commfunc.transDateN(this.maximumInfoBean.endDate),//到期日期
            note: this.maximumInfoBean.note,//备注
            operId: this.batchData.detailData.operId || (this.batchData.groupData ? this.batchData.groupData.GROUP_OWNER_ID : null),//客户群经办人ID
            operName: this.batchData.detailData.operName || (this.batchData.groupData ? this.batchData.groupData.GROUP_OWNER_NAME : null),//客户群经办人名称
            bankId: this.batchData.detailData.bankId || (this.batchData.groupData ? this.batchData.groupData.org_id : null),//客户群经办人机构ID
            bankName: this.batchData.detailData.bankName || (this.batchData.groupData ? this.batchData.groupData.org_name : null),//客户群经办人机构名称
            acDate: this.batchData.detailData.acDate || (this.batchData.groupData ? this.batchData.groupData.dt : null),//客户群经办日期
            operationType: this.isFirst,//操作类型
        }
        console.log(params)
        this.creditCcmService.updateBatchPreCredit(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                Object.assign(this.batchData.detailData, params)
                this.commfunc.setSessionDataCH('groupBranchData', JSON.stringify(this.batchData));
                this.commfunc.setSessionDataCH('groupBranchDataSave', JSON.stringify({
                    isSave: this.maximumForm.valid
                }));
                // this.communicationService.branchSave.emit({
                //    save:'1'
                //   });
                this.msgs = [{ severity: 'success', summary: '', detail: '保存成功' }];
                // this.commfunc.setSessionDataCH('createBatchBean', JSON.stringify(params));
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        })
    }

