import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { BranchExamApprovalData } from "app/pages/tzb/custom/overdue/overdue-case/branch-examination-approval/base/localData";



export class GeneralApprovalData extends BranchExamApprovalData {

    isGeneralApprovalEmpty: Boolean = false;

    constructor (public commfunc: Commfunc) {
        super(commfunc);
    }

    getHeadOfficeApproveEmpty () {
        let headOfficeApprove = this.getTemporaryData("overdue-headOfficeApprove");
        headOfficeApprove = headOfficeApprove ? headOfficeApprove.headOfficeApprove : null;
        return headOfficeApprove;
    }

    isHeadOfficeApproveEmpty () {
        let headOfficeApprove = this.getHeadOfficeApproveEmpty();
        if (!headOfficeApprove) {
            this.isGeneralApprovalEmpty = true;
            return;
        }
        if (!headOfficeApprove.headOfficeOption) {
            this.isGeneralApprovalEmpty = true;
            return;
        }
    }

    setMsg5 () {
        // super.setMsg4();
        if (this.isGeneralApprovalEmpty) {
            this.msg.push('总行层级审批意见：本岗意见有空数据');
        }
    }

    clear5 () {
        this.msg = [];
        this.isGeneralApprovalEmpty = false;
    }

    runEmptyCheck () {

        this.clear5();

        let father = super.runEmptyCheck();
        this.isHeadOfficeApproveEmpty();

        this.setMsg5();

        this.flags.concat([this.isCreditInfoEmpty, this.isOverdueReasonEmpty, this.isBranchExamApprovalEmpty, father]);

        let current = father || this.isGeneralApprovalEmpty;
        return current;
    }

}