import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

import { Message, ConfirmationService } from 'primeng/primeng';

import { CreditCustBean } from '../bean/credit-cust.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';
import { ActivatedRoute } from '@angular/router';
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import { API } from 'app/pages/tzb/custom/http/custom-view/custom-view.constant';
import * as $ from 'jquery';
import * as _ from 'lodash';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';

@Component({
    selector: 'app-credit-cust',
    templateUrl: './credit-cust.component.html',
    styleUrls: ['./credit-cust.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})

// tslint:disable-next-line:component-class-suffix
export class CreditCust implements OnInit {
    taskTypes: string;
    TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
    inverArr: any[] = [];
    investTrades: any[];
    msgs: Message[] = []; // 提示信息

    blockedPanel: boolean = false; // 如果只有查看权限的情况下不能编辑数据

    hiddenBtn: boolean; // 控制按钮显示或者隐藏

    hiddenUpdateBtn = true;// 隐藏修改按钮

    creditCustBean: CreditCustBean = new CreditCustBean(); // 客户信息bean

    firstToinvestTradeOptions: any[];//投向行业一级

    secondToinvestTradeOptions: any[];//投向行业二级

    thirdToinvestTradeOptions: any[];//投向行业三级

    fourthToinvestTradeOptions: any[];//投向行业四级

    firstToinvestTrade: any;//投向一

    secondToinvestTrade: any;//投向二

    thirdToinvestTrade: any;//投向三

    fourthToinvestTrade: any;//投向四

    // 隐藏展示企业客户信息
    displayEnter = false;

    // 非生产经营性客户
    displayNoproduct = false;

    // 生产经营性客户
    displayProduct = false;

    // 行政事业单位
    displayInstiution = false;

    // 客户信息是否完整性-下拉框
    isCompleteCustInfoList = [];

    // tslint:disable-next-line:no-input-rename
    count: number = 0;//投向行业计数

    inValueReportId: string;
    reportCustNm: string;
    userId: string;
    reportDisplay: boolean = false;
    progress: boolean = false;
    certType: string;
    certID: string;
    reportId: string;
    @Input('data') data;
    isApprove: boolean = true;//判断是否审批岗
    initQueryIsExecute = false;
    isHistory: boolean = true;
    taskTypeQryByIndName: any = [];
    constructor(
        public httpServices: CusViewHttpService,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
        public routerInfo: ActivatedRoute,

    ) {
        this.communicationService.queryTransfer.subscribe(_event => {
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditCust') !== -1) {
                setTimeout(() => {
                    this.initQuery();
                }, 100);
                this.initQueryIsExecute = true;
            }
        });
    }

    ngOnInit() {
        //判断是否是从历史进入
        if (this.data['transferCreditData']['flag'] && this.data['transferCreditData']['flag'] == '2') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        // 延迟查询
        setTimeout(() => {
            if (!this.initQueryIsExecute) {
                this.initQuery();
            }
        }, 200);
    }

    initQuery() {
        if (this.data.transferCreditData['isPreview']) {
            return;
        }
        //投向行业计数初始设置
        this.count = 0;
        // this.queryTaskByIndtstry('');
        // 加载码值表
        this.getCodeValues();
        // 判断客户是否完整性——决定“修改”按钮是否可以显示
        this.queryCustNeedInfo('init');
        // 获取综合信用报告ID
        // this.queryQualificationReviewInfo();
        // 判断是否是审批节点
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] === 'approve') {
            this.isApprove = false;
        }

    }

    // 获取下拉框值
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.isCompleteCustInfoList = codeValues['isCompleteCustInfo'];
    }

    //查询投向行业下拉框的值，递归三次
    queryTask(taskTypeParentId?) {

        this.count++;
        let param_zone = {
            taskTypeParentId,
            taskTypeKey: 'indtstryType'
        }
        this.httpService.taskTypeQry(param_zone).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (this.count == 1) {
                    this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                    this.secondToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                } else if (this.count == 2) {
                    this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                    this.thirdToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                } else if (this.count == 3) {
                    this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                    this.fourthToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                }
            }
            if (this.count < 3) {
                this.queryTask(this.inverArr[this.count])
            }
        });

    }
    //投向行业四级区划
    queryTaskByIndtstry(data) {
        if (data == '') {
            this.firstToinvestTradeOptions = [{ label: '请选择', value: '' }];
            this.secondToinvestTradeOptions = [{ label: '请选择', value: '' }];
            this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
            this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
            this.firstToinvestTrade = '';
            this.secondToinvestTrade = '';
            this.thirdToinvestTrade = '';
            this.fourthToinvestTrade = '';
            let param_zone = {
                taskTypeParentId: '',
                taskTypeKey: 'indtstryType'
            }
            //四级区划查询投向一
            this.httpService.taskTypeQry(param_zone).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.firstToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                    this.firstToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                    this.secondToinvestTradeOptions = [{ label: '请选择', value: '' }];
                    this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                    this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                    this.secondToinvestTrade = '';
                    this.thirdToinvestTrade = '';
                    this.fourthToinvestTrade = '';
                }
            });
        } else if (data == '1') {
            if (this.firstToinvestTrade == '') {
                this.secondToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.secondToinvestTrade = '';
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
            } else {
                this.secondToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.secondToinvestTrade = '';
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.firstToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向二
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.secondToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                        this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                        this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                        this.thirdToinvestTrade = '';
                        this.fourthToinvestTrade = '';
                    }
                });
            }
        } else if (data == '2') {
            if (this.secondToinvestTrade == '') {
                this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
            } else {
                this.thirdToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.thirdToinvestTrade = '';
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.secondToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向三
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.thirdToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                        this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                        this.fourthToinvestTrade = '';
                    }
                });
            }
        } else if (data == '3') {
            if (this.thirdToinvestTrade == '') {
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTrade = '';
            } else {
                this.fourthToinvestTradeOptions = [{ label: '请选择', value: '' }];
                this.fourthToinvestTrade = '';
                let param_zone = {
                    taskTypeParentId: this.thirdToinvestTrade,
                    taskTypeKey: 'indtstryType'
                }
                //四级区划查询投向四
                this.httpService.taskTypeQry(param_zone).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
                        this.fourthToinvestTradeOptions.unshift({ label: '请选择', value: '' });
                    }
                });
            }
        }
    }
    //投向行业查询
    queryTaskByIndtstryAll(taskTypeId) {
        let params = {
            taskTypeId,
            taskTypeKey: 'indtstryType'
        }
        this.creditCcmService.queryTaskByIndtstryAll(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                data.taskList.forEach(ele => {
                    this.inverArr.unshift(ele['taskTypeId']);
                });
                this.queryTask(this.inverArr[this.count]);
                //投向行业回显
                setTimeout(() => {
                    this.firstToinvestTrade = this.inverArr[0];
                    this.secondToinvestTrade = this.inverArr[1];
                    this.thirdToinvestTrade = this.inverArr[2];
                    this.fourthToinvestTrade = this.inverArr[3];
                    if (this.fourthToinvestTrade && this.fourthToinvestTrade != '') {
                        let param = {
                            ToinvestTrade1: this.firstToinvestTrade,
                            ToinvestTrade2: this.secondToinvestTrade,
                            ToinvestTrade3: this.thirdToinvestTrade.replace(this.secondToinvestTrade, ''),
                            ToinvestTrade4: this.fourthToinvestTrade.replace(this.thirdToinvestTrade, '')
                        }
                        this.commfunc.setSessionDataCH('toinvestTrade', JSON.stringify(param));
                    }
                }, 200);
            }
        })
    }
    // 客户详细信息查询
    queryCustNeedInfo(flag: string) {
        if (flag === 'closeDialog') {
            this.closeDialog();
        }
        const param = {
            custNo: this.data.transferCreditData['custNo']
        };
        this.creditCcmService.queryCustNeedInfo(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.creditCustBean.isCompleteCustInfo = data.creditCustBean
                // 界面展示
                this.creditCustBean = data;
                // 处理修改按钮是否显示—— Y:不显示按钮
                if (this.creditCustBean.isCompleteCustInfo === 'Y') {
                    this.hiddenUpdateBtn = true;
                } else {
                    this.hiddenUpdateBtn = false;
                }
                // this.queryTaskByIndtstryAll(data['investTrade']);
                this.taskTypeQryByInd({ taskTypeId: data['investTrade'] })

            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }

    closeDialog(): void {
        this.displayNoproduct = false;
        this.displayProduct = false;
        this.displayEnter = false;
        this.displayInstiution = false;
        this.msgs = [{ severity: 'success', summary: '提示：', detail: '保存成功' }];
    }

    // 点击修改按钮，弹出对话框
    updateCreditCust() {
        if (this.data.transferCreditData['isPreview']) {
            return;
        }
        // 1-非生产经营型个人客户；
        // 2-生产经营型个人客户；
        // 4-企业客户；
        // 5-行政事业单位
        let taskType = this.data.transferCreditData.varMap.dataMap.taskType;
        this.taskTypes = taskType ? taskType : '';
        if (this.creditCustBean.custAttribute === '1') {
            this.displayNoproduct = true;
        } else if (this.creditCustBean.custAttribute === '2') {
            this.displayProduct = true;
        } else if (this.creditCustBean.custAttribute === '4') {
            this.displayEnter = true;
        } else if (this.creditCustBean.custAttribute === '5') {
            this.displayInstiution = true;
        }
    }

    lookReport1() {
        this.queryCard()
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        let param = { custNo: this.creditCustBean.custNo }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
                        _self.creatMainMsg({ custNo: _self.creditCustBean.custNo, modNum: data.updatedNum, custName: _self.creditCustBean.custName, qryUserId: _self.userId, cretType: _self.certType, cretNo: _self.certID, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
                    } else {
                        _self.inValueReportId = data.reportId;
                        _self.reportCustNm = _self.creditCustBean.custNo;
                        _self.reportDisplay = true;
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            },
            error: function (err) {
                //////console.log(err);
            }
        })

    }

    //创建报告
    creatMainMsg(param) {
        this.progress = true;
        let params = this.commfunc.handleParams(param);
        this.httpServices.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = data.reportId;
                this.reportCustNm = this.creditCustBean.custNo;
                this.progress = false;
                this.reportDisplay = true;
            } else {
                this.progress = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.progress = false;
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用创建报告失败！' }];
        })
    }

    queryCard() {
        this.httpService.selectCustBriefInfo({ custNo: this.creditCustBean.custNo }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.certType = data.idenType;
                this.certID = data.idenNum;
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }

    /**
     * 获取综合信用报告ID
     */
    queryQualificationReviewInfo() {
        let processId = '';
        if ((this.data.transferCreditData.varMap.dataMap['taskType'] == '01'||(this.data.transferCreditData.varMap.dataMap['taskType'] == '18'&&this.data.transferCreditData.varMap.dataMap['requestSeqNo'] != ''||this.data.transferCreditData.varMap.dataMap['requestSeqNo'] !=undefined)||this.data.transferCreditData.varMap.dataMap['taskType'] == '02') && (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] != 'approve')) {
            processId = this.data.transferCreditData.varMap['requestSeqNo']
        } else {
            processId = this.data.transferCreditData['custRequestNo']
        }
        console.log(this.data.transferCreditData.varMap.dataMap['requestSeqNo'],"00000")
        let stageTypes = '';
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'approve') {
            stageTypes = '910002';
        } else if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'task') {
            if (this.data.transferCreditData.varMap.dataMap['taskType'] == '01'||(this.data.transferCreditData.varMap.dataMap['taskType'] == '18'&&this.data.transferCreditData.varMap.dataMap['requestSeqNo'] != ''||this.data.transferCreditData.varMap.dataMap['requestSeqNo'] !=undefined)||this.data.transferCreditData.varMap.dataMap['taskType'] == '02') {
                stageTypes = '910000';
            } else {
                stageTypes = '910001';
            }
        }
        //console.log(this.data.transferCreditData);
        let param = {
            processId: processId,
            custNo: this.data.transferCreditData['custNo'],
            stageType: stageTypes
        }
        this.creditCcmService.queryQualificationReviewInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.list && data.list.length > 0 && data.list[0].reportId) {
                    this.reportId = data.list[0].reportId;
                    if (this.reportId) {
                        this.inValueReportId = this.reportId;
                        this.reportCustNm = this.creditCustBean.custNo;
                        this.commfunc.setSessionData('custNo', this.creditCustBean.custNo);
                        this.reportDisplay = true;
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '无BI报告' }];
                    }
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '无BI报告' }];
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }

    lookReport() {
        // 获取综合信用报告ID
        this.queryQualificationReviewInfo();
    }


    //查询投向行业中文
    taskTypeQryByInd(param) {
        this.creditCcmService.taskTypeQryByInd(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.indtstryTypeList.length > 0) {
                    this.taskTypeQryByIndName = data.indtstryTypeList
                    this.commfunc.setSessionDataCH('taskTypeQryByIndName', JSON.stringify( this.taskTypeQryByIndName))
                }

            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
            //console.log(data)
        });
    }

    compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}
}