import { Component, OnInit, Input } from '@angular/core';
import { ApplyMessageBean } from './apply-message.bean';
import { ParentChildCommunicationService } from '../common/service/parent-child-comm.service';
import { CreditCcmService } from '../common/http/credit-ccm.http.service';
import { TZB_HTTP_YXXT_ZCZM } from "app/@uisftech/common/service/http.constant";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
  selector: 'app-apply-message',
  templateUrl: './apply-message.component.html',
  styleUrls: ['./apply-message.component.scss'],
  providers: [Commfunc]
})
export class ApplyMessageComponent implements OnInit {
  isPicInfo: boolean;//资产证明
  applyMessageBean: ApplyMessageBean = new ApplyMessageBean();
  dataMapParam: any;
  custRequestId: any;
  picType: any;
  //url='http://10.1.97.166:9083/SunIAP/SunIAPRequestController.do?UID=MOBILE&PWD=cyg&AppID=SF&';
  urlsuffix='SunIAP/SunIAPRequestController.do?UID=MOBILE&PWD=cyg&AppID=SF&';
  urlprefix=TZB_HTTP_YXXT_ZCZM;
  url = this.urlprefix+ this.urlsuffix;
  urls:any;
  urls1:any;
  // UID=admin&PWD=111&AppID=SF&UserID=zhangsan&UserName=zhangsan&OrgID=0117&OrgName=台州银行OrgOwner=CRM&QueryFlag=1&info1=TABSORDER:1;BUSI_SERIAL_NO:SN000000000001202086;TABNAME:借款人-张三;PROSERIALNO:zgd2018101501;OBJECTNAME:APP1009
  maritalStatuss = [];
  InvestIndustryss=[];
  disaply: boolean = false;
  contactTypess=[{ label: '请选择', value: '' },{ label: '配偶', value: 'A' },{ label: '亲人', value: 'B' },{ label: '朋友', value: 'C' },{ label: '同事', value: 'D' },{ label: '其他', value: 'E' }];
  companyNaturess=[{ label: '请选择', value: '' },{ label: '政府机构', value: 'A' },{ label: '事业单位', value: 'B' },{ label: '国有企业', value: 'C' },{ label: '垄断行业', value: 'D' },{ label: '其他企业', value: 'E' }];
  dutyss=[{ label: '请选择', value: '' },{ label: '高级领导', value: '1' },{ label: '中级领导', value: '2' },{ label: '一般员工', value: '3' },{ label: '其他', value: '4' },{ label: '未知', value: '9' }]
  cardSendss=[ { label: '请选择', value: '' },
              { label: '户籍地址', value: '0' },
               { label: '居住地址', value: '1' },
                { label: '单位地址', value: '2' }
              ];
  degrees = [];
  liveStatusss=[];
  @Input('data') data;
  count: number;//计数，防抖
  constructor(
    public communicationService: ParentChildCommunicationService,
    public creditCcmService: CreditCcmService,
    public commfunc: Commfunc,
  ) {
    this.communicationService.queryTransfer.subscribe(_event => {
      this.custRequestId = this.data.transferCreditData.varMap.dataMap.custRequestId;
      if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditDebt') !== -1) {
        this.count++;
        if (this.count == 1) {
          // this.getInformation()
        }
      }
    });
  }

  ngOnInit() {
    console.log(this.data);
    this.isPicInfo = true;
    this.getInformation();
    this.getCodeValues();
  }
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.maritalStatuss = codeValues['MARITAL_STATUS'];
    this.degrees = codeValues['ACADEMIC'];
    this.liveStatusss=codeValues['LIVE_STATUS'];
    this.InvestIndustryss=codeValues['InvestIndustry']
    console.log(codeValues);
  }
  //获取基础信息
  getInformation() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let OrgName = chName.orgName;
    let OrgID= commonHeader.orgId;
    let param = {
      custRequestId: this.data.transferCreditData.custRequestId,
      ratingFlag:"1"
      
      // custRequestId: '17160000',
    }
    this.creditCcmService.queryCustRequestDetail(param).subscribe(data => {
      debugger;
      if (data.returnCode.code === 'success') {
        this.applyMessageBean = data;
        this.picType=this.applyMessageBean.picInfoList[0].picSerialNo;
      this.urls=this.url+'UserID='+data.custNo+'&UserName='+data.clientNameRel+'&OrgID='+OrgID+'&OrgName='+OrgName+'COrgOwner=CRM&QueryFlag=1&info1=TABSORDER:1;BUSI_SERIAL_NO:'+this.data.transferCreditData.custRequestId;
      this.urls1=';TABNAME:申请人-'+data.clientNameRel+';PROSERIALNO:'+this.picType+';OBJECTNAME:APP1009;';
        if(this.applyMessageBean.maritalStatus == '20'){
          this.disaply = true;
        }
        if (data.picInfoList) {
          for (let i = 0; i < data.picInfoList.length; i++) {
            this.applyMessageBean.picInfoList = data.picInfoList;
          }
        }
      }
    })
  }
}
