import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as moment from 'moment';
// import { OrderHttpService } from './../../../http/order.http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { LoninterrestBean } from './bean/loninterrest.bean';
import { CreditCcmService } from '../common/http/credit-ccm.http.service';
import { preAppprovedListinfo } from '../../constant/custOperation.codeValue';
import { TZB_CREL_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
// import { bankNo, salesChannelEnumId, tranType } from '../../../constant/codeValue';
@Component({
  selector: 'app-loninterrest',
  templateUrl: './loninterrest.component.html',
  styleUrls: ['./loninterrest.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class LoninterrestComponent implements OnInit {
 
  dataMap: any;
  loanDataMap: any;
  loanDataList: any;
  duebillNo: any;
  msgs: any[];
  unrecycleInterestCoun: any;
  total: any;
  custName:any;
  first=0;
  ch: any = CALENDAR_CN;//讲英文转化为中文
//表数据
tableData: any = [];
isjsstar=true;
isjsstardate=true;
retdate: Date = new Date();
pageSize=10;
pageNumber=1;
// bean
LoninterrestBean: LoninterrestBean = new LoninterrestBean();
//模态框
personPage: boolean = false;
code: any;
DOWNLOAD: string = TZB_CREL_DOWNLOAD;

constructor(
  public confirmationService: ConfirmationService,
  public commfunc: Commfunc,
  public router: Router,
  public fb: FormBuilder,
  public creditCcmService: CreditCcmService,
) {
  this.codeValues() //调用方法，获取全部码值
 
}

ngOnInit() {
  // this.regisStatuOptions = this.code['regisStatuIntro'];//登记簿状态
  let _thiss = this;
}
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code = ret //code是自己声明的变量
}

//查询
queryClick(duebillNo) {
  this.LoninterrestBean= new LoninterrestBean();
  this.custName='';
  let pamse = {
    duebillNo:duebillNo
  };
  this.creditCcmService.LOAN_02003000001_03(pamse).subscribe(data => {
    if (data.returnCode.code == 'success') {
      this.LoninterrestBean = data;
      this.queryCustNeedInfo(data.clientNo);
      this.loanDataMap= data;
      let datetime=moment(data.tranDate).format('Y-MM-DD');
      this.retdate =new Date(datetime);
      this.isjsstar=false;
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    }
  }, error => {
    this.msgs = [];
    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
  })
}
selectTime1(){
  this.loanDataMap['loanDate']= moment(this.LoninterrestBean.loanDate).format('YYYYMMDD');
  const pamse={
    duebillNo: this.duebillNo,
    registerDate: moment(this.LoninterrestBean.loanDate).format('YYYYMMDD'),
  }
  this.creditCcmService.LOAN_02002000005_11(pamse).subscribe(data => {
    if (data.returnCode.code == 'success') {
      this.tableData=data.listArray;
      this.dataMap=data.dataMap;
      this.isjsstardate=false;
      this.LoninterrestBean.totalAmt=Number(this.LoninterrestBean.loanBalance)+Number(this.dataMap.unrecycleInterestCount);
      this.unrecycleInterestCoun=this.dataMap.unrecycleInterestCount;
      this.loanDataMap['totalAmt']=this.LoninterrestBean.totalAmt;
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    }
  }, error => {
    this.msgs = [];
    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
  })
}
getLoanDataToExcel(){
  this.dataMap['unrecycleAmt']=this.LoninterrestBean.loanBalance;
  this.dataMap['totalAmt']=this.LoninterrestBean.totalAmt;
  const pamse={
    loanDataList: this.tableData,
    loanDataMap:this.loanDataMap,
    dataMap:this.dataMap,
  }
  this.creditCcmService.getLoanDataToExcel(pamse).subscribe(data => {
    if (data.returnCode.code == 'success') {
      let fileUrl=data.fileUrl;
      let fileName=data.fileName;
      open(this.DOWNLOAD + "?fileName=" + encodeURI(fileName) + "&fileUrl=" + fileUrl);
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    }
  }, error => {
    this.msgs = [];
    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
  })
}

queryCustNeedInfo(custNo) {

  const param = {
      custNo: custNo
  };
  this.creditCcmService.queryCustNeedInfo(param).subscribe(data => {

      if (data.returnCode.code === 'success') {
          this.custName = data.custName;
          this.loanDataMap['custName']=this.custName;
      } else {
          this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
  });
}

//分页
paginate(event) {
  //每页显示的条数
  this.pageSize = event.rows * 1;
  //当前页
  let currentPage = event.page + 1;
  this.pageNumber = currentPage;
  this.first = event.page * event.rows;
  //调用查询的方法
  // this.queryClick();
}


}