 // 获取今天最大最小日期
 getMinMaxDate(timeParam, param) {
    let time = new Date(timeParam);
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let data = time.getDate() + '';
    if (month.length < 2) {
        month = '0' + month;
    }
    if (data.length < 2) {
        data = "0" + data
    }
    let todayTime;
    if (param == 'min') {
        todayTime = year + "/" + month + "/" + data + ' 00:00:00';
    }
    if (param == 'max') {
        todayTime = year + "/" + month + "/" + data + ' 23:59:59';
    }
    return todayTime;
}
    //申请金额校验
    onKeyupNumber() {
        if (this.applyAmts) {
            this.applyAmts = this.commfunc.transform(this.applyAmts) + '';
        }
        // 校验金额上下限
        if (this.minJe) {
            if (parseInt(this.applyAmts) < parseInt(this.minJe)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '申请金额最低金额不能低于' + this.minJe + '!' });
                return
            }
        }
        if (this.maxJe) {
            if (parseInt(this.applyAmts) > parseInt(this.maxJe)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '申请金额最高金额不能高于' + this.maxJe + '!' });
                return
            }
        }
        var regAmtJy = /^([1-9][0-9]*[0]{2})?$/;
        if (!regAmtJy.test(this.applyAmts) && parseInt(this.applyAmts) != 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '申请金额必须为100的整数倍!' });
            return
        }
        this.applyAmts = this.decimalPipe.transform(this.applyAmts, '1.2-2');//申请金额
        // this.advanceCp();
    }
