import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { Router } from "@angular/router/src";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { OverdueHttpService } from "app/pages/tzb/custom/overdue/http/overdue.http.service";

export class ProcessHelper {

    userId: string = '';
    taskId: string = '';
    overdueCaseAnalyzeId: string = '';
    examineDisplay: Boolean = false;
    extableData: any = [];
    dealPerShow: boolean;

    localData: LocalData = new LocalData(this.commfunc);
    commonHeader: any;
    mainFlowNo: string = '';
    objectHelper: ObjectHelper = new ObjectHelper();
    historyProcessList: any = [];
    doResult2Status: boolean = false;
    info: string = '';
    isNeedUserIdMap: boolean = false;

    // 打回任一节点
    checkOneNode: string;
    nodeDisplay: boolean = false;
    nodeList: any = [];

    submitPage: string;
    submitData: any;

    maxCreditAmount: number = 0;


    constructor (
        public commfunc: Commfunc,
        public customService: CustomeHttpService,
        public commonlyMessage: CommonlyMessage,
        public router: Router,
        public customCenterHttpService: CustomCenterHttpService,
        public httpService: OverdueHttpService
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.mainFlowNo = this.localData.getOverdueConfidential().mainFlowNo;
    }

    loadData (obj) {
        this.userId = obj.userId;
        this.taskId = obj.taskId;
        this.overdueCaseAnalyzeId = obj.overdueCaseAnalyzeId;
    }

    setUserIdMap () {
        this.isNeedUserIdMap = true;
    }


    overdueCaseAnalyze (callBack: Function) {
        this.httpService.overdueCaseAnalyze(this.submitData).subscribe(data => {
            if (data.returnCode.code == 'success') {
                callBack();
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }

    overdueCaseApprove (callBack: Function) {
        this.httpService.overdueCaseApprove(this.submitData).subscribe(data => {
            // console.log(data)
            if (data.returnCode.code == 'success') {
                callBack();
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError('调用失败!');
        });
    }


    selectNextPerson (item) {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // 删除之前选中的人
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        
        let param = {
          userIdMap: { userId: this.userId },
          proxessDesc: '选人策略',
          taskId: this.taskId,
          processCode: "RUM0011",
          bussinessMap: {
              approve:'0'
          }
        }
        if(item){
            Object.assign(param.bussinessMap, item)  
        }
        this.customService.bpmFindNextNodeInfo(param).subscribe(data => {
            this.examineDisplay = true;
          if (data.returnCode.code == 'success') {
            if (data.result.nextOutData && data.result.nextOutData) {
              // let waitSelectUserList = data.result.nextOutData;
              let waitSelectUserList = [];
              this.arrayCopy(data.result.nextOutData, waitSelectUserList);
              // console.log(waitSelectUserList)
              this.extableData = [];
              this.extableData = waitSelectUserList;
              let a = commonHeader.extParam.specifyNextOpers;
              if (!a) {
                if (this.extableData && this.extableData.length && this.extableData[0].selectUserList && this.extableData[0].selectUserList.length) {
                    this.examineDisplay = true;
                } else {
                    this.examineDisplay = false;
                }
              }

            }
          } else {
            this.examineDisplay = false;
            this.commonlyMessage.logicError(data.returnCode.message);
          }
        }, error => {
            this.examineDisplay = false;
            this.commonlyMessage.logicError("调用服务失败！");
        })
    }

    arrayCopy (source: any, outArray: any) {
        debugger;
        for (let i = 0, len = source.length; i < len; i++) {
            let element = source[i];
            if (element.nextOutData) {
                if (!element.nextOutData.length) {
                    let t = this.objectHelper.objectDeepCopy(element);
                    outArray.push(t);
                } else {
                    this.arrayCopy(element.nextOutData, outArray);
                }
            }
        }
    }


    //查询流水号
    checkSerIal(params) {
        this.resetSerialNumber();
        this.pushToNext(params);
    }


    pushToNext(params) {

        this.customService.bpmNextTask(params).subscribe(data => {
            // console.log(data);
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
                //this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
                this.jumpToList();
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError("调用服务失败！");
        })
    }


    outDisplayCall(e) {
        this.dealPerShow = e
    }


    outValueCall(e) {
        let persons = '';
        e.forEach(el => {
            persons += el['tellerId'] + ','
        });
        debugger;
        let dataMap = this.localData.getTemporaryData("overdue-confidential").dataMap;
        dataMap['inspectAnalyzeTaskId'] = this.overdueCaseAnalyzeId;

        let params = {
            userId: this.userId,
            taskId: this.taskId,
            bussinessMap: {
                overdueMark: 'overdue-process',
                specifyNextOpers: persons,
                specifyNextNode: e[0] ? e[0].outFlowId : '',
                dataMap: dataMap
            },
            approveMap: {
                result: '0',
                info: this.info
            }
        }
        // if (this.isNeedUserIdMap) {
        //     params['bussinessMap']['userIdMap'] = { userId: this.userId };
        // }
        // console.log(params);
        // this.pushToNext(params)
        if (this.submitPage == 'overdue-reason') {
            params.bussinessMap['maxCreditAmt'] = this.maxCreditAmount;
            this.overdueCaseAnalyze(() => {
                this.checkSerIal(params)
            })
        } else if (this.submitPage == 'general-approval') {
            this.checkSerIal(params)
        } else {
            this.overdueCaseApprove(() => {
                this.checkSerIal(params)
            })
        }
    }

    setMaxCreditAmount (creditAmount: number) {
        this.maxCreditAmount = creditAmount;
    }

    setSubmitEnvironment (param: any, submitPage: string) {
        this.submitData = param;
        this.submitPage = submitPage;
    }

    setInfo (info: string) {
        this.info = info;
    }

    // 打回按钮
    back(param) {
        this.setCurrentSerialNumber(()=>{
            // let param = { userId: this.userId, taskId: this.taskId, tranType: type, doResion: resion, approveInfo: info };
            this.customService.bpmUserTaskManage(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.commonlyMessage.logicSuccess(data.returnCode.message);
                    this.jumpToList();
                } else {
                    this.commonlyMessage.logicError(data.returnCode.message);
                }
            }, error => {
                this.commonlyMessage.logicError('调用服务失败！');
            });
        })
    }


    getNewSerialNumer (callback: Function) {
        let param = {
            globalSeqNo: this.mainFlowNo
        }
        this.customCenterHttpService.createGlobalSeqNo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                commonHeader['globalSeqNo'] = data.globalSeqNo;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
            }
            callback();
        }, error => {
            callback();
        })
    }

    resetSerialNumber () {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader['globalSeqNo'] = this.mainFlowNo;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    }

    setCurrentSerialNumber (callback: Function) {
        this.resetSerialNumber();
        callback();
    }

    // 追回
    cancel (info) {
        this.back({ userId: this.userId, taskId: this.taskId, tranType: 'A1', doResion: '3', approveInfo: info });
    }

    // 查询节点
    queryNodeInfo() {
        let param = {
            tranType: 'B1',
            taskId: this.taskId,
            isAllNode: false,
        }
        this.customService.bpmQueryWorkflowDefNode(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
            this.nodeList = data.pageData.result == null ? [] : data.pageData.result;
            if (this.nodeList.length > 0) {
                this.showNodeChoose();
            } else {
                this.hideNodeChoose();
            }
        } else {
            this.commonlyMessage.logicSuccess(data.returnCode.message);
        }
        }, error => {
            this.commonlyMessage.logicError('调用服务失败！');
        })
    }

    // 选择节点打回
    returnToAnyNode (info) {
        if (!this.checkOneNode) {
            this.commonlyMessage.logicError('请选择退回节点！');
            return;
        }
        this.nodeDisplay = false;
        // 退回选中节点
        let param = { userId: this.userId, taskId: this.taskId, tranType: 'B3', doResion: '7', targetId: this.checkOneNode };
        this.back(param);
    }
    showNodeChoose () {
        this.nodeDisplay = true;
    }
    hideNodeChoose () {
        this.nodeDisplay = false;
    }

    // 打回拟稿人
    returnToStart (info) {
        let param = { userId: this.userId, taskId: this.taskId, tranType: 'B4', doResion: '5', approveInfo: info };
        this.back(param);
    }

    // 打回上一个节点
    returnBack (info) {
        let param = { userId: this.userId, taskId: this.taskId, tranType: 'B0', doResion: '2', approveInfo: info };
        this.back(param);
    }


    jumpToList () {
        this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
    }


    // 查询(历史)审批记录
    bpmQueryTaskList(parameter: any) {
        let param = {
            processId: parameter.processId,                 // this.theRequest.processId,,
            isAllNode: false,
            curPage: 1,
            pageRows: 99,
        }
        this.customService.bpmQueryTaskList(param).subscribe(data => {
        if (data.returnCode.code == "success") {
            this.historyProcessList = data.pageData.result;
            if (this.historyProcessList && this.historyProcessList.length) {
                let t = this.historyProcessList.filter(x => x.taskId == parameter.taskId);
                let element = t ? t[0] : null;
                if (element && element.doResult == '2') {
                    this.doResult2Status = true;
                }
            }
        } else {
            this.commonlyMessage.logicSuccess(data.returnCode.message);
        }
        }, error => {
            this.commonlyMessage.logicError('调用服务失败！');
        })
    }

}