export class MyFormValidate {

    msg: string = '此项必填';

    constructor () {}

    chineseCharacterLength (text) {
        let ret = 0;
        for (let i = 0, len = text.length; i < len; i ++) {
            let char = text.charAt(i);
            if (char.match(/[^\x00-\xff]/ig) != null) {
                ret += 2;
            } else {
                ret ++;
            }
        }
        return ret;
    }

    IsOverLengthLimit (val, limit) {
        return this.chineseCharacterLength(val) > limit;
    }

    cannotBeEmpty (event) {
        let ret = false;
        let val = event.value;
        if (val) {
            ret = true
        }
        return ret;
    }

    onlyNumber (val) {
        let ret = val;

        if (ret) {
            ret = ret.replace(/[^\d.]/g, '');
            // ret = ret.replace(/\.{2,}/g, '.');
        }

        return ret;
    }


}