import { Component, OnInit, Input } from '@angular/core';
import { Message } from 'primeng/primeng';
import { TransformLoanPlanBean } from '../bean/transform-loan-plan.bean';
import { ActivatedRoute, Router } from '@angular/router';
import { TransformationPlanHttpService } from '../http/transformation-plan.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ParentChildCommunicationService } from '../../common/service/parent-child-comm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'transformation-loan',
    templateUrl: './transformation-loan.component.html',
    styleUrls: ['./transformation-loan.component.scss'],
    providers: [TransformationPlanHttpService, Commfunc]
})
export class TransformationLoanComponent implements OnInit {
    accTotal: any;
    displayComponent: boolean;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    dataTable: any = [];
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    first: number = 0;
    addPage: boolean = false;//新增弹框
    addHeader: string = '风险资产处置新增';
    updatePage: boolean = false;//修改弹框
    updateHeader: string = '风险资产处置修改';
    detailPage: boolean = false;//详情弹框
    detailHeader: string = '风险资产处置详情';
    transformLoanPlanBean: TransformLoanPlanBean = new TransformLoanPlanBean();
    inValue: any;//详情传值
    inValue_add: any;//新增传值
    inValue_update: any;//修改传值
    value_param: any;//跳转的传值
    tranSeqNo: string;//传入的流水号
    taskRate: any;//传入的值
    @Input('data') data;
    code: any;
    question: any = [];//是否
    guaranteeModeOptions: any = [];//主担保方式
    approveFlag: boolean = false;//审批中不可修改
    minJe: string;//最小限制金额
    maxJe: string;//最大限制金额

    approveParams: any;//审批时不点下一步传送出去的参数
    addShow1: boolean = false;
    addShow2: boolean = false
    constructor(
        public httpService: TransformationPlanHttpService,
        public routeInfo: ActivatedRoute,
        public router: Router,
        private communicationService: ParentChildCommunicationService,
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
    ) {

    }

    ngOnInit() {
        // console.log(this.data)
        this.communicationService.queryTransfer.subscribe(_event => {
            console.log(_event.componentNames.indexOf('TransformationPlan'));
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('TransformationPlan') !== -1) {
                this.codeValues();
                this.question = this.code['transformQuestion'];//是否
                this.guaranteeModeOptions = this.code['guaranteeModeTransform'];//主担保方式

                this.tranSeqNo = this.data.transferCreditData['custRequestNo'];
                this.taskRate = this.data.transferCreditData['taskRate'];
                this.query('flag');
                this.productCallTime();
                //首节点 发起人  可操作 isExamine(查看)
                if (this.taskRate == 1 && this.data.transferCreditData['flag'] != '2' && !this.data.transferCreditData['isExamine']) {
                    this.approveFlag = true;
                } else {
                    this.approveFlag = false;
                }
                //只要进入转化贷款就向common-view-page页面发送一个标识
                this.communicationService.transformSendData.emit({ flag: true, approveFlag: this.approveFlag });
            }
        });
    }
    // 查询转换贷款类型
    getBusinessHappenTypeValue() {
        const param = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
        };
        this.httpService.dynamicProductQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 产品动态属性“转换贷款”的值===0或者1，展示组件isConverLoan  PCA0000013
                if (data.dynamicProductValue['PCA0000013'] === '0') {
                    this.displayComponent = false;
                } else {
                    this.displayComponent = true;
                }
            }
        });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //新增
    add() {
        this.inValue_add = this.data;
        this.inValue_add['tranSeqNo'] = this.tranSeqNo;
        this.inValue_add['taskRate'] = this.taskRate;
        this.addPage = true;
    }

    //详情
    toDetail(col) {

        this.inValue = col;
        this.inValue['tranSeqNo'] = this.tranSeqNo;
        this.inValue['taskRate'] = this.taskRate;
        this.inValue['value_param'] = this.data;
        this.detailPage = true;
    }
    //修改
    toUpdate(col) {
        this.inValue_update = col;
        this.inValue_update['tranSeqNo'] = this.tranSeqNo;
        this.inValue_update['taskRate'] = this.taskRate;
        this.inValue_update['value_param'] = this.data;
        this.updatePage = true;
    }
    //删除
    toDelete(col) {
        let param = {
            operateType: '3',
            custRequestId: this.tranSeqNo,
            contractNo: col.contractNo,
            taskRate: this.taskRate,
        }
        this.httpService.maintainConvertPlanInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功!' });
                this.query();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                return
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //查询
    query(flag?) {
        let param = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            tranSeqNo: this.data.transferCreditData['custRequestNo'],
        }
        this.httpService.queryConvertPlanListInfo(param).subscribe(data => {

            if (data.returnCode.code == 'success') {
                if (data.resultList && data.resultList.length > 0) {
                    this.addShow1 = true;
                    this.addShow2 = false;
                    this.dataTable = data.resultList;

                    //审批时的参数传输 当前不是
                    let numThis = null;
                    let isRepayment = 3;//是否以贷还贷
                    for (let item of this.dataTable) {
                        if (item.convertContractAmount) {
                            let money = item.convertContractAmount.replace(/\,/g, '');
                            numThis += Number(money);
                        }
                        if (item.isLoanRepayment == '0') {
                            isRepayment = 0;
                        }
                    }
                    if (flag) {//初始化时进行赋值
                        this.accTotal = numThis;
                    }
                    if (this.data.transferCreditData['taskRate'] != 1) {

                        this.approveParams = {
                            convertContractAmount: numThis,
                            index: 0,
                            isLoanRepayment: isRepayment
                        }
                        this.approveFlag = false;
                        this.communicationService.changeTabview.emit(this.approveParams);
                    }
                } else {

                    this.dataTable = [];
                    this.addShow1 = false;
                    this.addShow2 = true;
                    this.inValue_add = this.data;
                    this.inValue_add['tranSeqNo'] = this.tranSeqNo;
                    this.inValue_add['taskRate'] = this.taskRate;

                }
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //借据列表翻页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.query();
    }
    addCall(event) {
        this.addPage = event;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功!' });
        this.query();
    }
    updateCall(event) {
        this.updatePage = event;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功!' });
        this.query();
    }
    //下一步
    goToNext() {
        if (this.dataTable && this.dataTable.length == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请至少添加一条数据!' });
            return
        }
        let numThis = null;
        let isRepayment = 3;//是否以贷还贷
        for (let item of this.dataTable) {
            if (item.convertContractAmount) {
                let money = item.convertContractAmount.replace(/\,/g, '');
                numThis += Number(money);
            }
            if (item.isLoanRepayment == '0') {
                isRepayment = 0;
            }
        }
        if (numThis < this.minJe) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '转化金额之和不能小于' + this.minJe });
            return
        }
        if (numThis > this.maxJe) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '转化金额之和不能大于' + this.maxJe });
            return
        }
        //转化金额若发生改变，传递一个标识到授信页面，并将新的金额保存
        let accChangeF: Boolean
        if (numThis == this.accTotal) {
            accChangeF = false;
        } else {
            accChangeF = true;
            this.accTotal = numThis;
        }
        let param = {
            convertContractAmount: numThis,
            index: 1,
            isLoanRepayment: isRepayment,
            accChangeF
        }
        this.communicationService.changeTabview.emit(param);
    }
    //根据产品选取对应的时间端
    productCallTime() {
        let pro = {
            productId: this.data.transferCreditData['productNo'],
            queryType: '9',
        }
        this.creditCcmService.getAvailableProduct(pro).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let productFeatureTypeList = [];
                let productFeatureList = [];
                let productFeatureListQX = [];
                let productFeatureListJe = [];
                if (data.productFeatureList && data.productFeatureList.length > 0) {
                    for (let i = 0; i < data.productFeatureList.length; i++) {
                        if (data.productFeatureList[i].productFeatureCategoryId == 'PC00000003') {
                            productFeatureTypeList = data.productFeatureList[i].productFeatureTypeList;
                            break
                        }
                    }
                    if (productFeatureTypeList.length > 0) {
                        for (let j = 0; j < productFeatureTypeList.length; j++) {
                            if (productFeatureTypeList[j].productFeatureTypeId == 'PCA0000172') {
                                productFeatureList = productFeatureTypeList[j].productFeatureList;
                                break
                            }
                        }
                        for (let k = 0; k < productFeatureTypeList.length; k++) {
                            if (productFeatureTypeList[k].productFeatureTypeId == 'PCA0000014') {
                                productFeatureListQX = productFeatureTypeList[k].productFeatureList;
                                break
                            }
                        }
                        for (let b = 0; b < productFeatureTypeList.length; b++) {
                            if (productFeatureTypeList[b].productFeatureTypeId == 'PCA0000012') {
                                productFeatureListJe = productFeatureTypeList[b].productFeatureList;
                                break
                            }
                        }
                    }
                    if (productFeatureListJe.length > 0) {
                        // 设置金额的最大值和最小值
                        let startminJe = productFeatureListJe[0].min; //最小金额
                        let endmaxJe = productFeatureListJe[0].max; //最大金额
                        if (startminJe && startminJe != null) {
                            this.minJe = startminJe;
                        }
                        if (endmaxJe && endmaxJe != null) {
                            this.maxJe = endmaxJe;
                            //console.log(this.maxJe)
                        }
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    //详情回调
    detailCall(event) {

        this.detailPage = event;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功!' });
        this.query();
    }
    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }
}
